<template>
	<!-- 修改钱包地址-->
	<div class="divColumn">
		<Title title="修改钱包地址"></Title>
		<div class="conTop divColumn">
			<div style="margin: 0rem 0.625rem;" class="divColumn">
				<div class="leftAndRight itemW">
					<span style="width: 30%;">钱包秘钥：</span>
					<van-field style="background-color:#f6f8fe;" v-model="secret" placeholder="请输入新的瑞波链钱包秘钥"></van-field>
				</div>
				<div class="leftAndRight itemW">
					<span style="width: 30%;">交易密码：</span>
					<van-field style="background-color:#f6f8fe;" v-model="pwd" placeholder="请输入交易密码" type="password"></van-field>
				</div>
				<div
					@click="sureUpd"
					class="itemW divCenter"
					style="background-color: #518CFF;border-radius: 0.3125rem;margin-top: 2.5rem;font-size: 1rem;font-weight: 600;color: white;"
				>
					<span>确认</span>
				</div>
				<!-- 温馨提示 -->
				<div class="divColumn" style="text-align: left;margin-top: 1.25rem;margin-bottom: 2.5rem;">
					<span style="margin-bottom: 0.625rem;">温馨提示</span>
					<span style="text-indent:2em;line-height: 1.5rem;">*修改钱包地址/交易密码会覆盖之前的钱包信息，请务必保持好之前的钱包文件或秘钥</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
export default {
	components: {
		Title
	},
	data() {
		return {
			pwd: '',
			secret: '',
			walData: {}
		};
	},
	created: function() {
		var _this = this;
		_this.walData = _this.getWallent();
	},
	methods: {
		sureUpd: function() {
			var _this = this;
			if (_this.secret == '') {
				_this.$toast.fail('请输入新的钱包秘钥！');
				return;
			}
			if (_this.pwd == '') {
				_this.$toast.fail('请输入交易密码！');
				return;
			}
			if (_this.pwd != _this.walData.pwd) {
				_this.$toast.fail('请输入的密码不正确，请重新输入！');
				_this.pwd = '';
				return;
			}
			var data = {
				secret_ro: _this.secret
			};
			_this.saveWallent(data);
			_this.$toast.success('修改钱包地址成功！');
			_this.$router.go(-1);
		}
	}
};
</script>

