<template>
	<div id="app">
		<router-view />
		<van-tabbar v-model="active" class="barWrap" v-show="$route.meta.navShow">
			<van-tabbar-item icon="home-o" replace to="/Home">
				<span class="itemSpan">{{$t('message.a1')}}</span>
				<template #icon="props">
					<img :src="props.active ? icon1.active : icon1.inactive" />
				</template>
			</van-tabbar-item>
			<van-tabbar-item icon="search" replace to="/Quote">
				<span class="itemSpan">{{$t('message.a2')}}</span>
				<template #icon="props">
					<img :src="props.active ? icon2.active : icon2.inactive" />
				</template>
			</van-tabbar-item>
			<van-tabbar-item icon="friends-o" replace to="/Exchange">
				<span class="itemSpan">{{$t('message.a3')}}</span>
				<template #icon="props">
					<img :src="props.active ? icon3.active : icon3.inactive" />
				</template>
			</van-tabbar-item>
			<van-tabbar-item icon="setting-o" replace to="/Find">
				<span class="itemSpan">{{$t('message.a4')}}</span>
				<template #icon="props">
					<img :src="props.active ? icon4.active : icon4.inactive" />
				</template>
			</van-tabbar-item>
			<van-tabbar-item icon="setting-o" replace to="/Assets">
				<span class="itemSpan">{{$t('message.a5')}}</span>
				<template #icon="props">
					<img :src="props.active ? icon5.active : icon5.inactive" />
				</template>
			</van-tabbar-item>
		</van-tabbar>
	</div>
</template>
<script>
export default {
	data() {
		return {
			active: 0,
			icon1: {
				active: require('./assets/img/tab1_c.png'),
				inactive: require('./assets/img/tab1.png')
			},
			icon2: {
				active: require('./assets/img/tab2_c.png'),
				inactive: require('./assets/img/tab2.png')
			},
			icon3: {
				active: require('./assets/img/tab3_c.png'),
				inactive: require('./assets/img/tab3.png')
			},
			icon4: {
				active: require('./assets/img/tab4_c.png'),
				inactive: require('./assets/img/tab4.png')
			},
			icon5: {
				active: require('./assets/img/tab5_c.png'),
				inactive: require('./assets/img/tab5.png')
			}
		};
	},
	created: function() {
		this.checkTab();
	},
	beforeUpdate: function() {
		this.checkTab();
		var _this = this;
		if (_this.getInfoData() == null) {
			_this.$router.push('/');
		}
	},
	methods: {
		checkTab: function() {
			var _this = this;
			var route = _this.$route.path;
			if (route == '/Home') {
				_this.active = 0;
			} else if (route == '/Quote') {
				_this.active = 1;
			} else if (route == '/Exchange') {
				_this.active = 2;
			} else if (route == '/Find') {
				_this.active = 3;
			} else if (route == '/Assets') {
				_this.active = 4;
			}
		}
	}
};
</script>

<style>
#app {
	font-family: Avenir, Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	font-size: 0.875rem;
}
.spanTitle {
	font-size: 1rem;
	font-weight: bold;
}
.navRightImg {
	width: 1.25rem;
	position: absolute;
	right: 1rem;
}
#nav {
	padding: 30px;
}

#nav a {
	font-weight: bold;
	color: #2c3e50;
}

#nav a.router-link-exact-active {
	color: #42b983;
}

.barWrap {
	background: rgba(255, 255, 255, 1);
	box-shadow: 0px 0px 43px 0px rgba(142, 189, 148, 0.35);
}
.conTop {
	margin-top: 3rem;
}
.sureBtnWrap {
	height: 2.625rem;
	background-color: #518cff;
	color: white;
	margin-top: 3rem;
	border-radius: 0.3125rem;
	font-weight: bold;
}
.conMagin {
	margin-left: 0.625rem;
	margin-right: 0.625rem;
}
</style>
