<template>
	<!-- 修改钱包密码 -->
	<div class="divColumn">
		<Title :title="$t('message.b56')"></Title>
		<div class="conTop divColumn">
			<div style="margin: 0rem 0.625rem;" class="divColumn">
				<div class="leftAndRight itemW">
					<span style="width: 30%;">{{ $t('message.b57') }}：</span>
					<van-field style="background-color:#f6f8fe;" v-model="oldPwd" :placeholder="$t('message.b58')" type="password"></van-field>
				</div>
				<div class="leftAndRight itemW">
					<span style="width: 30%;">{{ $t('message.b59') }}：</span>
					<van-field style="background-color:#f6f8fe;" v-model="pwd" :placeholder="$t('message.b60')" type="password"></van-field>
				</div>
				<div class="leftAndRight itemW">
					<span style="width: 30%;">{{ $t('message.b50') }}：</span>
					<van-field style="background-color:#f6f8fe;" v-model="pwdAg" :placeholder="$t('message.b61')" type="password"></van-field>
				</div>
				<div
					@click="sureUpdata"
					class="itemW divCenter"
					style="background-color: #518CFF;border-radius: 0.3125rem;margin-top: 2.5rem;font-size: 1rem;font-weight: 600;color: white;"
				>
					<span>{{ $t('message.b62') }}</span>
				</div>
				<!-- 温馨提示 -->
				<div class="divColumn" style="text-align: left;margin-top: 1.25rem;margin-bottom: 2.5rem;">
					<span style="margin-bottom: 0.625rem;">{{ $t('message.a80') }}</span>
					<span style="text-indent:2em;line-height: 1.5rem;">*{{ $t('message.b63') }}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
export default {
	components: {
		Title
	},
	data() {
		return {
			pwd: '',
			oldPwd: '',
			pwdAg: ''
		};
	},
	created: function() {},
	methods: {
		sureUpdata: function() {
			var _this = this;
			if (_this.oldPwd == '') {
				_this.$toast.fail(_this.$t('message.b58'));
				return;
			}
			if (_this.oldPwd != _this.getWallent().pwd) {
				_this.$toast.fail(_this.$t('message.b64'));
				return;
			}
			if (_this.pwd == '') {
				_this.$toast.fail(_this.$t('message.b65'));
				return;
			}
			if (_this.pwdAg == '') {
				_this.$toast.fail(_this.$t('message.b66'));
				return;
			}
			if (_this.pwd != _this.pwdAg) {
				_this.$toast.fail(_this.$t('message.b67'));
				return;
			}
			var data = {
				pwd: _this.pwd
			};
			_this.saveWallent(data);
			_this.$toast.success(_this.$t('message.b68'));
			_this.$router.go(-1);
		}
	}
};
</script>

<style></style>
