		<div style="width: 100%;position: relative;margin-top: 14%;">
			<div style="width: 60%;height: auto;position: absolute;right: 10%;" class="divColumn">
				<input :placeholder="$t('message.e46')" style="font-size: 0.75rem;text-align: center;background: none;font-size: 0.75rem;" v-model="address" readonly="true" />
				<div style="width: 100%;height: 0.0625rem;background-color: white;margin-top: 0.625rem;"></div>
			</div>
			<div style="position: absolute;right: 72%;">{{ $t('message.e47') }}：</div>
		</div>
		<div style="width: 100%;position: relative;margin-top: 14%;">
			<div style="width: 60%;height: auto;position: absolute;right: 10%;" class="divColumn">
				<input :placeholder="$t('message.e48')" style="font-size: 0.75rem;text-align: center;background: none;" v-model="name" />
				<div style="width: 100%;height: 0.0625rem;background-color: white;margin-top: 0.625rem;"></div>
			</div>
			<div style="position: absolute;right: 72%;">{{ $t('message.e49') }}：</div>
		</div>
		<div style="width: 100%;position: relative;margin-top: 14%;">
			<div style="width: 60%;height: auto;position: absolute;right: 10%;" class="divColumn">
				<input :placeholder="$t('message.e50')" type="password" style="font-size: 0.75rem;text-align: center;background: none;" v-model="password" />
				<div style="width: 100%;height: 0.0625rem;background-color: white;margin-top: 0.625rem;"></div>
				<span style="color: white;font-size: 0.625rem;width: 100%;text-align: left;margin-top: 0.1875rem;">·{{ $t('message.e51') }}</span>
			</div>
			<div style="position: absolute;right: 72%;">{{ $t('message.e52') }}：</div>
