<script>
/**
	 * 			name: _this.name.trim(),
				password: _this.pwd.trim(),
				tradepassword: _this.tradepassword.trim(),
				recommend_code: _this.recommend_code.trim(),
				nickname: _this.name.trim(),
				mobile: _this.mobile,
				area_code: '',
				code: _this.code.trim(),
				realname: _this.name.trim(),
				idcard: '',
				address: 'address',
				unknowd1: ' this.encrydata(privateKey)',
				unknowd2: ' this.encrydata(mnemonic)',
				mnemonic: _this.mnemonic
	  * */
import md5 from 'js-md5';
export default {
	data() {
		return {
			openBk: {
				backgroundImage: 'url(' + require('../../assets/img/op_bk.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%',
				minHeight: '640px'
			},
			openBtnBk: {
				backgroundImage: 'url(' + require('../../assets/img/nf4.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			curHeight: 0,
			name: '',
			password: '',
			address: '',
			recommend_code: '', //邀请人账号
			dataW: {}
		};
	},
	beforeMount: function() {
		var h = document.documentElement.clientHeight || document.body.clientHeight;
		this.curHeight = h;
		this.openBk.minHeight = h + 'px';
	},
	created: function() {
		var _this = this;
		_this.dataW = _this.getWallent();
		_this.address = _this.dataW.address;
	},
	methods: {
		sureOpen: function() {
			var _this = this;
			if (_this.recommend_code == '') {
				_this.$toast(_this.$t('message.e45'));
				return;
			}
			if (_this.address == '') {
				_this.$toast(_this.$t('message.e46'));
				return;
			}
			if (_this.name == '') {
				_this.$toast(_this.$t('message.e55'));
				return;
			}
			if (_this.password == '') {
				_this.$toast(_this.$t('message.e50'));
				return;
			}
			var data = {
				name: _this.name.trim(),
				password: _this.password.trim(),
				tradepassword: _this.password.trim(),
				recommend_code: _this.recommend_code.trim(),
				nickname: _this.name.trim(),
				mobile: '',
				area_code: '',
				code: '',
				realname: _this.name.trim(),
				idcard: '',
				address: _this.address,
				unknowd1: '',
				unknowd2: '',
				mnemonic: md5(_this.dataW.secret + '' + _this.dataW.address)
			};
			_this.postApi('api/Auth/register.html', data, function(res) {
				console.log(JSON.stringify(res));
				_this.$router.push({
					path: '/Exlogin',
					query: {
						type: 1
					}
				});
			});
		},
		btnBack: function() {
			this.$router.go(-1);
		},
		toLogin: function() {
			this.$router.push({
				path: '/Exlogin',
				query: {
					type: 1
				}
			});
		}
	}
};
</script>

