<template>
	<div class="divColumn" style="height: 100%;margin-bottom: 3.75rem;">
		<!-- 顶部背景 -->
		<div style="width: 100%;height: 11.25rem" :style="topBk">
			<img src="../../assets/img/find_share.png" style="width: 1.5rem;position: absolute;top: 0.625rem;right: 0.625rem;" />
		</div>
		<!-- 宫格栏 -->
		<div class="girdWrap">
			<van-grid square :border="false">
				<van-grid-item v-for="(item, index) in girdList" :key="index" @click="itemClick(index)">
					<img :src="item.icon" style="width: 1.8rem;margin-bottom: 0.625rem;" />
					<span style="font-size: 0.75rem;">{{ item.name }}</span>
				</van-grid-item>
			</van-grid>
		</div>
		<!-- 通知 -->
		<div class="noticeWrap divRow divColumnCenter " @click="noticeAd">
			<div class="noteText">{{ $t('message.b9') }}</div>
			<van-notice-bar :text="notic.title" class="nSWrap" />
		</div>
		<!-- 孵化专区 -->
		<div :style="fhBk" class="fhbkWrap">
			<h2 style="position: absolute;left: 1.875rem;top: 0.625rem;">{{ $t('message.e1') }}</h2>
			<span style="position: absolute;left:1.875rem;top:3.75rem;font-size: 1rem;">·{{ $t('message.e2') }}·</span>
			<!-- <span style="position: absolute;left:3.75rem;top:5.2rem;font-size: 0.875rem;">交易、低分险、快出售</span> -->
			<div :style="openBk" class="divCenter openWrap" @click="openJExchange">{{ btnText }}</div>
			<img src="../../assets/img/nf2.png" style="position: absolute;width: 6.75rem;right: 1.875rem;top: 3.875rem;" />
			<img src="../../assets/img/nf3.png" style="position: absolute;width: 2.1875rem;right: 6.875rem;top: 7.25rem;" />
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			topBk: {
				backgroundImage: 'url(' + require('../../assets/img/find_top.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			fhBk: {
				backgroundImage: 'url(' + require('../../assets/img/nf1.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			openBk: {
				backgroundImage: 'url(' + require('../../assets/img/nf4.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			girdList: [
				{
					name: this.$t('message.a56'),
					icon: require('../../assets/img/f1.png'),
					url: '/WalManger'
				},
				{
					name: this.$t('message.a57'),
					icon: require('../../assets/img/f2.png'),
					url: '/Transfer'
				},
				{
					name: this.$t('message.a58'),
					icon: require('../../assets/img/f3.png'),
					url: '/ActiveWalt'
				},
				{
					name: this.$t('message.a59'),
					icon: require('../../assets/img/f4.png'),
					// url: '/JxNode',
					url: '/NodeApply'
				},
				{
					name: this.$t('message.d62'),
					icon: require('../../assets/img/f5.png'),
					url: '/CurApply'
				},
				{
					name: this.$t('message.a61'),
					icon: require('../../assets/img/f6.png'),
					url: '/VerCode'
				},
				{
					name: this.$t('message.a62'),
					icon: require('../../assets/img/f7.png'),
					url: '/SetLang'
				},
				{
					name: this.$t('message.a63'),
					icon: require('../../assets/img/f8.png'),
					url: '/HelpCenter'
				}
			],
			isWal: false,
			notic: {},
			btnText: this.$t('message.e3'),
			isOpen: false
		};
	},
	mounted() {
		document.querySelector('html').setAttribute('style', 'background-color:#F2F2F4');
	},
	beforeDestroy() {
		document.querySelector('html').removeAttribute('style');
	},
	created: function() {
		var _this = this;
		_this.getNoticeInfo(function(res) {
			if (res.data[0].dataList.length > 0) {
				_this.notic = res.data[0].dataList[0];
			}
			console.log(JSON.stringify(res));
		});
		//判断是否添加钱包
		var dataW = _this.getWallent();
		if (dataW == null) {
			//没有添加钱包
			_this.isWal = false;
		} else {
			if (dataW.pwd == undefined) {
				_this.isWal = false;
			} else {
				_this.isWal = true;
			}
		}
		//判断是否开通竞价交易
		var userAcc = localStorage.getItem('ACC');
		if (userAcc == null) {
			//未登录
			_this.btnText = _this.$t('message.e3');
			_this.isOpen = false;
		} else {
			_this.btnText = _this.$t('message.e4');
			_this.isOpen = true;
		}
	},
	methods: {
		itemClick: function(index_) {
			var _this = this;
			var url = _this.girdList[index_].url;
			if (url == '') {
				_this.$toast(_this.$t('message.a18'));
			} else {
				if (url == '/WalManger' || url == '/Transfer' || url == '/ActiveWalt') {
					if (_this.isWal) {
						_this.$router.push(url);
					} else {
						_this.$dialog
							.confirm({
								title: _this.$t('message.b7'),
								message: _this.$t('message.b8'),
								confirmButtonText: _this.$t('message.b4'),
								cancelButtonText: _this.$t('message.b5')
							})
							.then(() => {
								// on confirm
								_this.$router.push({
									path: '/CreateWallent',
									query: {
										type: 1
									}
								});
							})
							.catch(() => {
								// on cancel
							});
					}
				} else {
					_this.$router.push(url);
				}
			}
		},
		noticeAd: function() {
			var _this = this;
			_this.$router.push('/Notice');
		},
		openJExchange: function() {
			var _this = this;
			if (!_this.isAddWalt()) {
				_this.$toast(_this.$t('message.e5'));
				return;
			}
			if (this.isOpen) {
				this.$router.push({
					path: '/JjExchange',
					query: {
						type: 1
					}
				});
			} else {
				this.$router.push('/JxExOpen');
			}
		}
	}
};
</script>

<style>
.fhbkWrap {
	margin: 0rem 0.625rem;
	margin-top: 0.625rem;
	position: relative;
	color: white;
	height: 12.5rem;
}
.openWrap {
	position: absolute;
	left: 1.875rem;
	bottom: 2.5rem;
	width: 6.4rem;
	height: 2.4rem;
	color: #745cf6;
	font-weight: 600;
}

.girdWrap {
	margin-left: 0.625rem;
	margin-right: 0.625rem;
	margin-top: -3rem;
	height: auto;
	background-color: white;
	z-index: 999;
	border-radius: 0.3125rem;
	padding: 0.625rem;
}
.noticeWrap {
	margin: 0rem 0.625rem;
	margin-top: 0.75rem;
	height: 2.625rem;
	background-color: white;
	border-radius: 0.3125rem;
	padding: 0rem 0.625rem;
}
.noteText {
	padding: 0.125rem 0.625rem;
	border: 0.0625rem solid red;
	color: red;
	font-size: 0.75rem;
}
.nSWrap {
	height: 2rem;
	width: 70%;
	background-color: white;
	color: black;
	font-size: 0.75rem;
}
</style>
