<template>
	<div class="divColumn">
		<div class="divColumn" style="width: 100%;position: fixed;background-color: white;z-index: 9999;">
			<img src="../../assets/img/ex_back.png" style="width: 1.5rem;margin: 1.125rem 0.75rem;" @click="btnBack" />
		</div>
		<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
			<van-list v-model="loading" :finished="finished" :finished-text="$t('message.a13')" @load="onLoad">
				<div style="margin: 0rem 0.625rem;margin-top: 3.75rem;" class="divColumn">
					<div class="rWrap" v-for="(item, index) in dataList" :key="index">
						<span style="font-size: 0.875rem;color: black;font-weight: 800;position: absolute;left: 0.625rem;top: 0.9375rem;" v-show="item.fromtype==2">{{$t('message.e41')}}</span>
						<span style="font-size: 0.875rem;color: black;font-weight: 800;position: absolute;left: 0.625rem;top: 0.9375rem;" v-show="item.fromtype==3">{{$t('message.e69')}}</span>
						<span style="position: absolute;right: 0.625rem;top: 0.9375rem;">{{$t('message.e31')}}：{{ item.create_time }}</span>
						<div class="divRow divColumnCenter" style="position: absolute;left: 0.625rem;bottom: 10%;">
							<span>{{$t('message.e42')}}(ACC)：</span>
							<span style="font-size: 1.125rem;font-weight: 800;color: #4884F6;" v-show="item.fromtype==2">+{{ item.pay_val }}</span>
							<span style="font-size: 1.125rem;font-weight: 800;color: red;" v-show="item.fromtype==3">{{ item.pay_val }}</span>
						</div>
						<span style="position: absolute;right: 0.625rem;bottom: 10%;font-weight: 800;" >{{$t('message.e30')}}</span>
					</div>
				</div>
			</van-list>
		</van-pull-refresh>
	</div>
</template>

<script>
var page = 1;
var pagesize = 10;
export default {
	data() {
		return {
			dataList: [],
			fromType: '2,3',
			loading: false,
			finished: false,
			refreshing: false
		};
	},
	created: function() {
		page = 1;
	},

	methods: {
		btnBack: function() {
			this.$router.go(-1);
		},
		onLoad() {
			if (this.refreshing) {
				this.dataList = [];
				this.refreshing = false;
			}
			this.getDataList();
		},
		getDataList: function() {
			var _this = this;
			_this.postApi(
				'api/Account/index.html',
				{
					page: page,
					pagesize: pagesize,
					fromtype: _this.fromType
				},
				function(res) {
					_this.dataList = _this.dataList.concat(res.data);
					console.log(JSON.stringify(res));
					page++;
					_this.loading = false;
					if (res.data.length == 0) {
						_this.finished = true;
					}
				}
			);
		},
		onRefresh() {
			// 清空列表数据
			this.finished = false;
			page = 1;
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.refreshing = true;
			this.loading = true;
			this.onLoad();
		}
	}
};
</script>

<style>
.rWrap {
	background: rgba(248, 253, 255, 1);
	border-radius: 5px;
	border: 1px solid rgba(226, 246, 255, 1);
	width: 100%;
	height: 5.2rem;
	margin-bottom: 0.625rem;
	position: relative;
}
</style>
