<template>
	<!-- 审核列表 -->
	<div class="divColumn">
		<div style="position: fixed;width: 100%;background-color: white;" class="divColumn">
			<div class="divColumn">
				<div style="width: 100%;position: relative;margin-top: 0.625rem;" class="divCenter">
					<img src="../../assets/img/ex_back.png" style="width: 1.5rem;position: absolute;left: 0.625rem;" @click="btnBack" />
					<span style="font-size: 1.125rem;font-weight: 600;">提币申请</span>
				</div>
				<div style="width: 100%;height: 5rem;" class="leftAndRight">
					<div class="divColumn divCenter" style="margin-left: 30%;" @click="unDone">
						<span class="titleUn" :class="{ titleChoi: type == 1 }">未处理</span>
						<div class="linnUn" v-show="type == 1"></div>
					</div>
					<div class="divColumn divCenter" style="margin-right: 30%;" @click="Done">
						<span class="titleUn" :class="{ titleChoi: type == 2 }">已处理</span>
						<div class="linnUn" v-show="type == 2"></div>
					</div>
				</div>
			</div>
		</div>
		<div style="width: 100%;height: auto;margin-top: 7.5rem;" class="divColumn">
			<div class="divColumn" v-for="(item, index) in checklist" :key="index">
				<div style="width: 100%;height: 0.0625rem;background: #F0F0F0;margin-bottom: 0.625rem;"></div>
				<div style="width: 100%;height: 7rem;" class="divColumn">
					<div style="width: 100%;" class="leftAndRight">
						<span style="margin-left: 0.625rem;">账号：{{ item.user_name }}</span>
						<span style="margin-right: 0.625rem;">提币数量：{{ Number(item.paymoney).toFixed(4) }}</span>
					</div>
					<span style="text-align: left;padding-left: 0.625rem;margin-top: 0.625rem;">地址：{{ item.address }}</span>
					<span style="text-align: left;padding-left: 0.625rem;margin-top: 0.625rem;">时间：{{ item.create_time }}</span>
					<span
						v-show="type == 1"
						@click="itemDone(index)"
						style="background-color:#2C54FF;color: white;padding: 0.25rem 1.6rem;border-radius: 0.3125rem;width: 5rem;margin-left: 60%;"
					>
						去处理
					</span>
					<span v-show="type == 2" style="background-color:#FF9898;color: white;padding: 0.25rem 1.6rem;border-radius: 0.3125rem;width: 5rem;margin-left: 60%;">
						已处理
					</span>
					<!-- 			<span style="position: absolute;left: 0.625rem;top: 0.625rem;">账号：{{ item.user_name }}</span>
					<span style="position: absolute;right: 0.625rem;top: 0.625rem;">提币数量：{{ Number(item.paymoney).toFixed(4) }}</span>
					<span style="position: absolute;left: 0.625rem;top: 40%;">地址：{{ item.address }}</span>
					<span style="position: absolute;left: 0.625rem;bottom: 0.625rem;">时间：{{ item.create_time }}</span>
					<span
						v-show="type == 1"
						@click="itemDone(index)"
						style="position: absolute;right: 0.625rem;bottom: 0.625rem;background-color:#2C54FF;color: white;padding: 0.25rem 1.6rem;border-radius: 0.3125rem;"
					>
						去处理
					</span>
					<span
						v-show="type == 2"
						style="position: absolute;right: 0.625rem;bottom: 0.625rem;background-color:#FF9898;color: white;padding: 0.25rem 1.6rem;border-radius: 0.3125rem;"
					>
						已处理
					</span> -->
				</div>
			</div>
		</div>
	</div>
</template>

<script>
var page = 1;
var pagesize = 10000000;
export default {
	data() {
		return {
			checklist: [],
			type: 1 // 1 未处理  2 已处理
		};
	},
	created: function() {
		this.getCheckList();
	},
	methods: {
		getCheckList: function() {
			/* Withdraw/checklist.html  page,pagesize,currency_id  */
			var _this = this;
			var status = '2';
			if (_this.type == 1) {
				status = '2';
			} else {
				status = '3';
			}
			var data = {
				page: page,
				pagesize: pagesize,
				status: status
			};
			_this.postApi('api/Withdraw/checklist.html', data, function(res) {
				console.log(JSON.stringify(res));
				_this.checklist = res.data;
			});
		},
		btnBack: function() {
			this.$router.go(-1);
		},
		unDone: function() {
			var _this = this;
			_this.type = 1;
			_this.getCheckList();
		},
		Done: function() {
			var _this = this;
			_this.type = 2;
			_this.getCheckList();
		},
		itemDone: function(index) {
			var _this = this;
			var item = _this.checklist[index];
			_this.$router.push({
				path: '/SureCheck',
				query: {
					name: item.user_name,
					paymoney: item.paymoney,
					address: item.address,
					id_: item.id
				}
			});
		}
	}
};
</script>

<style>
.titleUn {
	color: #999999;
	font-weight: 800;
}
.linnUn {
	height: 0.125rem;
	background-color: #2c54ff;
	width: 100%;
}
.titleChoi {
	color: #2c54ff;
	font-weight: 800;
}
</style>
