				<span class="neRi1">{{ $t('message.a46') }}</span>
			</div>
			<div style="height: 0.0625rem;width: 100%;background-color: #F0F0F0;"></div>
		</div>
		<!-- 具体列表数据 -->
		<div style="margin-top: 8.1rem;">
			<!-- 市场委托 -->
			<div v-show="active == 0">
				<div class="divRow">
					<!-- <div style="margin: 0rem 0.625rem;height:auto;" class="leftAndRight">
						<div class="divColumn" style="width: 50%;">
							<div style="width: 50%;height:100%;" class="leftAndRight">
