							<span style="color: #d81e06;font-weight: 600;">{{ Number(item.price).toFixed(6) }}</span>
							<span class="numLeftBk" v-show="curName == 'ETH'">{{ Number(item.amount).toFixed(4) }}</span>
							<span class="numLeftBk" v-show="curName != 'ETH'">{{ Number(item.amount).toFixed(2) }}</span>
							<div class="btLine"></div>
						</div>
					</div>
					<div style="width: 50%;height: auto;padding-right: 0.625rem;">
						<div style="width: 100%;height:1.9rem;position: relative;" class="leftAndRight" v-for="(item, index) in wtList" :key="index">
							<span class="numLeftBk" style="background-color:  rgb(7, 193, 96);color: white;" v-show="curName != 'ETH'">{{ Number(item.amount).toFixed(2) }}</span>
							<span class="numLeftBk" style="background-color:  rgb(7, 193, 96);color: white;" v-show="curName == 'ETH'">{{ Number(item.amount).toFixed(4) }}</span>
