						<span class="neL1">{{ (item[3] / 1000) | moment }}</span>
						<span class="neL2">{{ Number(item[2]).toFixed(6) }}</span>
						<span class="neRi2" v-show="curName == 'ETH'">{{ Number(item[1]).toFixed(4) }}</span>
						<span class="neRi2" v-show="curName != 'ETH'">{{ Number(item[1]).toFixed(2) }}</span>
						<span class="neRi1" style="color: rgb(7, 193, 96);">
							<span v-show="item[4] == 0" style="color: red;">{{ $t('message.a52') }}</span>
