</template>

<script>
export default {
	data() {
		return {
			value1: 0,
			option1: [{ text: 'AICC-USDT', value: 0 }, { text: 'ETH-USDT', value: 1 }, { text: 'XRP-USDT', value: 2 }, { text: 'ACC-USDT', value: 3 }],
			active: 0,
			wtList: [], // 卖单
			wtBuyList: [],
			newList: [],
			currency: 'SWT-JUSDT',
			curName: 'AICC'
		};
	},
	created: function() {
		var _this = this;
		_this.currency = _this.$route.query.currency;
		console.log('>>>>>>>>' + _this.currency);
		if (_this.currency == 'SWT-JUSDT') {
			_this.value1 = 0;
			_this.curName = 'AICC';
		} else if (_this.currency == 'JETH-JUSDT') {
			_this.value1 = 1;
			_this.curName = 'ETH';
		} else if (_this.currency == 'JXRP-JUSDT') {
			_this.value1 = 2;
			_this.curName = 'XRP';
		} else if (_this.currency == 'JACC-JUSDT') {
			_this.value1 = 3;
			_this.curName = 'ACC';
		}
		_this.getDataInfo();
	},
	methods: {
		getDataInfo: function() {
			var _this = this;
			//获取深度
			_this.getDePeth(_this.currency, function(res) {
				_this.wtList = res.data.asks;
				_this.wtBuyList = res.data.bids;
			});
			//获取最近市场交易
			_this.getLatExRecord(_this.currency, function(res) {
				console.log(JSON.stringify(res));
				_this.newList = res.data;
			});
			_this.active = _this.$route.query.type;
		},
		wtClick: function() {
			var _this = this;
			_this.active = 0;
		},
		newsEx: function() {
			var _this = this;
			_this.active = 1;
		},
		btnBack: function() {
			this.$router.go(-1);
		},
		change: function(value) {
			var _this = this;
			if (value == 0) {
				_this.currency = 'SWT-JUSDT';
				_this.curName = 'AICC';
			} else if (value == 1) {
				_this.currency = 'JETH-JUSDT';
				_this.curName = 'ETH';
			} else if (value == 2) {
				_this.currency = 'JXRP-JUSDT';
				_this.curName = 'XRP';
			} else if (value == 3) {
				_this.currency = 'JACC-JUSDT';
				_this.curName = 'ACC';
			}
			_this.getDataInfo();
		}
	}
};
</script>

