	right: 16%;
	font-weight: bold;
}
.neRi1 {
	position: absolute;
	right: 0.625rem;
}
.numLeftBk {
	background-color: #d81e06;
	height: 100%;
	line-height: 1.9rem;
	padding: 0rem 0.125rem;
	text-align: center;
	color: white;
	opacity: 0.7;
	font-size: 0.8125rem;
}
.btLine {
