<template>
	<div class="divColumn">
		<div style="margin: 0rem 0.625rem;margin-top: 1.25rem;text-align: left;" class="divColumn">
			<div style="width: 100%;" class="leftAndRight">
				<img src="../../assets/img/ex_back.png" style="width: 1.5rem;" @click="btnBack" />
				<img src="../../assets/img/order_img.png" style="width: 1.5rem;" @click="hzOrder" />
			</div>
			<div style="width: 100%;color: #3B79F4;text-align: left;">
				<h2>{{ $t('message.e40') }}</h2>
			</div>
			<div style="height: 5.625rem;width: 100%;border: 0.0625rem solid #EEEEEE;border-radius: 0.3125rem;position: relative;font-size: 0.875rem;">
				<div style="height: 0.0625rem;width: 100%;background-color:#EEEEEE;position: absolute;top: 50%;"></div>
				<div
					@click="exChangeType"
					style="width: 20%;height: 100%;background-color: #EEEEEE;position: absolute;right: 0rem;border-radius: 0rem 0.3125rem 0.3125rem 0rem;"
					class="divCenter"
				>
					<img src="../../assets/img/ex_img.png" style="width: 3.25rem;" />
				</div>
				<div style="height: 49%;width: 80%;position: absolute;top: 0rem;" class="divRow divColumnCenter">
					<div style="width: 0.75rem;height: 0.75rem;background-color: #0EAD18;border-radius: 50%;margin-left: 1rem;"></div>
					<span style="color: #0EAD18;margin-left: 1.25rem;">{{ $t('message.e60') }}</span>
					<span style="color: #000000;font-weight: 800;margin-left: 1rem;" v-show="type == 1">{{ $t('message.e58') }}</span>
					<span style="color: #000000;font-weight: 800;margin-left: 1rem;" v-show="type == 2">{{ $t('message.e59') }}</span>
				</div>
				<div style="height: 49%;width: 80%;position: absolute;bottom: 0rem;" class="divRow divColumnCenter">
					<div style="width: 0.75rem;height: 0.75rem;background-color: #FF3131;border-radius: 50%;margin-left: 1rem;"></div>
					<span style="color: #FF3131;margin-left: 1.25rem;">{{ $t('message.e61') }}</span>
					<span style="color: #000000;font-weight: 800;margin-left: 1rem;" v-show="type == 1">{{ $t('message.e59') }}</span>
					<span style="color: #000000;font-weight: 800;margin-left: 1rem;" v-show="type == 2">{{ $t('message.e58') }}</span>
				</div>
			</div>
			<span style="margin: 1.25rem 0rem;">{{ $t('message.a71') }}</span>
			<span style="font-weight: 600;">ACC</span>
			<div style="width: 100%;height: 0.0625rem;background-color: #EEEEEE;margin-top: 0.625rem;"></div>
			<span style="margin: 1.25rem 0rem;">{{ $t('message.e42') }}</span>
			<div style="width: 100%;" class="leftAndRight">
				<input :placeholder="$t('message.e62')" type="number" style="background: none;padding: 0rem;margin: 0rem;font-weight: 800;" v-model="count" />
				<div class="divRow divColumnCenter" @click="exAll">
					<span style="color: #666666;margin-top: 0.25rem;">ACC</span>
					<span style="font-weight: 800;margin-left: 0.625rem;">{{ $t('message.e35') }}</span>
				</div>
			</div>
			<div style="width: 100%;height: 0.0625rem;background-color: #EEEEEE;margin-top: 0.625rem;"></div>
			<span style="color: #DCDCDC;margin-top: 0.3125rem;font-weight: 600;">
				{{ $t('message.a33') }}
				<span v-show="type == 2">{{ balSys }}</span>
				<span v-show="type == 1">{{ bal }}</span>
			</span>
			<div
				@click="sureExchange"
				style="width: 100%;height: 3.25rem;background-color: #3B79F4;border-radius: 0.3125rem;color: white;margin-top: 4.375rem;"
				class="divCenter divColumn"
			>
				<span style="font-size: 1rem;">{{ $t('message.e10') }}</span>
				<div style="width: 2.625rem;height: 0.125rem;background-color: white;"></div>
			</div>
		</div>
		<van-dialog
			v-model="showPwd"
			:title="$t('message.b48')"
			show-cancel-button
			@confirm="sureBtn"
			:confirm-button-text="$t('message.b4')"
			:cancel-button-text="$t('message.b5')"
		>
			<div style="width: 100%;height: 3.625rem;" class="divColumn">
				<div style="margin: 0.875rem;"><van-field style="background-color:#f6f8fe;" v-model="pwd" :placeholder="$t('message.b30')" type="password"></van-field></div>
			</div>
		</van-dialog>
	</div>
</template>

<script>
import JCCExchange from 'jcc_exchange';

export default {
	data() {
		return {
			type: 1, // 1 币币账户到竞价账户， 2 竞价账户到币币账户
			user: {},
			balSys: 0, //为系统内的资产
			bal: 0, //链上的资产
			count: '', //划转数量
			showPwd: false,
			pwd: '',
			isChoice: false,
			address: '', //转出的钱包地址
			sysAddress: '', //系统收币钱包地址
			sysSecret: '', //系统秘钥
			title: 'JACC', //需要获取到的当前币的余额
			currencyData: {},
			walData: {}, //钱包信息
			currency_id: '' //系统币的ID
		};
	},
	created: function() {
		this.getInitData();
	},
	methods: {
		getInitData: function() {
			var _this = this;
			this.upDataUser(function(res) {
				_this.user = res;
				_this.balSys = Number(res.balance_1_1).toFixed(4);
			});
			_this.getSysData();
			//判断当前是否有那么多资产
			_this.walData = _this.getWallent();
			_this.getWallentBal(function(res) {
				console.log(JSON.stringify(res));
				for (var i = 0; i < res.data.length; i++) {
					if (_this.title == res.data[i].currency) {
						_this.currencyData = res.data[i];
						_this.bal = (Number(_this.currencyData.value) - Number(_this.currencyData.freezed)).toFixed(6);
					}
				}
			});
		},
		getSysData: function() {
			var _this = this;
			_this.postApi('api/user/balances.html', {}, function(res) {
				console.log(JSON.stringify(res));
				_this.sysAddress = res.data.ACC.recvaddress;
				_this.currency_id = res.data.ACC.id;
			});
		},
		btnBack: function() {
			this.$router.go(-1);
		},
		hzOrder: function() {
			this.$router.push('/hzOrder');
		},
		exChangeType: function() {
			if (this.type == 1) {
				this.type = 2;
			} else {
				this.type = 1;
			}
			this.count = '';
		},
		exAll: function() {
			if (this.type == 1) {
				this.count = this.bal;
			} else {
				this.count = this.balSys;
			}
		},
		sureExchange: function() {
			if (this.type == 1) {
				if (Number(this.count) > this.bal) {
					this.$toast.fail(this.$t('message.e63'));
					return;
				}
			}
			if (this.type == 2) {
				if (Number(this.count) > this.balSys) {
					this.$toast.fail(this.$t('message.e64'));
					return;
				}
			}
			this.showPwd = true;
		},
		async sureBtn() {
			var _this = this;
			if (_this.pwd.trim() == '') {
				_this.$toast.fail(_this.$t('message.e65'));
				return;
			}
			if (_this.pwd.trim() != _this.walData.pwd) {
				_this.$toast.fail(_this.$t('message.e66'));
				_this.pwd = '';
				return;
			}
			_this.$toast.loading({
				forbidClick: true,
				duration: 0
			});
			try {
				if (_this.type == 1) {
					// type=1，币币账户到竞价账户 用户钱包到系统钱包，type=2 竞价账户到币币账户，系统钱包到用户钱包
					JCCExchange.init(_this.getInfoData().jcNodes);
					var address = ''; //转出钱包地址
					var secret = ''; //转出钱包秘钥
					var to = _this.sysAddress; //互转到的系统钱包地址
					if (_this.type == 1) {
						address = _this.walData.address; //用户自己的钱包地址
						secret = _this.walData.secret; //用户自己的秘钥
						to = _this.sysAddress;
					} else {
						address = _this.sysAddress; //系统钱包地址
						secret = _this.sysSecret; //系统秘钥
						to = _this.walData.address;
					}
					var amount = _this.count; //交易数量
					var memo = '划转'; //描述
					var token = _this.title; //需要转的币种
					const issuer = 'jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or'; // the default value is "jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or"
					console.log(address, secret, amount, memo, to, token, issuer);
					var hash = await JCCExchange.transfer(address, secret, amount, memo, to, token, issuer);
					if (hash) {
						//互转成功
						_this.$toast.success(_this.$t('message.e67'));
						_this.pwd = '';
					} else {
						_this.$toast.fail(_this.$t('message.e68'));
					}
				} else {
					// Withdraw/createWithdraw.html   tradepassword  count    currency_id  币的id  recvaddress  提币地址
					_this.postApi(
						'api/Withdraw/createWithdraw.html',
						{
							tradepassword: _this.pwd,
							count: _this.count,
							currency_id: _this.currency_id, //币种id
							recvaddress: _this.walData.address
						},
						function(res) {
							_this.$toast.success(_this.$t('message.e67'));
							_this.getInitData();
							console.log(res);
						}
					);
				}
			} catch (e) {
				console.log(e);
				//TODO handle the exception
				_this.$toast.clear();
			}
		}
	}
};
</script>

<style></style>
