<template>
	<!-- 交易 -->
	<div class="divColumn" style="color: black;">
		<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
			<van-list v-model="loading" :finished="finished" :finished-text="$t('message.a13')" :loading-text="$t('message.b6')" @load="onLoad">
				<div style="width: 100%;height: 3.6rem;background-color: white;position: relative;" class="divCenter">
					<img src="../../assets/img/nex1.png" style="width: 2rem;position: absolute;left: 0.625rem;" @click="curRemark" />
					<img src="../../assets/img/nex2.png" style="width: 2rem;position: absolute;left: 3rem;" @click="tokLine" />
					<div class="divRow divColumnCenter" style="font-size: 1.25rem;font-weight: 600;color: #666666;" @click="showTopPop = true">
						<span>{{ itemOptions.name }}</span>
						<img src="../../assets/img/nex3.png" style="width: 1.5rem;height: 1.5rem;margin: 0rem 0.625rem;" />
						<span>{{ itemOptions.pai }}</span>
					</div>
					<div class="divCenter qhWrap" @click="showTopPop = true">切换</div>
				</div>
				<!-- 		<div style="height: 2.4rem;width: 100%;" class="leftAndRight">
					<div style="width: 50%;" class="leftAndRight">
						<van-dropdown-menu style="height: 1.875rem;"><van-dropdown-item v-model="value1" :options="option1" @change="change" /></van-dropdown-menu>
					 
						<span class="jianjieWrap" @click="curRemark">{{ $t('message.a28') }}</span>
					</div>
					<div style="width: 50%;" class="leftAndRight">
						<span></span>
						<div class="divRow divColumnCenter" @click="tokLine">
							<img src="../../assets/img/love.png" style="width: 1.6rem;" />
							<img src="../../assets/img/kx_img.png" style="width: 1.2rem;margin-right: 0.625rem;margin-left: 1rem;" />
						</div>
					</div>
				</div> -->
				<!-- 价格 -->
				<div class="leftAndRight" style="width: 100%;margin-top: 0.5rem;">
					<div class="divRow divColumnCenter" style="margin-left: 0.625rem;">
						<span style="font-weight: bold;margin-right: 0.375rem;">{{ Number(curQuote.price).toFixed(4) }}</span>
						<span style="color: #999999;">≈¥{{ (Number(curQuote.price).toFixed(4) * 7).toFixed(4) }}</span>
					</div>
					<div class="divRow divColumnCenter" style="margin-right: 0.625rem;">
						<span style="color: #d81e06;margin-right: 0.875rem;" v-show="curQuote.zt > 0 || curQuote.zt == 0">+{{ curQuote.zt }}%</span>
						<span style="color: #d81e06;margin-right: 0.875rem;" v-show="curQuote.zt < 0">{{ curQuote.zt }}%</span>
						<img src="../../assets/img/up_arrow.png" v-show="curQuote.zt > 0 || curQuote.zt == 0" class="arrowWrap" />
						<img src="../../assets/img/down_arrow.png" v-show="curQuote.zt < 0" class="arrowWrap" />
					</div>
				</div>
				<!-- 高低量 -->
				<div class="leftAndRight" style="margin-top: 0.5   rem;">
					<div style="width: 50%;font-size: 0.75rem;" class="leftAndRight">
						<div style="margin-left: 0.625rem;" class="divRow divColumnCenter">
							<span>{{ $t('message.a29') }} {{ Number(curQuote.zg).toFixed(6) }}</span>
							<img src="../../assets/img/up_arrow.png" class="arrowWrap" />
						</div>
						<div class="divRow divColumnCenter">
							<span>{{ $t('message.a30') }} {{ Number(curQuote.zd).toFixed(6) }}</span>
							<img src="../../assets/img/down_arrow.png" class="arrowWrap" />
						</div>
					</div>
					<span style="margin-right: 0.625rem;">
						{{ $t('message.a27') }}
						<span style="font-weight: bold;">{{ Number(curQuote.count).toFixed(2) }}</span>
					</span>
				</div>
				<!-- 买入卖出 市场委托 -->
				<div class="divRow" style="margin-top: 0.625rem;">
					<BuySell class="buySell" ref="childBS" :bName="bName" :bal_U="bal_U" :freezed_U="freezed_U" :bal="bal" :fatherMethod="TimeWatch" :freezed="freezed"></BuySell>
					<div style="width: 2%;"></div>
					<Wt class="buySell" ref="child" :currency="currency" style="margin-top: -0.68rem;"></Wt>
				</div>
				<!-- 当前委托 交易记录 -->
				<div style="margin: 0rem 0.625rem;position: relative;">
					<van-tabs v-model="active" :border="false" :line-height="2">
						<van-tab :title="$t('message.a43')">
							<div class="divRow divColumnCenter recordTitleWrap">
								<span style="width: 30%;text-align: left;">{{ $t('message.a46') }}</span>
								<span style="width: 25%;" class="divCenter">{{ $t('message.a47') }}</span>
								<span style="width: 25%;" class="divCenter">{{ $t('message.a48') }}</span>
								<span style="width: 25%;" class="divCenter">{{ $t('message.a49') }}</span>
								<span style="width: 20%;" class="divCenter">{{ $t('message.a50') }}</span>
							</div>
							<div class="divRow divColumnCenter itemWtListWrap" v-for="(item, index) in wtList" :key="index">
								<span style="width: 30%;text-align: left;">
									<span v-show="item.flag == 1" style="color: #d81e06;">
										{{ item.takerPays.currency.replace('SWTC', 'AICC').replace('J', '') }}/{{
											item.takerGets.currency.replace('SWTC', 'AICC').replace('J', '')
										}}
									</span>
									<span v-show="item.flag == 2" style="color: rgb(7, 193, 96);">
										{{ item.takerGets.currency.replace('SWTC', 'AICC').replace('J', '') }}/{{
											item.takerPays.currency.replace('SWTC', 'AICC').replace('J', '')
										}}
									</span>
								</span>
								<!-- 价格 卖 得到的总价/卖出的数量-->
								<span style="width: 25%;" class="divCenter" v-show="item.flag == 2">
									{{ Number(Number(item.takerPays.value) / Number(item.takerGets.value)).toFixed(4) }}
								</span>
								<span style="width: 25%;" class="divCenter" v-show="item.flag == 1">
									{{ Number(Number(item.takerGets.value) / Number(item.takerPays.value)).toFixed(4) }}
								</span>
								<!-- 数量 买  -->
								<span style="width: 25%;" class="divCenter" v-show="item.flag == 1">{{ item.takerPays.value }}</span>
								<span style="width: 25%;" class="divCenter" v-show="item.flag == 2">{{ item.takerGets.value }}</span>
								<!-- 总价 -->
								<span style="width: 25%;" class="divCenter" v-show="item.flag == 2">{{ item.takerPays.value }}</span>
								<span style="width: 25%;" class="divCenter" v-show="item.flag == 1">{{ item.takerGets.value }}</span>
								<div style="width: 20%;color: #999999;" class="divCenter" @click="itemCance(item.seq)">
									<div style="background-color: #F0F0F0;padding: 0.125rem 0.25rem;">{{ $t('message.a51') }}</div>
								</div>
							</div>
						</van-tab>
						<van-tab :title="$t('message.a44')">
							<div class="divRow divColumnCenter recordTitleWrap">
								<span style="width: 30%; text-align: left;">{{ $t('message.a46') }}</span>
								<span style="width: 25%;" class="divCenter">{{ $t('message.a81') }}</span>
								<span style="width: 25%;" class="divCenter">{{ $t('message.a47') }}</span>
								<span style="width: 25%;" class="divCenter">{{ $t('message.a48') }}</span>
								<span style="width: 20%;" class="divCenter">{{ $t('message.a82') }}</span>
							</div>
							<div class="divRow divColumnCenter itemWtListWrap" v-for="(item, index) in exRecordX" :key="index">
								<span style="width: 30%;color: #999999;text-align: left;">
									<!-- 		<span v-show="item.type == 'OfferCancel'">撤销委托</span>
									<span v-show="item.type == 'OfferCreate'">创建委托</span>
									<span v-show="item.type == 'OfferAffect'">被动成交</span>
									<span v-show="item.type == 'Receive'">收到转账</span> -->
									<span v-show="item.flag == 1" style="color: #d81e06;">
										{{
											item.takerPays.currency
												.replace('SWTC', 'AICC')
												.replace('J', '')
												.replace('J', '')
										}}/{{ item.takerGets.currency.replace('SWTC', 'AICC').replace('J', '') }}
									</span>
									<span v-show="item.flag == 2" style="color: rgb(7, 193, 96);">
										{{
											item.takerGets.currency
												.replace('SWTC', 'AICC')
												.replace('J', '')
												.replace('J', '')
										}}/{{
											item.takerPays.currency
												.replace('SWTC', 'AICC')
												.replace('J', '')
												.replace('J', '')
										}}
									</span>
								</span>
								<span style="width: 25%;color: #999999;" class="divCenter">{{ (item.time + 946684800) | moment }}</span>
								<!-- 价格 -->
								<span style="width: 25%;color: #999999;" class="divCenter" v-show="item.flag == 1">
									{{ (Number(item.takerGets.value) / Number(item.takerPays.value)).toFixed(4) }}
								</span>
								<span style="width: 25%;color: #999999;" class="divCenter" v-show="item.flag == 2">
									{{ (Number(item.takerPays.value) / Number(item.takerGets.value)).toFixed(4) }}
								</span>

								<!-- 数量 -->
								<span style="width: 25%;color: #999999;" class="divCenter" v-show="item.flag == 1">{{ item.takerPays.value }}</span>
								<span style="width: 25%;color: #999999;" class="divCenter" v-show="item.flag == 2">{{ item.takerGets.value }}</span>
								<!-- 状态 -->
								<span style="width: 20%;color: #999999;" class="divCenter">
									<span v-show="item.type == 'OfferCancel'">{{ $t('message.a83') }}</span>
									<span v-show="item.type == 'OfferCreate'">{{ $t('message.a84') }}</span>
									<span v-show="item.type == 'OfferAffect'">{{ $t('message.a85') }}</span>
									<span v-show="item.type == 'Receive'">{{ $t('message.a86') }}</span>
									<span v-show="item.type == 'Send'">{{ $t('message.a87') }}</span>
								</span>
							</div>
						</van-tab>
						<van-tab disabled></van-tab>
						<span class="cxWrap" @click="canceOrder">{{ $t('message.a45') }}</span>
					</van-tabs>
				</div>
			</van-list>
		</van-pull-refresh>
		<van-popup v-model="showTopPop" position="top" :style="{ 'min-height': '30%' }" round>
			<div style="height: 100%;margin: 0rem 1rem;margin-bottom: 1.25rem;" class="divColumn">
				<div style="height: 1.875rem;width: 100%;"></div>
				<div style="width: 100%;height: auto;" class="divColumn" v-for="(item, index) in optionsN" :key="index">
					<div style="height: 2rem;width: 100%;" class="leftAndRight">
						<img src="../../assets/img/nex1.png" style="width: 2rem;" v-show="item.isChoi" @click="curRemark" />
						<span v-show="!item.isChoi"></span>
						<div class="divRow divColumnCenter itemPop" :class="{ itemPopCh: item.isChoi }" @click="poItemClick(index)">
							<span>{{ item.name }}</span>
							<img src="../../assets/img/nex3.png" style="width: 1.5rem;height: 1.5rem;margin: 0rem 0.625rem;" />
							<span>{{ item.pai }}</span>
						</div>
						<img src="../../assets/img/nex2.png" style="width: 2rem;" v-show="item.isChoi" @click="tokLine" />
						<span v-show="!item.isChoi"></span>
					</div>
					<div style="width: 100%;height: 0.0625rem;background-color: #EEEEEE;margin: 0.75rem 0rem;" v-show="index != optionsN.length - 1"></div>
				</div>
			</div>
		</van-popup>
		<div style="height: 3.75rem;width: 100%;"></div>
	</div>
</template>

<script>
import BuySell from '@/components/buy_sell.vue';
import Wt from '@/components/wt.vue';
import JCCExchange from 'jcc_exchange';
import BigNumber from 'bignumber.js';
export default {
	components: {
		BuySell,
		Wt
	},
	data() {
		return {
			value1: 0,
			option1: [{ text: 'AICC-USDT', value: 0 }, { text: 'ETH-USDT', value: 1 }, { text: 'XRP-USDT', value: 2 }, { text: 'XRP-USDT', value: 2 }],
			optionsN: [
				{ name: 'AICC', pai: 'USDT', value: 0, isChoi: true },
				{ name: 'ETH', pai: 'USDT', value: 1, isChoi: false },
				{ name: 'XRP', pai: 'USDT', value: 2, isChoi: false },
				{ name: 'ACC', pai: 'USDT', value: 3, isChoi: false }
			],
			itemOptions: {},
			loading: false,
			finished: false,
			refreshing: false,
			list: [],
			active: 0,
			choiceName: 'SWT',
			curQuote: {},
			wtList: [],
			exRecord: [],
			exRecordX: [],
			walData: {},
			queData: [],
			bName: 'AICC',
			curValce: 0,
			bal_U: '0',
			freezed_U: '0',
			bal: '0',
			freezed: '0',
			walBal: {},
			currency: 'SWT-JUSDT',
			time: 15,
			showTopPop: false
		};
	},
	created: function() {
		var _this = this;
		_this.getQue();
		_this.getRefushData();
		_this.walData = _this.getWallent();
		_this.itemOptions = _this.optionsN[0];
	},
	methods: {
		poItemClick: function(index) {
			var _this = this;
			for (var i = 0; i < _this.optionsN.length; i++) {
				if (index == i) {
					_this.optionsN[i].isChoi = true;
					_this.itemOptions = _this.optionsN[i];
					_this.change(_this.optionsN[i].value);
				} else {
					_this.optionsN[i].isChoi = false;
				}
			}
			_this.showTopPop = false;
		},
		tokLine: function() {
			// this.$router.push('/Kline');
			this.$toast('敬请期待');
		},
		TimeWatch() {
			var _this = this;
			_this.$toast.loading({
				forbidClick: true,
				duration: 0
			});
			const interval = window.setInterval(() => {
				//打印每次递归函数打印time值
				console.log(this.time);
				//time值自减
				this.time--;
				if (this.time <= 0) {
					//定时后 再请求数据
					_this.getQue();
					_this.getRefushData();
					_this.walData = _this.getWallent();
					_this.$toast.success('成功！');
					// _this.$toast.clear();
					window.clearInterval(interval);
					_this.time = 15;
				}
			}, 1000);
		},
		getRefushData: function() {
			//获取当前委托数据
			//获取当前委托
			var _this = this;
			if (!_this.isAddWalt()) {
				return;
			}
			_this.getWtList(function(res) {
				_this.wtList = res.data.list;
			});
			//获取当前交易记录
			_this.getExRecord(function(res) {
				_this.exRecordX = [];
				for (var i = 0; i < res.data.list.length; i++) {
					if (res.data.list[i].takerGets != undefined) {
						_this.exRecordX.push(res.data.list[i]);
					}
				}
			});
			// 获取钱包余额
			_this.getWallentBal(function(res) {
				_this.walBal = res;
				_this.getBalAndfreezed(_this.choiceName);
				_this.itemOptions = _this.optionsN[_this.curValce];
			});
		},
		//获取可用和冻结的数量
		getBalAndfreezed: function(name) {
			var _this = this;
			if (!_this.isAddWalt()) {
				return;
			}
			for (var i = 0; i < _this.walBal.data.length; i++) {
				var item = _this.walBal.data[i];
				if (item.currency == 'JUSDT') {
					_this.bal_U = new BigNumber(item.value).toFixed(6);
					_this.freezed_U = new BigNumber(item.freezed).toFixed(6);
				}
				if (item.currency == name) {
					_this.bal = new BigNumber(Number(item.value) - Number(item.freezed)).toFixed(6);
					_this.freezed = new BigNumber(item.freezed).toFixed(6);
				}
			}
		},
		getQue: function() {
			var _this = this;
			_this.getQuote(function(res) {
				_this.queData = res;
				console.log(JSON.stringify(res));
				for (var i = 0; i < res.length; i++) {
					if (_this.choiceName == res[i].name && res[i].pai == 'JUSDT') {
						_this.curQuote = res[i];
						console.log('>>>>>>>' + JSON.stringify(_this.curQuote));
					}
				}
			});
		},
		onLoad() {
			setTimeout(() => {
				if (this.refreshing) {
					this.list = [];
					this.refreshing = false;
				}

				for (let i = 0; i < 10; i++) {
					this.list.push(this.list.length + 1);
				}
				this.loading = false;

				if (this.list.length >= 40) {
					this.finished = true;
				}
			}, 300);
		},
		onRefresh() {
			// 清空列表数据
			var _this = this;
			this.finished = false;
			_this.getQue();
			_this.getRefushData();
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.loading = true;
			this.onLoad();
			this.change(_this.curValce);
			for (var i = 0; i < _this.optionsN.length; i++) {
				if (i == _this.curValce) {
					_this.optionsN[i].isChoi = true;
				} else {
					_this.optionsN[i].isChoi = false;
				}
			}
		},
		change: function(value) {
			var _this = this;
			_this.curValce = value;
			if (value == 0) {
				_this.choiceName = 'SWT';
				_this.bName = 'AICC';
				_this.getBalAndfreezed('SWT');
				_this.currency = 'SWT-JUSDT';
			} else if (value == 1) {
				_this.choiceName = 'JETH';
				_this.bName = 'JETH';
				_this.getBalAndfreezed('JETH');
				_this.currency = 'JETH-JUSDT';
			} else if (value == 2) {
				_this.choiceName = 'JXRP';
				_this.getBalAndfreezed('JXRP');
				_this.bName = 'JXRP';
				_this.currency = 'JXRP-JUSDT';
			} else if (value == 3) {
				_this.choiceName = 'JACC';
				_this.getBalAndfreezed('JACC');
				_this.bName = 'JACC';
				_this.currency = 'JACC-JUSDT';
			}
			_this.getQue();
			_this.$refs.child.updataInfo(_this.currency);
			_this.$refs.childBS.isChangePrice(value);
		},
		// 取消挂单 先获取交易序列号
		canceOrder: function() {
			var _this = this;
			_this.$dialog
				.confirm({
					title: _this.$t('message.a51'),
					message: _this.$t('message.a88'),
					confirmButtonText: _this.$t('message.b4'),
					cancelButtonText: _this.$t('message.b5')
				})
				.then(() => {
					for (var i = 0; i < _this.wtList.length; i++) {
						console.log('>>>>'+JSON.stringify(_this.wtList[i]))
						_this.sureCance(_this.wtList[i].seq);
					}
				})
				.catch(() => {
					// on cancel
				});
		},
		curRemark: function() {
			var _this = this;
			if (_this.curValce == 0) {
				_this.$router.push('/CurrencyRemark');
			} else if (_this.curValce == 1) {
				_this.$router.push('/CurrencyRemarkEth');
			} else if (_this.curValce == 2) {
				_this.$router.push('/CurrencyRemarkXrp');
			}
		},
		async itemCance(sqe) {
			var _this = this;
			_this.$dialog
				.confirm({
					title: _this.$t('message.a51'),
					message: _this.$t('message.a88'),
					confirmButtonText: _this.$t('message.b4'),
					cancelButtonText: _this.$t('message.b5')
				})
				.then(() => {
					_this.sureCance(sqe);
				})
				.catch(() => {
					// on cancel
				});
		},
		async sureCance(sqe) {
			var _this = this;
			try {
				// on confirm
				JCCExchange.init(['https://srje115qd43qw2.swtc.top', 'https://srje071qdew231.swtc.top']);
				const address = _this.walData.address;
				const secret = _this.walData.secret;
				console.log(address, secret, sqe);
				var hash = await JCCExchange.cancelOrder(address, secret, sqe);
				console.log(hash);
				if (hash) {
					_this.$toast.success(_this.$t('message.a89'));
					// _this.onRefresh();
					_this.TimeWatch();
				} else {
					_this.$toast.success(_this.$t('message.a90'));
				}
			} catch (e) {
				//TODO handle the exception
				console.log(e);
			}
		}
		// 全部撤销
	}
};
</script>

<style>
.itemPop {
	font-size: 1.25rem;
	font-weight: 600;
	color: #999999;
}
.itemPopCh {
	font-size: 1.25rem;
	font-weight: 600;
	color: #666666;
}
.qhWrap {
	width: 4.8rem;
	height: 2rem;
	background-color: #528cff;
	position: absolute;
	right: 0.625rem;
	border-radius: 0.3125rem;
	color: white;
}

.jianjieWrap {
	background-color: #f0f0f0;
	padding: 0.125rem 0.875rem;
	border-radius: 0.3125rem;
}
.arrowWrap {
	width: 1rem;
}
.buySell {
	width: 49%;
}
.cxWrap {
	position: absolute;
	right: 0rem;
	top: 0.625rem;
	background-color: #f0f0f0;
	color: #999999;
	padding: 0.25rem 0.625rem;
	font-size: 0.8125rem;
}
.itemWtListWrap {
	width: 100%;
	height: 1.125rem;
	font-size: 0.75rem;
	margin-top: 0.625rem;
}
.recordTitleWrap {
	width: 100%;
