<template>
	<!-- 查看钱包地址和二维码 -->
	<div class="divColumn">
		<Title :title="$t('message.b34')"></Title>
		<div class="conTop conMagin">
			<div style="margin-top: 1.25rem;" class="divColumn divCenter">
				<span>{{ $t('message.b35') }}</span>
				<div id="qrcode" ref="qrcode" style="margin-top: 1.25rem;"></div>
				<span style="margin-top: 1.125rem;">{{ address }}</span>
				<span class="copyWrapC" v-clipboard:copy="address" v-clipboard:success="onCopy">{{ $t('message.b37') }}</span>
				<span style="margin-top: 1.25rem;">{{ $t('message.b36') }}</span>
				<div class="divColumn divCenter" v-show="isSecret">
					<div id="qrcode2" ref="qrcode2" style="margin-top: 1.25rem;"></div>
					<span style="margin-top: 1.125rem;">{{ secret }}</span>
					<span class="copyWrapC" v-clipboard:copy="secret" v-clipboard:success="onCopy">{{ $t('message.b37') }}</span>
				</div>
				<div style="width: 100%;margin-top: 1.25rem;" v-show="!isSecret">
					<van-field style="background-color:#f6f8fe;width: 100%;" v-model="pwd" :placeholder="$t('message.b38')" type="password"></van-field>
					<div style="width: 100%;text-align: left;margin-top: 1.125rem;font-size: 0.8125rem;text-align: justify;" class="divColumn">
						<span style="color: orange;margin-bottom: 0.625rem;">{{ $t('message.b39') }}</span>
						<span>{{ $t('message.b40') }}</span>
						<span>{{ $t('message.b41') }}</span>
						<span>{{ $t('message.b42') }}</span>
					</div>
				</div>
				<div class="divCenter sureBtnWrap" style="width: 100%;" @click="sure" v-show="!isSecret">{{ $t('message.b43') }}</div>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
import QRCode from 'qrcodejs2';
export default {
	components: {
		Title
	},
	data() {
		return {
			address: '',
			secret: '',
			pwd: '',
			walData: {},
			isSecret: false,
			type: 1
		};
	},
	created: function() {
		var _this = this;
		_this.walData = _this.getWallent();
		_this.type = _this.$route.query.type;
		if (_this.type == 1) {
			//AICC 钱包
			_this.address = _this.walData.address;
			_this.secret = _this.walData.secret;
		} else if (_this.type == 3) {
			// 瑞波链钱包
			_this.address = _this.walData.address_ro;
			_this.secret = _this.walData.secret_ro;
		} else if (_this.type == 2) {
			// 以太链钱包
			_this.address = _this.walData.address_eth;
			_this.secret = _this.walData.secret_eth;
		}

		_this.$nextTick(() => {
			_this.qrcode();
			_this.qrcode2();
		});
	},
	methods: {
		//动态生成二维码
		qrcode() {
			var _this = this;
			new QRCode('qrcode', {
				width: 100, // 设置宽度，单位像素
				height: 100, // 设置高度，单位像素
				text: _this.address // 设置二维码内容或跳转地址
			});
		},
		qrcode2() {
			var _this = this;
			new QRCode('qrcode2', {
				width: 100, // 设置宽度，单位像素
				height: 100, // 设置高度，单位像素
				text: _this.secret // 设置二维码内容或跳转地址
			});
		},
		onCopy: function() {
			this.$toast.success(this.$t('message.b44'));
		},
		sure: function() {
			var _this = this;
			if (_this.pwd == '') {
				_this.$toast.fail(_this.$t('message.b38'));
				return;
			}
			if (_this.pwd != _this.walData.pwd) {
				_this.pwd = '';
				_this.$toast.fail(_this.$t('message.b45'));
			} else {
				_this.isSecret = true;
			}
		}
	}
};
</script>

<style></style>
