<template lang="html">
	<div ref="kline_container" class="kline-css" style="background: #161b21;font-size: 0.75rem;" >
		<mKline v-on:listenToChildEvent="changeCycle" :kline-data-obj="klineDataObj" :kline-config="klineConfig"  style="background: #161b21;font-size: 0.75rem;"></mKline>
	</div>
</template>

<script>
let fontSize = document.getElementsByTagName('html')[0].style.fontSize || 100;
let baseHeight = parseFloat(fontSize);
let width = document.documentElement.clientWidth || document.body.clientWidth;
import { JcInfo } from 'jcc_rpc';
export default {
	data() {
		return {
			start: 0,
			size: {
				width,
				height: baseHeight * 5
			},
			klineDataObj: null,
			cycle: 'hour',
			platform: 'mobile',
			id: null,
			kline: null,
			klineConfig: {
				size: {
					width,
					height: baseHeight * 5
				},
				depthSize: {
					width,
					height: baseHeight * 3
				}
			},
			klineTime: '时',
			show: false,
			actions: []
		};
	},
	created: function() {
		setTimeout(() => {
			this.getKline();
		}, 500);
	},
	methods: {
		async getKline() {
			let inst = new JcInfo(['http://iai117qd293cabc.swtc.top']);
			console.log(JSON.stringify(inst));
			let p1 = inst.getKline('swt', 'cny', this.cycle);
			let p2 = inst.getDepth('swt', 'cny', "more");
			let [res, res2] = await Promise.all([p1, p2]);
			console.log(res,res2)
			this.klineDataObj = {
			  pricePrecision: 4,
			  amountPrecision: 4,
			  cycle: this.cycle,
			  klineSize: this.size,
			  coinType: { base: 'swt', counter: 'cny' }
			};
			if (res.result) {
			  this.klineDataObj.klineData = res.data;
			}
			if (res2.result) {
			  this.klineDataObj.depthData = res2.data;
			}
		},
		changeCycle(cycle) {
			this.cycle = cycle;
		}
	}
};
</script>

<style></style>
