<template>
	<!-- 设置语言 -->
	<div class="divColumn">
		<Title :title="$t('message.a7')"></Title>
		<div class="conTop">
			<div style="width: 100%;">
				<van-radio-group v-model="radio">
					<van-cell-group>
						<van-cell style="text-align: left;" :title="$t('message.a6')" clickable @click="itemClick1">
							<template #right-icon>
								<van-radio name="1" />
							</template>
						</van-cell>
						<van-cell style="text-align: left;" :title="$t('message.a12')" clickable @click="itemClick2">
							<template #right-icon>
								<van-radio name="2" />
							</template>
						</van-cell>
					</van-cell-group>
				</van-radio-group>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
export default {
	components: {
		Title
	},
	data() {
		return {
			radio: '1'
		};
	},
	created: function() {
		//判断当前语言
		var _this = this;
		var defaultLang = localStorage.getItem('lang');
		if (defaultLang == null || defaultLang == 'ch') {
			_this.radio = '1';
		} else if (defaultLang == 'en') {
			_this.radio = '2';
		}
	},
	methods: {
		itemClick1: function() {
			this.showDialog('1');
		},
		itemClick2: function() {
			this.showDialog('2');
		},
		showDialog: function(num) {
			var _this = this;
			this.$dialog
				.confirm({
					title: _this.$t('message.a8'),
					message: _this.$t('message.a9'),
					confirmButtonText: _this.$t('message.a10'),
					cancelButtonText: _this.$t('message.a11')
				})
				.then(() => {
					// on confirm
					_this.radio = num;
					if (num == '1') {
						this.$i18n.locale = 'ch';
						localStorage.setItem('lang', 'ch');
					} else if (num == '2') {
						this.$i18n.locale = 'en';
						localStorage.setItem('lang', 'en');
					}
					_this.$toast.success(_this.$t('message.d17'));
				})
				.catch(() => {
					// on cancel
				});
		}
	}
};
</script>

