<template>
	<div class="divColumn">
		<div style="height: 2.5rem;position: relative;" class="divCenter">
			<img src="../../assets/img/ex_back.png" style="width: 1.5rem;position: absolute;left: 0.625rem;" @click="btnBack" />
			<span style="color: #3B79F4;font-size: 1.125rem;font-weight: 600;">{{ $t('message.e32') }}</span>
		</div>
		<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
			<van-list v-model="loading" :finished="finished" :finished-text="$t('message.a13')" @load="onLoad">
				<div style="margin: 0rem 0.625rem;">
					<van-tabs v-model="active" title-active-color="#3B79F4" color="#3B79F4" background="#F2F2F4" swipeable @change="changeItem">
						<van-tab :title="$t('message.e35')">
							<div class="divCenter topWrapTitle">
								<span class="poLeft1">{{ $t('message.e31') }}</span>
								<span class="poLeft2">{{ $t('message.a71') }}</span>
								<span class="poRight2">{{ $t('message.a55') }}</span>
								<span class="poRight1">{{ $t('message.a48') }}</span>
							</div>
							<div class="lineDet"></div>
							<div style="width: 100%;background-color: white;" class="divColumn" v-for="(item, index) in dataList" :key="index">
								<div style="position: relative;height: 2.8rem;font-weight: 800;" class="divCenter">
									<span class="poLeft1">{{ item.create_time.substring(0, 11) }}</span>
									<span class="poLeft2">ACC</span>
									<span class="poRight2" v-show="item.fromtype == 2">{{ $t('message.e36') }}</span>
									<span class="poRight2" v-show="item.fromtype == 3">{{ $t('message.e37') }}</span>
									<span class="poRight2" v-show="item.fromtype == 20">{{ $t('message.e38') }}</span>
									<span class="poRight2" v-show="item.fromtype == 21">{{ $t('message.e39') }}</span>
									<span class="poRight1" style="color: #10B82F;" v-show="item.pay_val > 0">{{ item.pay_val }}</span>
									<span class="poRight1" style="color: red;" v-show="item.pay_val < 0">{{ item.pay_val }}</span>
								</div>
								<div class="lineDet"></div>
							</div>
						</van-tab>
					<van-tab :title="$t('message.e36')">
							<div class="divCenter topWrapTitle">
								<span class="poLeft1">{{ $t('message.e31') }}</span>
								<span class="poLeft2">{{ $t('message.a71') }}</span>
								<span class="poRight2">{{ $t('message.a55') }}</span>
								<span class="poRight1">{{ $t('message.a48') }}</span>
							</div>
							<div class="lineDet"></div>
							<div style="width: 100%;background-color: white;" class="divColumn" v-for="(item, index) in dataList" :key="index">
								<div style="position: relative;height: 2.8rem;font-weight: 800;" class="divCenter">
									<span class="poLeft1">{{ item.create_time.substring(0, 11) }}</span>
									<span class="poLeft2">ACC</span>
									<span class="poRight2">{{ $t('message.e36') }}</span>
									<span class="poRight1" style="color: #10B82F;">+{{ item.pay_val }}</span>
								</div>
								<div class="lineDet"></div>
							</div>
						</van-tab>
						<van-tab :title="$t('message.e37')">
							<div class="divCenter topWrapTitle">
								<span class="poLeft1">{{ $t('message.e31') }}</span>
								<span class="poLeft2">{{ $t('message.a71') }}</span>
								<span class="poRight2">{{ $t('message.a55') }}</span>
								<span class="poRight1">{{ $t('message.a48') }}</span>
							</div>
							<div class="lineDet"></div>
							<div style="width: 100%;background-color: white;" class="divColumn" v-for="(item, index) in dataList" :key="index">
								<div style="position: relative;height: 2.8rem;font-weight: 800;" class="divCenter">
									<span class="poLeft1">{{ item.create_time.substring(0, 11) }}</span>
									<span class="poLeft2">ACC</span>
									<span class="poRight2">{{ $t('message.e37') }}</span>
									<span class="poRight1" style="color: red;">{{ item.pay_val }}</span>
								</div>
								<div class="lineDet"></div>
							</div>
						</van-tab>
						<van-tab :title="$t('message.e40')">
							<div class="divCenter topWrapTitle">
								<span class="poLeft1">{{ $t('message.e31') }}</span>
								<span class="poLeft2">{{ $t('message.a71') }}</span>
								<span class="poRight2">{{ $t('message.a55') }}</span>
								<span class="poRight1">{{ $t('message.a48') }}</span>
							</div>
							<div class="lineDet"></div>
							<div style="width: 100%;background-color: white;" class="divColumn" v-for="(item, index) in dataList" :key="index">
								<div style="position: relative;height: 2.8rem;font-weight: 800;" class="divCenter">
									<span class="poLeft1">{{ item.create_time.substring(0, 11) }}</span>
									<span class="poLeft2">ACC</span>
									<span class="poRight2" v-show="item.fromtype == 20">{{ $t('message.e38') }}</span>
									<span class="poRight2" v-show="item.fromtype == 21">{{ $t('message.e39') }}</span>
									<span class="poRight1" style="color: #10B82F;" v-show="item.pay_val > 0">{{ item.pay_val }}</span>
									<span class="poRight1" style="color: red;" v-show="item.pay_val < 0">{{ item.pay_val }}</span>
								</div>
								<div class="lineDet"></div>
							</div>
						</van-tab>
					</van-tabs>
				</div>
			</van-list>
		</van-pull-refresh>
	</div>
</template>

<script>
var page = 1;
var pagesize = 10;
export default {
	data() {
		return {
			active: 0,
			dataList: [],
			fromType: '2,3,20,21',
			loading: false,
			finished: false,
			refreshing: false
		};
	},
	created: function() {
		page = 1;
	},
	methods: {
		onLoad() {
			if (this.refreshing) {
				this.dataList = [];
				this.refreshing = false;
			}
			this.getDataList();
		},
		getDataList: function() {
			var _this = this;
			_this.postApi(
				'api/Account/index.html',
				{
					page: page,
					pagesize: pagesize,
					fromtype: _this.fromType
				},
				function(res) {
					_this.dataList = _this.dataList.concat(res.data);
					console.log(JSON.stringify(res));
					page++;
					_this.loading = false;
					if (res.data.length == 0) {
						_this.finished = true;
					}
				}
			);
		},
		onRefresh() {
			// 清空列表数据
			this.finished = false;
			page = 1;
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.refreshing = true;
			this.loading = true;
			this.onLoad();
		},
		btnBack: function() {
			this.$router.go(-1);
		},
		changeItem: function(value) {
			var _this = this;
			_this.dataList = [];
			if (value == 0) {
				_this.fromType = '2,3,20,21';
			}
			if (value == 1) {
				_this.fromType = 2;
			}
			if (value == 2) {
				_this.fromType = 3;
			}
			if (value == 3) {
				_this.fromType = '20,21';
			}
			_this.onRefresh();
			console.log(value);
		}
	},
	mounted() {
		document.querySelector('html').setAttribute('style', 'background-color:#F2F2F4');
	},
	beforeDestroy() {
		document.querySelector('html').removeAttribute('style');
	}
};
</script>

<style>
.itemReWrap {
	width: 100%;
	height: 2.625rem;
	background-color: white;
	border-radius: 0rem 1.875rem 0rem 0rem;
	margin-top: 0.75rem;
	color: #666666;
}
.topWrapTitle {
	width: 100%;
	height: 2.625rem;
	background-color: white;
	margin-top: 0.625rem;
	border-radius: 0rem 1.875rem 0rem 0rem;
	position: relative;
}
.lineDet {
	height: 0.0625rem;
	background-color: #f0f0f0;
	width: 100%;
}
.poLeft1 {
	position: absolute;
	left: 1rem;
}
.poLeft2 {
	position: absolute;
	left: 34%;
}
.poRight2 {
	position: absolute;
	right: 34%;
}
.poRight1 {
	position: absolute;
	right: 1rem;
}
</style>
