<template>
	<!-- 跨链转入 -->
	<div class="divColumn">
		<Title :title="$t('message.a68')"></Title>
		<div class="conTop divColumn">
			<div style="margin: 0rem 0.625rem;" class="divColumn">
				<div class="leftAndRight itemW">
					<span>{{ $t('message.a76') }}</span>
					<van-dropdown-menu style="background-color: #F6F8FE;height: 2.625rem;">
						<van-dropdown-item v-model="value1" :options="option1" @change="changeItem" />
					</van-dropdown-menu>
				</div>
				<!-- 没有添加对应钱包 -->
				<div class="leftAndRight itemW" style="color: orange;" @click="addWalt" v-show="isWalt">{{ $t('message.d19') }}{{ walName }}{{ $t('message.d20') }}</div>
				<div class="leftAndRight itemW">
					<span>{{ $t('message.a77') }}：</span>
					<span style="font-size: 0.75rem;">{{ walData.address }}</span>
				</div>
				<div class="leftAndRight itemW">
					<span>{{ $t('message.d22') }}：</span>
					<span style="font-size: 0.75rem;">{{ address_out }}</span>
				</div>
				<div class="leftAndRight itemW">
					<span style="width: 30%;">{{ $t('message.d23') }}：</span>
					<van-field style="background-color:#f6f8fe;" v-model="num" :placeholder="$t('message.d34')"></van-field>
				</div>
				<div
					class="itemW divCenter"
					style="background-color: #518CFF;border-radius: 0.3125rem;margin-top: 2.5rem;
				font-size: 1rem;font-weight: 600;color: white;"
					@click="sureBtn"
				>
					<span>{{ $t('message.a79') }}</span>
				</div>
				<!-- 温馨提示 -->
				<div class="divColumn" style="text-align: left;margin-top: 1.25rem;margin-bottom: 2.5rem;">
					<span style="margin-bottom: 0.625rem;">{{ $t('message.a80') }}</span>
					<span style="text-indent:2em;line-height: 1.5rem;">{{ $t('message.d35') }}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
import { jtWallet } from 'jcc_wallet';
import JCCExchange from 'jcc_exchange';
import BigNumber from 'bignumber.js';
export default {
	components: {
		Title
	},
	data() {
		return {
			value1: 0,
			option1: [{ text: 'USDT', value: 0 }, { text: 'ETH', value: 1 }, { text: 'ACC', value: 3 }, { text: 'IQC', value: 4 }, { text: 'XRP', value: 2 }],
			walData: {},
			num: '',
			isWalt: true,
			address_out: '',
			type: 1, //1 添加eth地址 2添加瑞波链
			walBal: {}, //钱包地址余额
			ethBal: 0,
			walName: 'ETH',
			usdtBal: 0,
			accBal: 0,
			iqcBal: 0
		};
	},
	created: function() {
		var _this = this;
		//获取本地钱包数据
		_this.walData = _this.getWallent();
		_this.checkIsAdW();
		_this.walBal = _this.getWallentBal(function(res) {
			_this.walBal = res;
			for (var i = 0; i < _this.walBal.data.length; i++) {
				if (_this.walBal.data[i].currency == 'JETH') {
					_this.ethBal = new BigNumber(_this.walBal.data[i].value).minus(_this.walBal.data[i].freezed).toString(10);
				}
				if (_this.walBal.data[i].currency == 'JUSDT') {
					_this.usdtBal = new BigNumber(_this.walBal.data[i].value).minus(_this.walBal.data[i].freezed).toString(10);
				}
				if (_this.walBal.data[i].currency == 'JACC') {
					_this.accBal = new BigNumber(_this.walBal.data[i].value).minus(_this.walBal.data[i].freezed).toString(10);
				}
				if (_this.walBal.data[i].currency == 'JIQC') {
					_this.iqcBal = new BigNumber(_this.walBal.data[i].value).minus(_this.walBal.data[i].freezed).toString(10);
				}
			}
		});
	},
	methods: {
		//检测是否添加对应钱包
		checkIsAdW: function() {
			var _this = this;
			if (_this.value1 == 0 || _this.value1 == 1 || _this.value1 == 3 || _this.value1 == 4) {
				//判断是否添加eth钱包
				if (_this.walData.secret_eth == undefined) {
					_this.isWalt = true;
				} else {
					_this.isWalt = false;
				}
				_this.address_out = _this.walData.address_eth;
				_this.type = 1;
				_this.walName = 'ETH';
			} else {
				if (_this.walData.secret_ro == undefined) {
					_this.isWalt = true;
				} else {
					_this.isWalt = false;
				}
				_this.address_out = _this.walData.address_ro;
				_this.type = 2;
				_this.walName = _this.$t('message.d26');
			}
		},
		changeItem: function(value) {
			var _this = this;
			_this.value1 = value;
			_this.checkIsAdW();
			if (value == 0 || value == 1 || value == 3 || value == 4) {
				_this.type = 1;
			} else {
				_this.type = 2;
			}
		},
		addWalt: function() {
			var _this = this;
			_this.$router.push({
				path: '/AddWallent',
				query: {
					type: _this.type
				}
			});
		},

		async sureBtn() {
			this.$toast.loading({
				forbidClick: true,
				duration: 0
			});
			if (this.num == '') {
				this.$toast.clear();
				this.$toast.fail(this.$t('message.d34'));
				return;
			}
			if (this.isWalt) {
				this.$toast.clear();
				if (this.type == 2) {
					this.$toast.fail(this.$t('message.d39'));
				} else {
					this.$toast.fail(this.$t('message.d40'));
				}
				return;
			}
			if (this.value1 === 0 || this.value1 === 1 || this.value1 === 3 || this.value1 === 4) {
				const ethGas = 0.0015; //
				const amount = this.num;
				const to = 'jsk45ksJZUB7durZrLt5e86Eu2gtiXNRN4'; //银关
				const swtSecret = this.walData.secret;
				const memo = {
					eth_wallet: this.address_out,
					value: amount
				};
				if (this.value1 === 0 || this.value1 === 3 || this.value1 === 4) {
					console.log(swtSecret, memo);
					var binType = '';
					/* { text: 'ACC', value: 3 }, { text: 'IQC', value: 4 } */
					if (this.value1 == 0) {
						binType = 'JUSDT';
						if (new BigNumber(this.usdtBal).lt(amount)) {
							this.$toast.fail('USDT余额不足');
							return;
						}
					} else if (this.value1 == 3) {
						binType = 'JACC';
						if (new BigNumber(this.accBal).lt(amount)) {
							this.$toast.fail('ACC余额不足');
							return;
						}
					} else if (this.value1 == 4) {
						binType = 'JIQC';
						if (new BigNumber(this.iqcBal).lt(amount)) {
							this.$toast.fail('IQC余额不足');
							return;
						}
					}
					// usdt 判断是有够gas费
					if (new BigNumber(this.ethBal).lt(ethGas)) {
						this.$toast.fail(this.$t('message.d36'));
						return;
					}
					const data = this.serializePayment(swtSecret, to, amount, binType, memo);
					const hash = await this.transfer(data);
					console.log('转出hash', hash);
					if (hash) {
						const gasMemo = {
							eth_wallet: this.address_out,
							value: ethGas,
							relate: hash
						};
						setTimeout(async () => {
							try {
								const gasHash = await this.payGas(swtSecret, to, ethGas, 'jeth', gasMemo);
								if (gasHash) {
									this.$toast.clear();
									this.$toast.success(this.$t('message.d37'));
								}
							} catch (error) {
								console.log(error.message);
								this.$toast.clear();
								this.$toast.fail(this.$t('message.d38'));
							}
						}, 12000);
					}
				} else {
					// eth
					console.log(swtSecret, memo);
					if (new BigNumber(this.ethBal).lt(amount)) {
						this.$toast.fail('ETH资产不足');
						return;
					}
					const paymentData = this.serializePayment(swtSecret, to, amount, 'jeth', memo);
					const transferHash = await this.transfer(paymentData);
					console.log('转出eth hash', transferHash);
					if (transferHash) {
						this.$toast.clear();
						this.$toast.success(this.$t('message.d37'));
					} else {
						this.$toast.clear();
						this.$toast.fail(this.$t('message.d38'));
					}
				}
			} else {
				const memo = {
					xrp_wallet: this.walData.address_ro,
					value: this.num,
					chain: 'XRP'
				};
				console.log(this.walData.secret, this.num, memo);
				const to = 'jQs5cAcZrKmyWSQgkmUtXsdeFMzwSYcBA4';
				const data = this.serializePayment(this.walData.secret, to, this.num, 'JXRP', memo);
				const hash = await this.transfer(data);
				if (hash) {
					this.$toast.clear();
					this.$toast.success(this.$t('message.d37'));
				} else {
					this.$toast.clear();
					this.$toast.fail(this.$t('message.d38'));
				}
			}
		},
		async payGas(swtSecret, to, gas, token, memo) {
			const gasData = this.serializePayment(swtSecret, to, gas, token, memo);
			this.$toast.loading({
				forbidClick: true,
				duration: 0
			});
			let gasHash = null;
			let count = 0;
			while (gasHash === null) {
				// try 10
				if (count === 10) {
					break;
				}
				try {
					gasHash = await this.transfer(gasData);
				} catch (error) {
					console.log('transfer gas error:', error);
				} finally {
					count = count + 1;
				}
			}
			if (!gasHash) {
				this.$toast.clear();
				this.$toast.fail(this.$t('message.d38'));
			}
			return gasHash;
		},
		serializePayment(secret, to, amount, token, memo, issuer = 'jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or') {
			const data = {
				address: jtWallet.getAddress(secret),
				secret,
				to,
				amount,
				issuer,
				currency: token.toUpperCase(),
				memo: JSON.stringify(memo)
			};
			return data;
		},
		async transfer(payment) {
			const { address, secret, to, amount, issuer, currency, memo } = payment;
			try {
				JCCExchange.init(this.getInfoData().jcNodes);
				const hash = await JCCExchange.transfer(address, secret, amount, memo, to, currency, issuer);
				console.log('payment hash: ', hash);
				return hash;
			} catch (error) {
				console.log('payment error: ', error);
				// throw new Error(this.$t("message.withdraw.failed"));
			}
		}
	}
};
</script>

<style>
.itemW {
	height: 2.625rem;
	background-color: #f6f8fe;
	margin-top: 0.625rem;
	padding: 0rem 0.625rem;
}
</style>
