						</div>
						<img src="../../assets/img/as1.png" class="scanImg" @click="lkWaltDetials" />
						<div style="position: absolute;bottom: 0.625rem;width: 100%;color: white;" class="leftAndRight">
							<div class="asBtnWrap divCenter" style="margin-left: 1rem;" @click="income">{{ $t('message.a67') }}</div>
							<div class="asBtnWrap divCenter" style="margin-right: 1rem;" @click="RollOut">{{ $t('message.a68') }}</div>
						</div>
					</div>
				</div>
			</div>

			<!-- 新的资产下部分 -->

			<!-- 资产下部分 -->
			<div class="divColumn zcWrap" :class="{ wrapW: isWal }">
				<!-- <div style="height: 3rem;background-color: #F6F8FE;margin: 0rem 0.625rem;padding: 0rem 0.625rem;margin-top: 1.25rem;" class="leftAndRight">
					<div class="divRow divColumnCenter" @click="isShow">
						<img src="../../assets/img/na1.png" v-show="checked" style="width: 1.25rem;height: 1.25rem;" />
						<img src="../../assets/img/l_cho.png" v-show="!checked" style="width: 1.5rem;height: 1.25rem;" />
						<span style="color: #999999;margin-left: 0.625rem;">{{ $t('message.a70') }}</span>
						<img src="../../assets/img/na3.png" style="width: 1rem;height: 1rem;margin-left: 0.625rem;" />
					</div> -->
				<!-- 
					<div class="divRow divColumnCenter">
						<img src="../../assets/img/na2.png" style="width: 1rem;height: 1rem;" />
						<div class="divColumn divColumnCenter">
							<span style="color: #999999;">搜索币种</span>
							<div style="width:4.6rem;height: 0.125rem;background-color: #DBDBDB;"></div>
						</div>
					</div> -->
				<!-- 具体列表 -->
				<!-- </div> -->
				<div class="asItemWrap" v-for="(item, index) in walBal.data" :key="index" v-show="checked">
					<div class="divRow divColumnCenter">
						<span v-show="item.currency == 'SWT'">AICC</span>
						<span v-show="item.currency != 'SWT'">{{ item.currency.replace('J', '') }}</span>
						<img v-show="item.currency == 'SWT'" src="../../assets/img/l_aicc.png" style="width: 1.25rem;height:1.25rem;margin-left: 0.625rem;" />
						<img v-show="item.currency == 'JUSDT'" src="../../assets/img/l_usdt.png" style="width: 1.25rem;height:1.25rem;margin-left: 0.625rem;" />
						<img v-show="item.currency == 'JETH'" src="../../assets/img/l_eth.png" style="width: 1.25rem;height:1.25rem;margin-left: 0.625rem;" />
						<img v-show="item.currency == 'JXRP'" src="../../assets/img/l_.png" style="width: 1.25rem;height:1.25rem;margin-left: 0.625rem;" />
						<img v-show="item.currency == 'JACC'" src="../../assets/img/acc_img.png" style="width: 1.25rem;height:1.25rem;margin-left: 0.625rem;" />
						<img v-show="item.currency == 'JIQC'" src="../../assets/img/iqc_img.png" style="width: 1.25rem;height:1.25rem;margin-left: 0.625rem;" />
						<img v-show="item.currency == 'JEQC'" src="../../assets/img/eqc_img.png" style="width: 1.25rem;height:1.25rem;margin-left: 0.625rem;" />
					</div>
					<div style="position: absolute;bottom: 0.625rem;color: #999999;font-size: 0.75rem;width: 100%;" class="divCenter">
						<div class="divColumn" style="position: absolute;left: 0rem;text-align: left;">
							<span>{{ $t('message.a73') }}</span>
							<span style="font-weight: 600;">{{ Number(item.freezed).toFixed(4) }}</span>
						</div>
						<div class="divColumn">
							<span>{{ $t('message.a72') }}</span>
							<span style="font-weight: 600;">{{ Number(Number(item.value).toFixed(4) - Number(item.freezed).toFixed(4)).toFixed(4) }}</span>
						</div>
						<div class="divColumn" style="position: absolute;right: 1.25rem;text-align: right;">
							<span>{{ $t('message.a74') }}</span>
							<span style="font-weight: 600;">{{ Number(item.value).toFixed(4) }}</span>
						</div>
					</div>
				</div>

				<!-- 小额资产币种 -->
				<!-- 	<div class="smaBinWrap divCenter">
					<div :style="leftBk" class="divCenter bk_wal">{{ $t('message.b10') }}</div>

					<div class="divRow divColumnCenter" style="position: absolute;right: 0.625rem;">
						<span style="color: white;margin-right: 0.5rem;font-size: 0.8125rem;">{{ $t('message.d14') }}</span>
						<van-switch v-model="checked" size="19px" active-color="#3B71D9" />
					</div>
				</div>
				<div style="height: 5rem;margin: 0rem 0.625rem;background-color: #F6F8FE;border-radius: 0.3125rem;" class="divColumn">
					<div style="width: 100%;height: 40%;position: relative;font-weight: bolder;" class="divCenter">
						<span style="position: absolute;left: 0.625rem;">{{ $t('message.c13') }}</span>
						<span style="position: absolute;left: 32%;">{{ $t('message.a33') }}</span>
						<span style="position: absolute;right: 32%;">{{ $t('message.a73') }}</span>
						<span style="position: absolute;right: 0.625rem;">{{ $t('message.a74') }}</span>
					</div>
					<div style="width: 100%;height: 60%;" class="divCenter">
						<van-field
							v-model="value"
							:placeholder="$t('message.a75')"
							style="width: 90%;height: 86%;font-size: 0.75rem;margin-top: -0.5rem;border-radius: 0.3125rem;"
						/>
					</div>
				</div> -->
				<!-- 具体列表 -->
				<!-- 
				<div v-show="checked">
					<div class="asItem divCenter" style="font-weight: bold;font-size: 0.8125rem;" v-for="(item, index) in walBal.data" :key="index">
						<span style="position: absolute;left: 0.625rem;" v-show="item.currency == 'SWT'">AICC</span>
						<span style="position: absolute;left: 0.625rem;" v-show="item.currency != 'SWT'">{{ item.currency }}</span>
						<span style="position: absolute;left: 32%;">{{ Number(Number(item.value).toFixed(4) - Number(item.freezed).toFixed(4)).toFixed(4) }}</span>
						<span style="position: absolute;right: 32%;">{{ Number(item.freezed).toFixed(4) }}</span>
						<span style="position: absolute;right: 0.625rem;">{{ Number(item.value).toFixed(4) }}</span>
