				</div> -->
			</div>
			<div style="height: 3.75rem;width: 100%;"></div>
		</div>
	</van-pull-refresh>
</template>

<script>
export default {
	data() {
		return {
			topBk: {
				backgroundImage: 'url(' + require('../../assets/img/as_bk.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			leftBk: {
				backgroundImage: 'url(' + require('../../assets/img/as_left_bk.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			isWal: false,
			walData: {},
			address: '',
			checked: true,
			value: '',
			walBal: {},
			aIccBal: {},
			loading: false,
			finished: false,
			refreshing: false,
			list: [],
			aiccPrice: 0,
			alBalance: 0,
			ethPrice: 0,
			usdtPrice: 0,
			xrpPrice: 0,
			assestCount: 0,
			allCount: 0,
			accPrice: 0,
			iqcPrice: 0,
			eqcPrice: 0
		};
	},
	created: function() {
		this.initData();
	},
	methods: {
		isShow: function() {
			if (this.checked) {
				this.checked = false;
			} else {
				this.checked = true;
			}
		},
		onLoad() {
			setTimeout(() => {
				if (this.refreshing) {
					this.list = [];
					this.refreshing = false;
				}
			}, 600);
		},
		onRefresh() {
			// 清空列表数据
			this.finished = false;
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.loading = true;
			this.onLoad();
			this.initData();
		},
		toCreateWal: function() {
			var _this = this;
			_this.$router.push({
				path: '/CreateWallent',
				query: {
					type: 1
				}
			});
		},
		initData: function() {
			var _this = this;
			_this.isAddWalt();
			var dataW = _this.getWallent();
			if (_this.isAddWalt()) {
				_this.isWal = true;
				_this.walData = dataW;
				_this.address = _this.replaceX(_this.walData.address);
				_this.getWallentBal(function(res) {
					_this.walBal = res;
					if (res.code == 0) {
						_this.alBalance = 0;
						_this.aIccBal = _this.walBal.data[0];
						// acCount, xrpCount, ethCount, usdtCount
						var acCount = 0;
						var xrpCount = 0;
						var ethCount = 0;
						var usdtCount = 0;
						var AccCount = 0;
						var IqcCount = 0;
						var EqcCount = 0;
						for (var i = 0; i < _this.walBal.data.length; i++) {
							var item = _this.walBal.data[i];
							if (item.currency == 'SWT') {
								acCount = Number(item.value).toFixed(4);
							}
							if (item.currency == 'JXRP') {
								xrpCount = Number(item.value).toFixed(4);
							}
							if (item.currency == 'JETH') {
								ethCount = Number(item.value).toFixed(4);
							}
							if (item.currency == 'JUSDT') {
								usdtCount = Number(item.value).toFixed(4);
							}
							if (item.currency == 'JACC') {
								AccCount = Number(item.value).toFixed(4);
							}
							if (item.currency == 'JIQC') {
								IqcCount = Number(item.value).toFixed(4);
							}
							if (item.currency == 'JEQC') {
								EqcCount = Number(item.value).toFixed(4);
							}
						}
						// _this.allCount = Number(acCount) + Number(xrpCount) + Number(ethCount) + Number(usdtCount) + Number(AccCount) + Number(IqcCount) + Number(EqcCount);
						_this.getQueteData(acCount, xrpCount, ethCount, usdtCount, AccCount, IqcCount, EqcCount);
					}
				});
			} else {
				_this.isWal = false;
			}

			_this.post(_this.gloabal.baseApi + _this.getInfoData().infoHosts[0] + '/info/alltickers', {}, function(res) {
				//遍历对象
				for (var key in res.data) {
					var pair = key.split('-');
					// 	日涨跌(大于0表示涨 小于0表示跌 换算成百分比要除100)
					var zt = 0.0;
					if (res.data[key].length > 2 && Number(res.data[key][2]).toFixed(0) != 0) {
						zt = Number(res.data[key][2]) / 100;
					}
					var data = {
						name: pair[0],
						pai: pair[1],
						count: res.data[key].length > 6 ? res.data[key][6] : 0,
						price: res.data[key][1],
						zt: zt.toFixed(2)
					};
					// if ((pair[0] === 'SWT' && pair[1] === 'JUSDT') || (pair[0] === 'JETH' && pair[1] === 'JUSDT') || (pair[0] === 'JXRP' && pair[1] === 'JUSDT')) {
					// 	_this.qouteList.push(data);
					// }
					if (pair[0] === 'SWT' && pair[1] === 'JUSDT') {
						console.log(data);
						_this.aiccPrice = data.price;
					}
				}
			});
		},
		income: function() {
			var _this = this;
			if (!this.walBal.isActive) {
				_this.$dialog
					.confirm({
						title: _this.$t('message.d15'),
						message: _this.$t('message.d16'),
						confirmButtonText: _this.$t('message.a10'),
						cancelButtonText: _this.$t('message.a11')
					})
					.then(() => {
						// on confirm
						var _this = this;
						_this.$router.push('/ActiveWalt');
					})
					.catch(() => {
						// on cancel
					});
				return;
			}
			this.$router.push('/Income');
		},
		RollOut: function() {
			var _this = this;
			if (!this.walBal.isActive) {
				_this.$dialog
					.confirm({
						title: _this.$t('message.d15'),
						message: _this.$t('message.d16'),
						confirmButtonText: _this.$t('message.a10'),
						cancelButtonText: _this.$t('message.a11')
					})
					.then(() => {
						// on confirm
						var _this = this;
						_this.$router.push('/ActiveWalt');
					})
					.catch(() => {
						// on cancel
					});
				return;
			}
			this.$router.push('/RollOut');
		},
		lkWaltDetials: function() {
			var _this = this;
			_this.$router.push({
				path: '/LookWalt',
				query: {
					type: 1
				}
			});
		},
		onCopy: function() {
			this.$toast.success(this.$t('message.b44'));
		},
		intoWal: function() {
			var _this = this;
			_this.$router.push({
				path: '/AddWallent',
				query: {
					type: 3
				} // 1 以太链钱包 2 瑞波链钱包
			});
		},
		toActive: function() {
			var _this = this;
			_this.$router.push('/ActiveWalt');
		},
		getQueteData: function(acCount, xrpCount, ethCount, usdtCount, accCount, iqcCount, eqcCount) {
			var _this = this;
			//获取行情
			_this.post(_this.gloabal.baseApi + _this.getInfoData().infoHosts[0] + '/info/alltickers', {}, function(res) {
				//遍历对象
				for (var key in res.data) {
					var pair = key.split('-');
					// 	日涨跌(大于0表示涨 小于0表示跌 换算成百分比要除100)
					var zt = 0.0;
					if (res.data[key].length > 2 && Number(res.data[key][2]).toFixed(0) != 0) {
						zt = Number(res.data[key][2]) / 100;
					}
					var data = {
						name: pair[0],
						pai: pair[1],
						count: res.data[key].length > 6 ? res.data[key][6] : 0,
						price: res.data[key][1],
						zt: zt.toFixed(2)
					};
					// if ((pair[0] === 'SWT' && pair[1] === 'JUSDT') || (pair[0] === 'JETH' && pair[1] === 'JUSDT') || (pair[0] === 'JXRP' && pair[1] === 'JUSDT')) {
					// 	_this.qouteList.push(data);
					// }
					if (pair[1] === 'JUSDT') {
						console.log('>>>>' + JSON.stringify(data));
						if (data.name == 'SWT') {
							_this.aiccPrice = Number(data.price).toFixed(6);
						}
						if (data.name == 'JXRP') {
							_this.xrpPrice = Number(data.price).toFixed(6);
						}
						if (data.name == 'JETH') {
							_this.ethPrice = Number(data.price).toFixed(6);
						}
						if (data.name == 'JACC') {
							_this.accPrice = Number(data.price).toFixed(6);
						}
						if (data.name == 'JIQC') {
							_this.iqcPrice = Number(data.price).toFixed(6);
						}
						if (data.name == 'JEQC') {
							_this.eqcPrice = Number(data.price).toFixed(6);
						}
					}
					// 根据实时价格，计算资产
					var aUsdt = Number(usdtCount * 7 * 1).toFixed(4);
					var aEth = Number(ethCount * 7 * _this.ethPrice).toFixed(4);
					var axrp = Number(xrpCount * 7 * _this.xrpPrice).toFixed(4);
					var aAicc = Number(acCount * 7 * _this.aiccPrice).toFixed(4);
					var acc = Number(accCount * 7 * _this.accPrice).toFixed(4);
					var iqc = Number(iqcCount * 7 * _this.iqcPrice).toFixed(4);
					var eqc = Number(eqcCount * 7 * _this.eqcPrice).toFixed(4);
					_this.assestCount = Number(aUsdt) + Number(aEth) + Number(axrp) + Number(aAicc) + Number(acc) + Number(iqc) + Number(eqc);
					_this.allCount = _this.assestCount / 7;
				}
			});
		}
	}
};
</script>

<style>
