	left: 0rem;
	color: white;
	font-size: 0.625rem;
}
.zcWrap {
	margin-top: 15rem;
	width: 100%;
}
.wrapW {
	margin-top: 16rem;
}
.asItem {
	height: 2.625rem;
	background-color: #f6f8fe;
