exports.install = function(Vue, md5) {
	Vue.prototype.post = function(url, data, callback) {
		var qs = require('qs');
		//创建实例
		var _this = this;
		_this.$toast.loading({
			forbidClick: true,
			duration: 0,
		})
		var ajx = _this.$ajax.create({
			headers: {}
		});
		console.log(JSON.stringify(data))
		//想获取服务端列表
		ajx.get(url, qs.stringify(data)).then(res => {
			_this.$toast.clear();
			callback(res.data);
			// console.log(url + '===Result' + JSON.stringify(res.data))
		}).catch(error => {
			_this.$router.push('/')
			alert(error)
		})

	};

	Vue.prototype.postApi = function(url, data, callback) {
		var qs = require('qs');
		//创建实例
		var _this = this;
		_this.$toast.loading({
			forbidClick: true,
			duration: 0,
		})
		var ajx = _this.$ajax.create({
			headers: {}
		});
		var user = localStorage.getItem('ACC')
		var _session = '';
		// console.log('====>本地存储数据' + user)
		if (user == undefined) {
			_session = md5(Math.random() + '' + md5(JSON.stringify(data)));
			console.log(_session)
			data['_session'] = _session;
			// console.log('====>' + _session)
		} else {
			data['_session'] = JSON.parse(user).data._session;
			// console.log('==User==>' + data['_session'])
		}
		var _r = Math.random();
		console.log(_r)
		data['_r'] = _r;
		// console.log(JSON.stringify(data))
		ajx.post('http://eqc.ifrasca.com/' + url, qs.stringify(data)).then(res => {
			_this.$toast.clear();
			if (res.data.code == 1) {
				callback(res.data)
			} else {
				_this.$toast.fail(res.data.msg);
				if (res.data.code == -1) {
					localStorage.removeItem('ACC');
				}

			}
			// console.log(url + '===Result' + JSON.stringify(res.data))
		}).catch(error => {
			alert(error)

		})

	}


}
