<template>
	<!-- 跨链转入 -->
	<div class="divColumn">
		<Title :title="$t('message.a67')"></Title>
		<div class="conTop divColumn">
			<div style="margin: 0rem 0.625rem;" class="divColumn">
				<div class="leftAndRight itemW">
					<span>{{ $t('message.a76') }}</span>
					<van-dropdown-menu style="background-color: #F6F8FE;height: 2.625rem;">
						<van-dropdown-item v-model="value1" :options="option1" @change="changeItem" />
					</van-dropdown-menu>
				</div>
				<!-- 提示 -->
				<div style="text-align: left;margin-top: 0.5rem;font-size: 0.75rem;color: orange;">{{ $t('message.d18') }}</div>
				<!-- 没有添加对应钱包 -->
				<div class="leftAndRight itemW" style="color: orange;" @click="addWalt" v-show="isWalt">{{ $t('message.d19') }}{{ walName }}{{ $t('message.d20') }}</div>
				<div class="leftAndRight itemW">
					<span>{{ $t('message.d21') }}：</span>
					<span style="font-size: 0.75rem;">{{ address_out }}</span>
				</div>
				<div class="leftAndRight itemW">
					<span>{{ $t('message.d22') }}：</span>
					<span style="font-size: 0.75rem;">{{ walData.address }}</span>
				</div>
				<div class="leftAndRight itemW">
					<span style="width: 30%;">{{ $t('message.d23') }}：</span>
					<van-field style="background-color:#f6f8fe;" v-model="num" :placeholder="$t('message.d24')" type="number"></van-field>
				</div>
				<div
					class="itemW divCenter"
					style="background-color: #518CFF;border-radius: 0.3125rem;margin-top: 2.5rem;font-size: 1rem;font-weight: 600;color: white;"
					@click="SureBtn"
				>
					<span>{{ $t('message.a79') }}</span>
				</div>
				<!-- 温馨提示 -->
				<div class="divColumn" style="text-align: left;margin-top: 1.25rem;margin-bottom: 2.5rem;">
					<span style="margin-bottom: 0.625rem;">{{ $t('message.a80') }}</span>
					<span style="text-indent:2em;line-height: 1.5rem;">{{ $t('message.d25') }}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
import RippleFingate from 'jcc-ripple-utils';
import { Fingate, Ethereum, ERC20 } from 'jcc-ethereum-utils';
import BigNumber from 'bignumber.js';

export default {
	components: {
		Title
	},
	data() {
		return {
			value1: 0,
			option1: [{ text: 'USDT', value: 0 }, { text: 'ETH', value: 1 }, { text: 'ACC', value: 3 }, { text: 'IQC', value: 4 }, { text: 'XRP', value: 2 }],
			walData: {},
			num: '',
			isWalt: true,
			address_out: '', //转出方钱包地址
			type: 1, //1 添加eth地址 2添加瑞波链
			func: '',
			secret: '', //转出方钱包秘钥
			amount: '', //转出数量
			memo: '', //收币地址对象
			walName: 'ETH',
			minLimit: 0.02 //需要保留最小的量
		};
	},
	created: function() {
		var _this = this;
		//获取本地钱包数据
		_this.walData = _this.getWallent();
		console.log(_this.walData);
		_this.checkIsAdW();
		_this.memo = {
			jtaddress: _this.walData.address
		};
		console.log(JSON.stringify(_this.walData));
		if (_this.type == 1) {
			_this.address_out = _this.walData.address_eth;
			_this.secret = _this.walData.secret_eth;
		}
		_this.updataWallent();
	},
	// mixins: [jDepositContract],
	methods: {
		//检测是否添加对应钱包
		checkIsAdW: function() {
			var _this = this;
			if (_this.value1 == 0 || _this.value1 == 1 || _this.value1 == 3 || _this.value1 == 4) {
				//判断是否添加eth钱包
				_this.walName = 'ETH';
				if (_this.walData.secret_eth == undefined) {
					_this.isWalt = true;
				} else {
					_this.isWalt = false;
				}
				_this.address_out = _this.walData.address_eth;
				_this.type = 1;
			} else {
				_this.walName = _this.$t('message.d26');
				if (_this.walData.secret_ro == undefined) {
					_this.isWalt = true;
				} else {
					_this.isWalt = false;
				}
				_this.address_out = _this.walData.address_ro;
				_this.type = 2;
			}
		},
		changeItem: function(value) {
			var _this = this;
			_this.value1 = value;
			_this.checkIsAdW();
			if (value == 0 || value == 1 || value == 3 || value == 4) {
				_this.type = 1;
			} else {
				_this.type = 2;
			}
		},
		addWalt: function() {
			var _this = this;
			_this.$router.push({
				path: '/AddWallent',
				query: {
					type: _this.type
				}
			});
		},
		async SureBtn() {
			const node = 'https://' + this.getInfoData().ethHosts[0];
			// Your ethereum secret
			const ethereumSecret = this.walData.secret_eth;
			// Your ethereum address
			const ethereumAddress = this.address_out;
			// Your swtc address
			const swtcAddress = this.walData.address;
			// const swtcAddress = "jnp1FYahkrZ7mLUBmwyRbb3qWANtwj5jne";
			// Deposit amount
			const amount = this.num;
			// Ethereum fingate contract address, don't change it.
			const scAddress = '0x3907acb4c1818adf72d965c08e0a79af16e7ffb8';
			console.log('>>>>>' + ethereumSecret + '    ' + swtcAddress + '  ' + amount + '    ' + ethereumAddress);
			if (this.num == '') {
				this.$toast.fail(this.$t('message.d24'));
				return;
			}
			if (this.address_out == '') {
				if (this.value1 == 2) {
					this.$toast.fail(this.$t('message.d27'));
				} else {
					this.$toast.fail(this.$t('message.d28'));
				}
				return;
			}
			this.$toast.loading({
				forbidClick: true,
				duration: 0
			});
			if (this.value1 == 1) {
				//以太链
				try {
					// deposit 1 ETH
					const ethereumInstance = new Ethereum(node, true);
					ethereumInstance.initWeb3();
					// 获取余额
					const ethBalance = await ethereumInstance.getBalance(this.address_out);
					console.log(ethBalance);
					if (new BigNumber(ethBalance).minus(this.minLimit).lt(amount)) {
						this.$toast.fail(this.$t('message.d29'));
						return;
					}
					if (new BigNumber(this.num).gt(ethBalance)) {
						this.$toast.fail(this.$t('message.d30'));
						return;
					}
					const fingateInstance = new Fingate();
					fingateInstance.init(scAddress, ethereumInstance);
					// Check if has pending order, if has don't call the next deposit api
					const state = await fingateInstance.depositState(ethereumAddress);
					if (fingateInstance.isPending(state)) {
						this.$toast.fail(this.$t('message.d31'));
						return;
					}
					// start to transfer 1 ETH to fingate address
					// debugger;
					const hash = await fingateInstance.deposit(ethereumSecret, swtcAddress, amount);
					if (hash) {
						console.log(hash);
						this.$toast.clear();
						this.num = '';
						this.$toast.success(this.$t('message.d32'));
					}
				} catch (error) {
					console.log(error);
					this.$toast.clear();
				}
			} else if (this.value1 == 0 || this.value1 == 3 || this.value1 == 4) {
				//以太链的代币
				try {
					// deposit 1 JCC
					// JCC contract address
					var usdtContractAddress = '';
					if (this.value1 == 0) {
						//USDT
						usdtContractAddress = '0xdac17f958d2ee523a2206206994597c13d831ec7'; //usdt
					} else if (this.value1 == 3) {
						//ACC
						usdtContractAddress = '0x630D4b0ef9F062BD5D95d34B225A0A72ECf13F6D';
					} else if (this.value1 == 4) {
						//IQC
						usdtContractAddress = '0x150b57cC923A2048a48d1a4C8447eF2eC8E80f51';
					}
					// const usdtContractAddress = '0xdac17f958d2ee523a2206206994597c13d831ec7';//usdt
					// const usdtContractAddress = '0x630D4b0ef9F062BD5D95d34B225A0A72ECf13F6D';
					// 0x630D4b0ef9F062BD5D95d34B225A0A72ECf13F6D
					const ethereumInstance = new Ethereum(node, true);
					ethereumInstance.initWeb3();

					const erc20Instance = new ERC20();
					erc20Instance.init(usdtContractAddress, ethereumInstance);
					const ethBalance = await ethereumInstance.getBalance(this.address_out);
					console.log(ethBalance);
					// 暂时不判断余额
					const usdtBalance = await erc20Instance.balanceOf(this.address_out);
					console.log(usdtBalance);
					if (new BigNumber(ethBalance).lt(this.minLimit)) {
						this.$toast.fail(this.$t('message.d29'));
						return;
					}
					if (new BigNumber(amount).gt(usdtBalance)) {
						this.$toast.fail(this.$t('message.d33'));
						return;
					}
					const fingateInstance = new Fingate();
					fingateInstance.init(scAddress, ethereumInstance);

					// Check if has pending order, if has don't call transfer api
					const state = await fingateInstance.depositState(ethereumAddress, usdtContractAddress);

					if (fingateInstance.isPending(state)) {
						this.$toast.fail(this.$t('message.d31'));
						return;
					}

					const decimals = await erc20Instance.decimals();

					// The first step to transfer 1 JCC to fingate address.
					const transferHash = await erc20Instance.transfer(ethereumSecret, scAddress, amount);

					// The next step to submit previous transfer hash.
					const depositHash = await fingateInstance.depositToken(swtcAddress, usdtContractAddress, decimals, amount, transferHash, ethereumSecret);
					console.log(depositHash);
					if (depositHash) {
						this.$toast.clear();
						this.num = '';
						this.$toast.success(this.$t('message.d32'));
					}
					// Warning:
					// This is not an atomic operating to deposit erc20 tokens for now,
					// If the first step is successful but next step is failed, please contact us.
					// The next version will make it an atomic operating after the next version of solidity contract upgrade.
				} catch (error) {
					console.log(error);
					this.$toast.clear();
				}
			} else {
				//瑞波链
				const testServer = 'wss://s1.ripple.com';
				const instance = new RippleFingate(testServer);
				/* 'sh8Y27qyNJazuLc4mhfsBodsNRaUS' */
				const testSecret = this.walData.secret_ro;
				//  'rn5CUr36CHu812CvKL6aMCr7hZkNigbUFS';
				const testAddress = this.walData.address_ro;
				// Don't change it. The fingate address is it for now.
				const destination = 'rMUpPikgdhmtCida2zf4CMBLrBREfCeYcy';
				const testMemo = {
					jtaddress: this.walData.address
				};
				const amount = this.num;
				try {
					// transfer 1 XRP
					// const instance = new RippleFingate(testServer);
					await instance.connect();
					const balance = await instance.getXrpBalance(testAddress);
					console.log(balance);
					const hash = await instance.transfer(testSecret, destination, amount, testMemo);
					console.log(hash);
					this.$toast.clear();
					this.num = '';
					this.$toast.success(this.$t('message.d32'));
				} catch (error) {
					console.log(error);
					this.$toast.clear();
				} finally {
					instance.disconnect();
				}
			}
		}
	}
};
</script>

<style>
.itemW {
	height: 2.625rem;
	background-color: #f6f8fe;
	margin-top: 0.625rem;
	padding: 0rem 0.625rem;
}
</style>
