<template>
	<!-- 买入或者卖出 -->
	<div class="divColumn">
		<div style="width: 100%;height: 2rem;" class="divRow divColumnCenter">
			<div class="divCenter buyWrap" :class="{ unClick: type == 2 }" @click="buy">{{ $t('message.a31') }}</div>
			<div class="divCenter sellWrap" :class="{ unClick: type == 1 }" @click="sell">{{ $t('message.a32') }}</div>
		</div>
		<!-- 可用冻结买入 -->
		<div class="divColumn" style="margin-top: 0.625rem;font-size: 0.8125rem;" v-show="type == 1">
			<div style="margin-left: 0.625rem;color: #ee4a4a;" class="leftAndRight">
				<span>{{ $t('message.a33') }}</span>
				<span>{{ bal_U }}USDT</span>
			</div>
			<div style="margin-left: 0.625rem;color: #0ab9fa;margin-top: 0.25rem;" class="leftAndRight">
				<span>{{ $t('message.a73') }}</span>
				<span>{{ freezed_U }}USDT</span>
			</div>
		</div>
		<!-- 可用冻结卖出 -->
		<div class="divColumn" style="margin-top: 0.625rem;font-size: 0.8125rem;" v-show="type == 2">
			<div style="margin-left: 0.625rem;color: #28b869;" class="leftAndRight">
				<span>{{ $t('message.a33') }}</span>
				<span>{{ bal }}{{ bName.replace('J', '') }}</span>
			</div>
			<div style="margin-left: 0.625rem;color: #0ab9fa;margin-top: 0.25rem;" class="leftAndRight">
				<span>{{ $t('message.a73') }}</span>
				<span>{{ freezed }}{{ bName.replace('J', '') }}</span>
			</div>
		</div>
		<!-- 输入价格和数量 -->
		<div class="divColumn" v-show="type == 1">
			<div class="inpWrap leftAndRight">
				<span>{{ $t('message.a35') }}</span>
				<input v-model="buyPrice" style="width: 50%;text-align: right;" type="number" placeholder="0" v-show="valueType != 3" />
				<input v-model="buyPrice" style="width: 50%;text-align: right;" type="number" placeholder="0" readonly="true" v-show="valueType == 3" />
			</div>
			<!-- 约等于人民币 -->
			<div style="text-align: left;padding-left: 0.625rem;color: #999999;font-size: 0.75rem;margin-top: 0.25rem;">
				≈ ¥
				<span v-show="valueType == 3">{{ Number(renPric).toFixed(2) }}</span>
				<span v-show="valueType != 3">{{ Number(renPric).toFixed(6) }}</span>
			</div>
			<div class="inpWrap leftAndRight">
				<span>{{ $t('message.a36') }}</span>

				<input v-model="buyCount" style="width: 50%;text-align: right;" type="number" placeholder="0" />
			</div>
			<!-- 交易额 -->
			<div class="inpWrap leftAndRight">
				<span>{{ $t('message.a37') }}：</span>
				<span v-show="valueType == 3">{{ Number(allMoney).toFixed(2) }}</span>
				<span v-show="valueType != 3">{{ Number(allMoney).toFixed(6) }}</span>
				USDT
			</div>
		</div>
		<!-- 输入价格和数量 -->
		<div class="divColumn" v-show="type == 2">
			<div class="inpWrap leftAndRight">
				<span>{{ $t('message.a91') }}</span>
				<input v-model="sellPrice" style="width: 50%;text-align: right;" type="number" placeholder="0" v-show="valueType != 3" />
				<input v-model="sellPrice" style="width: 50%;text-align: right;" type="number" placeholder="0" readonly="true" v-show="valueType == 3" />
			</div>
			<!-- 约等于人民币 -->
			<div style="text-align: left;padding-left: 0.625rem;color: #999999;font-size: 0.75rem;margin-top: 0.25rem;">
				≈ ¥
				<span v-show="valueType == 3">{{ Number(renPricSell).toFixed(2) }}</span>
				<span v-show="valueType != 3">{{ Number(renPricSell).toFixed(6) }}</span>
			</div>
			<div class="inpWrap leftAndRight">
				<span>{{ $t('message.a92') }}</span>

				<input v-model="sellCount" style="width: 50%;text-align: right;" type="number" placeholder="0" />
			</div>
			<!-- 交易额 -->
			<div class="inpWrap leftAndRight">
				<span>{{ $t('message.a37') }}：</span>
				<span v-show="valueType == 3">{{ Number(allMoneySell).toFixed(2) }}</span>
				<span v-show="valueType != 3">{{ Number(allMoneySell).toFixed(6) }}</span>
				USDT
			</div>
		</div>
		<div style="margin-left: 0.625rem;margin-top: 1.4rem;margin-bottom: 1.4rem;"><!-- <van-slider active-color="#28b869" v-model="value" @change="onChange" /> --></div>
		<div style="margin-left: 0.625rem;" v-show="type == 1">
			<div class="divCenter btnBuy" @click="sureBuy">
				{{ $t('message.a31') }}
				<span v-show="bName == 'AICC'" style="margin-top: 0.3125rem;">AICC</span>
				<span v-show="bName == 'JETH'" style="margin-top: 0.3125rem;">ETH</span>
				<span v-show="bName == 'JXRP'" style="margin-top: 0.3125rem;">XRP</span>
				<span v-show="bName == 'JACC'" style="margin-top: 0.3125rem;">ACC</span>
			</div>
		</div>
		<div style="margin-left: 0.625rem;" v-show="type == 2">
			<div class="divCenter btnSell" @click="sureSell">
				{{ $t('message.a32') }}
				<span v-show="bName == 'AICC'" style="margin-top: 0.3125rem;">AICC</span>
				<span v-show="bName == 'JETH'" style="margin-top: 0.3125rem;">ETH</span>
				<span v-show="bName == 'JXRP'" style="margin-top: 0.3125rem;">XRP</span>
				<span v-show="bName == 'JACC'" style="margin-top: 0.3125rem;">ACC</span>
			</div>
		</div>
		<van-dialog v-model="show" :title="$t('message.b48')" show-cancel-button @confirm="sureBtn" :confirm-button-text="$t('message.b4')" :cancel-button-text="$t('message.b5')">
			<div style="width: 100%;height: 3.625rem;" class="divColumn">
				<div style="margin: 0.875rem;"><van-field style="background-color:#f6f8fe;" v-model="pwd" :placeholder="$t('message.b30')" type="password"></van-field></div>
			</div>
		</van-dialog>
	</div>
</template>

<script>
import JCCExchange from 'jcc_exchange';
import BigNumber from 'bignumber.js';
export default {
	data() {
		return {
			type: 1,
			buyPrice: '',
			buyCount: '',
			value: 1,
			sellPrice: '',
			sellCount: '',
			walBal: {}, // 钱包币种余额
			renPric: 0,
			timeout: null,
			allMoney: 0,
			renPricSell: 0,
			allMoneySell: 0,
			walData: {},
			show: false,
			pwd: '',
			time: 10,
			valueType: ''
		};
	},
	props: {
		bName: String,
		bal_U: String,
		freezed_U: String,
		bal: String,
		freezed: String,
		fatherMethod: {
			type: Function,
			default: null
		}
	},
	created: function() {
		var _this = this;
		if (_this.bName == 'ACC') {
			_this.sellPrice = 1;
			_this.buyPrice = 1;
		}
		_this.getDataInfo();
	},
	watch: {
		buyPrice(curVal) {
			// 实现input连续输入，只发一次请求
			var _this = this;
			_this.renPric = Number(curVal * 7.026).toFixed(4);
			_this.allMoney = curVal * _this.buyCount;
		},
		buyCount(curVal) {
			var _this = this;
			_this.allMoney = curVal * _this.buyPrice;
		},
		sellCount(curVal) {
			// 实现input连续输入，只发一次请求
			var _this = this;
			_this.allMoneySell = curVal * _this.sellPrice;
		},
		sellPrice(curVal) {
			var _this = this;
			_this.renPricSell = Number(curVal * 7.026).toFixed(4);
			_this.allMoneySell = curVal * _this.sellCount;
		}
	},
	methods: {
		// ACC 交易时 固定价格
		setPrice: function() {},
		getDataInfo: function() {
			var _this = this;
			_this.getExSeq(function(res) {
				console.log(res);
			});
			//  获取钱包余额 用来判断交易时币是否够
			_this.getWallentBal(function(res) {
				_this.walBal = res.data;
			});
			//获取本地钱包数据
			_this.walData = _this.getWallent();
		},

		buy: function() {
			this.type = 1;
			this.pwd = '';
		},
		sell: function() {
			this.type = 2;
			this.pwd = '';
		},
		//判断买是否够币 如果是买 就判断消耗的币种是否够，如果是卖就判断交易的币种是否够
		checkNumBuy: function() {
			var _this = this;
			var money = ''; //总价数量*金额
			var isEnough = false;
			var bi = 'JUSDT';
			if (_this.type == 1) {
				//买
				money = _this.allMoney;
			} else {
				//卖
				money = _this.sellCount;
				bi = _this.bName.toUpperCase();
				if (bi == 'AICC') {
					bi = 'SWT';
				}
			}
			console.log(JSON.stringify(_this.walBal));
			for (var i = 0; i < _this.walBal.length; i++) {
				if (_this.walBal[i].currency.toUpperCase() == bi) {
					//判断余额是否足够
					var bal = new BigNumber(_this.walBal[i].value).minus(_this.walBal[i].freezed).toString(10);
					console.log(bal, money, new BigNumber(money).gt(bal));
					if (new BigNumber(money).lt(bal)) {
						isEnough = true;
					}
				}
			}
			return isEnough;
		},
		// 更新是否输入价格
		isChangePrice: function(val) {
			this.valueType = val;
			if (val == 3) {
				this.sellPrice = '0.1423';
				this.buyPrice = '0.1423';
			} else {
				this.sellPrice = '';
				this.buyPrice = '';
			}
		},
		async sureBtn() {
			var _this = this;
			if (_this.pwd == '') {
				_this.$toast.fail('请输入交易密码');
				return;
			}
			if (_this.pwd != _this.walData.pwd) {
				_this.$toast.fail('输入交易密码错误！');
				_this.pwd = '';
				return;
			}
			try {
				if (_this.type == 1) {
					//买  判断USDT 数量是否够
					// if (_this.bal_U < _this.buyCount) {
					// 	_this.$toast.fail('余额不足！');
					// 	return;
					// }
					JCCExchange.init(_this.getInfoData().jcNodes); //
					const address = _this.walData.address; //井筒钱包地址
					const secret = _this.walData.secret;
					const amount = _this.buyCount;
					const sum = _this.buyCount * _this.buyPrice; //挂单的总价
					var base = _this.bName; //需要交易的当前币种
					if (base == 'AICC') {
						base = 'SWT';
					}
					const counter = 'JUSDT'; //交易区
					const type = 'buy';
					const platform = ''; // swtc address for service charge
					const issuer = 'jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or'; // the default value is "jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or"
					console.log(address, secret, amount, sum, base, counter, type);

					_this.$toast.loading({
						forbidClick: true,
						duration: 0
					});
					const hash = await JCCExchange.createOrder(address, secret, Number(amount).toFixed(7), base, counter, Number(sum).toFixed(7), type, platform, issuer);
					console.log(JSON.stringify(hash) + '===========');
					if (hash) {
						_this.$toast.success(_this.$t('message.b2'));
						_this.pwd = '';
						if (this.fatherMethod) {
							this.fatherMethod();
						}
					} else {
						_this.$toast.success(_this.$t('message.b3'));
					}
				} else {
					//卖
					// if (_this.bal < _this.sellCount) {
					// 	_this.$toast.fail('余额不足！');
					// 	return;
					// }
					JCCExchange.init(_this.getInfoData().jcNodes); //
					const address = _this.walData.address; //井筒钱包地址
					const secret = _this.walData.secret;
					const amount = _this.sellCount;
					const sum = _this.sellCount * _this.sellPrice; //挂单的总价
					var base_ = _this.bName; //需要交易的当前币种
					if (base_ == 'AICC') {
						base_ = 'SWT';
					}
					const counter = 'JUSDT'; //交易区
					const type = 'sell';
					const platform = ''; // swtc address for service charge
					const issuer = 'jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or'; // the default value is "jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or"
					console.log(address, secret, amount, base_, counter, sum, type, platform, issuer);
					_this.$toast.loading({
						forbidClick: true,
						duration: 0
					});
					const hash = await JCCExchange.createOrder(address, secret, Number(amount).toFixed(7), base_, counter, Number(sum).toFixed(7), type, platform, issuer);
					console.log(JSON.stringify(hash));
					if (hash) {
						_this.$toast.success(_this.$t('message.a97'));
						_this.pwd = '';
						if (this.fatherMethod) {
							this.fatherMethod();
						}
					} else {
						_this.$toast.success(_this.$t('message.a98'));
					}
				}
			} catch (e) {
				//TODO handle the exception
				_this.$toast.fail('创建委托失败！');
				console.log(e);
			}
		},
		async sureSell() {
			var _this = this;
			try {
				if (JSON.stringify(_this.walData) == '{}') {
					_this.$toast.fail(_this.$t('message.a93'));
					return;
				}
				if (_this.sellPrice == '') {
					_this.$toast.fail(_this.$t('message.a94'));
					return;
				}
				if (_this.sellCount == '') {
					_this.$toast.fail(_this.$t('message.a95'));
					return;
				}
				if (!_this.checkNumBuy()) {
					_this.$toast.fail(_this.$t('message.a96') + _this.bName);
					return;
				}
				_this.show = true;
				// //卖
				// JCCExchange.init(['https://srje115qd43qw2.swtc.top', 'https://srje071qdew231.swtc.top']); //
				// const address = _this.walData.address; //井筒钱包地址
				// const secret = _this.walData.secret;
				// const amount = _this.sellCount;
				// const sum = _this.sellCount * _this.sellPrice; //挂单的总价
				// var base = _this.bName; //需要交易的当前币种
				// if (base == 'AICC') {
				// 	base = 'SWT';
				// }
				// const counter = 'JUSDT'; //交易区
				// const type = 'sell';
				// const platform = ''; // swtc address for service charge
				// const issuer = 'jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or'; // the default value is "jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or"
				// const hash = await JCCExchange.createOrder(address, secret, amount, base, counter, sum, type, platform, issuer);
				// console.log(JSON.stringify(hash));
				// if (hash) {
				// 	_this.$toast.success(_this.$t('message.a97'));
				// } else {
				// 	_this.$toast.success(_this.$t('message.a98'));
				// }
			} catch (e) {
				//TODO handle the exception
				console.log(e);
			}
		},
		async sureBuy() {
			//买
			var _this = this;
			if (JSON.stringify(_this.walData) == '{}') {
				_this.$toast.fail(_this.$t('message.a93'));
				return;
			}
			if (_this.buyPrice == '') {
				_this.$toast.fail(_this.$t('message.a99'));
				return;
			}
			if (_this.buyCount == '') {
				_this.$toast.fail(_this.$t('message.a100'));
				return;
			}
			if (!_this.checkNumBuy()) {
				_this.$toast.fail(_this.$t('message.b1'));
				return;
			}
			_this.show = true;
			try {
				// JCCExchange.init(['https://srje115qd43qw2.swtc.top', 'https://srje071qdew231.swtc.top']); //
				// const address = _this.walData.address; //井筒钱包地址
				// const secret = _this.walData.secret;
				// const amount = _this.buyCount;
				// const sum = _this.buyCount * _this.buyPrice; //挂单的总价
				// var base = _this.bName; //需要交易的当前币种
				// if (base == 'AICC') {
				// 	base = 'SWT';
				// }
				// const counter = 'JUSDT'; //交易区
				// const type = 'buy';
				// const platform = ''; // swtc address for service charge
				// const issuer = 'jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or'; // the default value is "jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or"
				// console.log(address, secret, amount, sum, base, counter, type);
				// const hash = await JCCExchange.createOrder(address, secret, amount, base, counter, sum, type, platform, issuer);
				// console.log(JSON.stringify(hash) + '===========');
				// if (hash) {
				// 	_this.$toast.success(_this.$t('message.b2'));
				// } else {
				// 	_this.$toast.success(_this.$t('message.b3'));
				// }
			} catch (e) {
				//TODO handle the exception
				console.log(e);
			}
		},
		onChange() {}
	}
};
</script>

<style>
.buyWrap {
	width: 49%;
	height: 100%;
	background-color: #ee4a4a;
	margin-left: 0.625rem;
	color: white;
}
.sellWrap {
	width: 49%;
	height: 100%;
	background-color: #28b869;
	color: white;
}
.unClick {
	background-color: #f0f0f0;
	color: black;
}
.inpWrap {
	height: 2.625rem;
	margin-left: 0.625rem;
	border: 0.0625rem solid #f0f0f0;
	border-radius: 0.3125rem;
	margin-top: 0.625rem;
	font-size: 0.75rem;
	padding: 0rem 0.5rem;
	color: #999999;
}
.btnBuy {
	width: 100%;
	height: 2.2rem;
	background-color: #ee4a4a;
	border-radius: 0.3125rem;
	color: white;
}
.btnSell {
	width: 100%;
	height: 2.2rem;
	background-color: #28b869;
	border-radius: 0.3125rem;
	color: white;
}
</style>
