<template>
	<!-- 修改钱包地址-->
	<div class="divColumn">
		<Title :title="$t('message.b46')"></Title>
		<div class="conTop divColumn">
			<div style="margin: 0rem 0.625rem;" class="divColumn">
				<div class="leftAndRight itemW">
					<span style="width: 30%;">{{ $t('message.b36') }}：</span>
					<van-field style="background-color:#f6f8fe;" v-model="secret" :placeholder="$t('message.b47')"></van-field>
				</div>
				<div class="leftAndRight itemW">
					<span style="width: 30%;">{{ $t('message.b48') }}：</span>
					<van-field style="background-color:#f6f8fe;" v-model="pwd" :placeholder="$t('message.b30')" type="password"></van-field>
				</div>
				<div class="leftAndRight itemW">
					<span style="width: 30%;">{{ $t('message.b50') }}：</span>
					<van-field style="background-color:#f6f8fe;" v-model="pwdAg" :placeholder="$t('message.b49')" type="password"></van-field>
				</div>
				<div
					@click="sureUpda"
					class="itemW divCenter"
					style="background-color: #518CFF;border-radius: 0.3125rem;margin-top: 2.5rem;font-size: 1rem;font-weight: 600;color: white;"
				>
					<span>{{ $t('message.b43') }}</span>
				</div>
				<!-- 温馨提示 -->
				<div class="divColumn" style="text-align: left;margin-top: 1.25rem;margin-bottom: 2.5rem;">
					<span style="margin-bottom: 0.625rem;">{{ $t('message.a80') }}</span>
					<span style="text-indent:2em;line-height: 1.5rem;">*{{ $t('message.b51') }}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
export default {
	components: {
		Title
	},
	data() {
		return {
			secret: '',
			pwd: '',
			pwdAg: ''
		};
	},
	created: function() {},
	methods: {
		sureUpda: function() {
			var _this = this;
			if (_this.secret == '') {
				_this.$toast.fail(_this.$t('message.b52'));
				return;
			}
			if (_this.pwd == '') {
				_this.$toast.fail(_this.$t('message.b30'));
				return;
			}
			if (_this.pwdAg == '') {
				_this.$toast.fail(_this.$t('message.b53'));
				return;
			}
			if (_this.pwd != _this.pwdAg) {
				_this.$toast.fail(_this.$t('message.b54'));
				_this.pwdAg = '';
				return;
			}
			var data = {
				pwd: _this.pwd,
				secret: _this.secret
			};
			_this.saveWallent(data);
			_this.$toast.success(_this.$t('message.b55'));
			_this.$router.go(-1);
		}
	}
};
</script>

<style></style>
