<template>
	<!-- 市场委托 -->
	<div class="divColumn" style="font-size: 0.75rem;">
		<van-tabs v-model="active" style="height: 1.25rem;" :border="false" :line-height="2">
			<van-tab :title="$t('message.a38')">
				<div style="height: 19.375rem;width: 100%;position: relative;">
					<div style="height: 1.75rem;margin: 0rem 0.625rem;" class="leftAndRight">
						<span>{{ $t('message.a40') }}</span>
						<span>{{ $t('message.a41') }}</span>
					</div>
					<!-- 卖 -->
					<div class="leftAndRight itemHWrap" v-for="(item, index) in wtList" :key="index">
						<span style="color: #07C160;">{{ Number(item.price).toFixed(8) }}</span>
						<span style="color: #999999;">{{ Number(item.amount).toFixed(6) }}</span>
					</div>
					<div style="height: 0.75rem;width: 100%;"></div>
					<!-- 买 -->
					<div class="leftAndRight itemHWrap" v-for="(item, index) in wtBuyList" :key="'info2' + index">
						<span style="color: #d81e06;">{{ Number(item.price).toFixed(8) }}</span>
						<span style="color: #999999;">{{ Number(item.amount).toFixed(6) }}</span>
					</div>
					<div class="divCenter moreWrap" @click="lookMore">{{ $t('message.a42') }}</div>
				</div>
			</van-tab>
			<van-tab :title="$t('message.a39')">
				<div style="height: 19.375rem;width: 100%;position: relative;">
					<div style="height: 1.75rem;margin: 0rem 0.625rem;" class="divCenter leftAndRight">
						<span>{{ $t('message.a54') }}</span>
						<span>{{ $t('message.a48') }}</span>
					</div>
					<div class="itemHWrap leftAndRight" v-for="(item, index) in newList" :key="index">
						<span class="sellColor" :class="{ buyColor: item[4] == 0 }">{{ Number(item[2]).toFixed(6) }}</span>
						<span style="color: #999999;">{{ Number(item[1]).toFixed(6) }}</span>
					</div>
					<div class="divCenter moreWrap" @click="lookMore">{{ $t('message.a42') }}</div>
				</div>
			</van-tab>
		</van-tabs>
	</div>
</template>

<script>
export default {
	data() {
		return {
			active: 0,
			wtList: [], // 卖单
			wtBuyList: [],
			newList: [],
			currency_: this.currency
		};
	},
	props: {
		currency: String
	},
	created: function() {
		var _this = this;
		_this.getDePeth(_this.currency, function(res) {
			console.log('>>>>>委托' + JSON.stringify(res));
			_this.wtList = res.data.asks;
			_this.wtBuyList = res.data.bids;
		});
		//获取最近市场交易
		_this.getLatExRecord(_this.currency, function(res) {
			_this.newList = res.data;
		});
	},
	methods: {
		lookMore: function() {
			var _this = this;
			_this.$router.push({
				path: '/MoreData',
				query: {
					type: _this.active,
					currency: _this.currency_
				}
			});
		},
		updataInfo: function(cur) {
			//获取深度
			var _this = this;
			_this.currency_ = cur;
			_this.getDePeth(cur, function(res) {
				console.log('>>>>>委托' + JSON.stringify(res));
				_this.wtList = res.data.asks;
				_this.wtBuyList = res.data.bids;
			});
			//获取最近市场交易
			_this.getLatExRecord(cur, function(res) {
				console.log('>>>>>>>'+JSON.stringify(res))
				_this.newList = res.data;
			});
		}
	}
};
</script>

<style>
.moreWrap {
	height: 2.2rem;
	background-color: #f0f0f0;
	width: 90%;
	border-radius: 0.3125rem;
	color: #999999;
	position: absolute;
	left: 5%;
	bottom: 0rem;
}
.itemHWrap {
	height: 1.2rem;
	margin: 0rem 0.625rem;
}
.sellColor {
	color: #07c160;
}
.buyColor {
	color: #d81e06;
}
</style>
