<template>
	<div class="divColumn">
		<div style="height: 2.5rem;position: relative;" class="divCenter">
			<img src="../../assets/img/ex_back.png" style="width: 1.5rem;position: absolute;left: 0.625rem;" @click="btnBack" />
			<span style="color: #3B79F4;font-size: 1.125rem;font-weight: 600;">{{ $t('message.e6') }}</span>
		</div>
		<div style="width: 100%;margin-top: 1.25rem;color: #666666;" class="divColumn">
			<!-- <div class="divCenter btnWrapDes" @click="ExAccount">切换账号</div> -->
			<div class="divCenter btnWrapDes" @click="exitLogin">{{ $t('message.e33') }}</div>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {};
	},
	methods: {
		btnBack: function() {
			this.$router.go(-1);
		},
		ExAccount: function() {
			this.$router.push('/ChangeAccount');
		},
		exitLogin: function() {
			var _this = this;
			this.$dialog.confirm({
					title: _this.$t('message.e33'),
					message: _this.$t('message.e34'),
					confirmButtonText: _this.$t('message.b4'),
					cancelButtonText: _this.$t('message.b5')
				})
				.then(() => {
					// on confirm
					_this.postApi('api/Auth/logOut.html', {}, function() {
						localStorage.removeItem('ACC');
						_this.$router.push('/Find');
					});
				})
				.catch(() => {
					// on cancel
				});
		}
	},
	mounted() {
		document.querySelector('html').setAttribute('style', 'background-color:#F2F2F4');
	},
	beforeDestroy() {
		document.querySelector('html').removeAttribute('style');
	}
};
</script>

<style>
.btnWrapDes {
	margin: 0rem 1rem;
	background-color: white;
	border-radius: 0.625rem;
	color: #666666;
	height: 3.25rem;
	margin-top: 1rem;
}
</style>
