<template>
	<!-- 实时转账 -->
	<div class="divColumn">
		<Title :title="$t('message.a57')"></Title>
		<div class="conTop conMagin">
			<div style="height: 2.625rem;margin-top: 1.125rem;" class="leftAndRight">
				<div :style="lbk" class="divCenter dlWrap" :class="{ dlWrapChoi: type == 1 }" @click="intoKey">{{ $t('message.c9') }}</div>
				<div :style="rbk" class="divCenter dlWrap" :class="{ dlWrapChoi: type == 2 }" @click="intoStore">{{ $t('message.c10') }}</div>
			</div>
			<!-- 具体内容 -->
			<div class="divColumn" style="text-align: left;">
				<span style="margin-top: 0.625rem;" v-show="type == 1">{{ $t('message.c11') }}</span>
				<div style="height: 2.625rem;background-color: #f6f8fe;margin-top: 0.625rem;padding: 0rem 1rem;" class="leftAndRight">
					<span>{{ $t('message.c13') }}</span>
					<van-dropdown-menu style="background-color: #F6F8FE;height: 2.625rem;">
						<van-dropdown-item v-model="value1" :options="option1" @change="changeItem" />
					</van-dropdown-menu>
				</div>
				<van-field class="itemTrInWrap" :placeholder="$t('message.c14')" v-show="type == 2" v-model="out_secrect"></van-field>
				<van-field class="itemTrInWrap" :placeholder="$t('message.c15')" v-model="to_address"></van-field>
				<van-field class="itemTrInWrap" :placeholder="$t('message.c16')" type="number" v-model="num"></van-field>
				<van-field class="itemTrInWrap" :placeholder="$t('message.c17')" v-model="remark"></van-field>
				<van-field class="itemTrInWrap" :placeholder="$t('message.c18')" v-model="pwd" type="password"></van-field>

				<div
					@click="sureTr"
					class="itemW divCenter"
					style="background-color: #518CFF;border-radius: 0.3125rem;margin-top: 3.125rem;font-size: 1rem;font-weight: 600;color: white;"
				>
					<span>{{ $t('message.c12') }}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
import BigNumber from 'bignumber.js';
import { jtWallet } from 'jcc_wallet';
import JCCExchange from 'jcc_exchange';
export default {
	components: {
		Title
	},
	data() {
		return {
			lbk: {
				backgroundImage: 'url(' + require('../../assets/img/dlc.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			rbk: {
				backgroundImage: 'url(' + require('../../assets/img/dr.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			type: 1,
			value1: 0,
			option1: [],
			to_address: '',
			num: '',
			remark: '',
			out_secrect: '',
			walBal: {},
			itemBal: 0,
			token: '',
			walData: {},
			pwd: ''
		};
	},
	created: function() {
		var _this = this;
		_this.getWallentBal(function(res) {
			_this.walBal = res;
			if (!res.isActive) {
				return;
			}
			_this.itemBal = new BigNumber(_this.walBal.data[0].value).minus(_this.walBal.data[0].freezed).toString(10);
			for (var i = 0; i < res.data.length; i++) {
				var item = res.data[i];
				var data = {};
				if (item.currency == 'SWT') {
					data = {
						text: 'AICC',
						value: i
					};
				} else {
					data = {
						text: item.currency.replace('J', ''),
						value: i
					};
				}
				if (i == 0) {
					_this.token = item.currency;
				}
				_this.option1.push(data);
			}
		}); //获取钱包余额,将钱包余额搞到筛选中
		//获取本地钱包数据
		_this.walData = _this.getWallent();
	},
	methods: {
		cleanData: function() {
			var _this = this;
			_this.to_address = '';
			_this.num = '';
			_this.remark = '';
		},
		intoKey: function() {
			var _this = this;
			_this.type = 1;
			_this.lbk.backgroundImage = 'url(' + require('../../assets/img/dlc.png') + ')';
			_this.rbk.backgroundImage = 'url(' + require('../../assets/img/dr.png') + ')';
			_this.cleanData();
		},
		intoStore: function() {
			var _this = this;
			_this.type = 2;
			_this.lbk.backgroundImage = 'url(' + require('../../assets/img/dl.png') + ')';
			_this.rbk.backgroundImage = 'url(' + require('../../assets/img/drc.png') + ')';
			_this.cleanData();
		},
		changeItem: function(value) {
			var _this = this;
			_this.itemBal = new BigNumber(_this.walBal.data[value].value).minus(_this.walBal.data[value].freezed).toString(10);
			if (_this.option1[value].text != 'AICC') {
				_this.token = 'J' + _this.option1[value].text;
			} else {
				_this.token = _this.option1[value].text;
			}
		},
		async sureTr() {
			var _this = this;
			try {
				JCCExchange.init(_this.getInfoData().jcNodes); //
				if (_this.to_address == '') {
					_this.$toast.fail(_this.$t('message.c15'));
					return;
				}
				if (_this.num == '') {
					_this.$toast.fail(_this.$t('message.c16'));
					return;
				}
				if (Number(_this.num) > Number(_this.itemBal)) {
					_this.$toast.fail(_this.$t('message.c19'));
					return;
				}
				if (_this.pwd == '') {
					_this.$toast.fail(_this.$t('message.b30'));
					return;
				}
				if (_this.pwd != _this.walData.pwd) {
					_this.$toast.fail(_this.$t('message.c20'));
					_this.pwd = '';
					return;
				}
				var address = ''; //转出钱包地址
				var secret = _this.out_secrect.trim(); //转出钱包秘钥
				const amount = _this.num;
				const memo = _this.remark; //描述
				const to = _this.to_address; //转入到的钱包地址
				const token = _this.token;
				const issuer = 'jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or'; // the default value is "jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or"
				if (_this.type == 1) {
					//我的钱包转账
					address = _this.walData.address;
					secret = _this.walData.secret;
				} else {
					//通用钱包转账
					if (secret == '') {
						_this.$toast.fail(_this.$t('message.b47'));
						return;
					}
					//验证秘钥合法性 不合法返回false
					if (!jtWallet.isValidSecret(secret)) {
						_this.$toast.fail(_this.$t('message.c21'));
						_this.out_secrect = '';
						return;
					}
					address = jtWallet.getAddress(secret);
				}
				console.log(address, secret, amount, token);
				_this.pwd = '';
				var hash = await JCCExchange.transfer(address, secret, amount, memo, to, token, issuer);
				console.log(hash);
				if (hash) {
					_this.$toast.success(_this.$t('message.c22'));
				} else {
					_this.$toast.success(_this.$t('message.c23'));
				}
			} catch (error) {
				//TODO handle the exception
				console.log('payment error: ', error);
			}
		}
	}
};
</script>

<style>
.spTitleWrap {
	color: #999999;
}

.spTitleWrapCh {
	color: #518cff;
}
.spHzLine {
	height: 0.125rem;
	background-color: #518cff;
	width: 50%;
	margin-top: 0.25rem;
	position: absolute;
	bottom: 20%;
}
.itemTrInWrap {
	background-color: #f6f8fe;
	margin-top: 0.625rem;
	font-size: 0.875rem;
}
</style>
