<template>
	<!-- 钱包管理 -->
	<div class="divColumn">
		<Title :title="$t('message.a56')"></Title>
		<div class="conTop">
			<div class="conMagin divColumn">
				<div class="divRow" id="topName">
					<span style="color: #000000;font-weight: 600;margin-right: 0.625rem;">{{ $t('message.b10') }}</span>
					<span style="font-size: 0.8125rem;margin-top:0.1875rem;">{{ $t('message.b11') }}</span>
				</div>
				<!-- 图片背景 -->
				<div style="width: 100%;height: 10rem;position: relative;color: white;" :style="topBk">
					<span style="position: absolute;left: 0.625rem;top: 0.625rem;font-weight: bold;">{{ $t('message.b12') }}</span>
					<img src="../../assets/img/as1.png" style="width: 1.25rem;position: absolute;right: 0.625rem;top: 0.625rem;" @click="lkWalt" />
					<!-- 钱包地址 -->
					<div style="position: absolute;top: 24%;margin: 0rem 0.625rem;" class="divRow divColumnCenter">
						<span>{{ address }}</span>
						<img src="../../assets/img/white_copy.png" style="width: 1.125rem;margin-left: 1.625rem;" v-clipboard:copy="walData.address" v-clipboard:success="onCopy" />
					</div>
					<!-- 底部三格栏 -->
					<div style="position: absolute;bottom: 8%;width: 100%;height: auto;color: white;font-size: 0.8125rem;" class="divRow divColumnCenter">
						<div style="width: 33.33%;height: 100%;" class="divColumn divCenter" @click="updataAdd">
							<img src="../../assets/img/wm1.png" style="width: 1.25rem;" />
							<span style="margin-top: 0.625rem;">{{ $t('message.b13') }}</span>
						</div>
						<div style="width: 33.33%;height: 100%;" class="divColumn divCenter" @click="updataPwd">
							<img src="../../assets/img/wm2.png" style="width: 1.25rem;" />
							<span style="margin-top: 0.625rem;">{{ $t('message.b14') }}</span>
						</div>
						<div style="width: 33.33%;height: 100%;" class="divColumn divCenter" @click="cleanCache">
							<img src="../../assets/img/wm3.png" style="width: 1.25rem;" />
							<span style="margin-top: 0.625rem;">{{ $t('message.b15') }}</span>
						</div>
					</div>
				</div>
				<!-- 其它钱包 -->
				<div style="width: 100%;height: auto;background-color: #F6F8FE;margin-top: 0.625rem;">
					<div class="divRow" style="margin: 0.625rem;">
						<span style="color: #000000;font-weight: 600;margin-right: 0.625rem;">{{ $t('message.b16') }}</span>
						<span style="font-size: 0.8125rem;margin-top:0.1875rem;">{{ $t('message.b17') }}</span>
					</div>
					<div style="width: 100%;" class="divColumn" v-for="(item, index) in otherWlist" :key="index">
						<div style="width: 100%;height: 0.125rem;background-color: white;"></div>
						<div style="height: 2.4rem;margin: 0rem 0.625rem;margin-top: 0.125rem;" class="leftAndRight">
							<div class="divRow divColumnCenter">
								<img :src="item.icon" style="width: 1.25rem;" />
								<span style="margin-left: 0.625rem;font-size: 0.875rem;font-weight: 600;">{{ item.name }}</span>
							</div>
							<div class="divRow divColumnCenter">
								<div class="divCenter">
									<div v-show="index == 1">
										<div v-show="!isAddRo" class="divCenter" @click="addWallent(index)">
											<img src="../../assets/img/wm5.png" style="width: 1rem;" />
											<span style="margin-left: 0.625rem;font-size: 0.8125rem;">{{ $t('message.b18') }}</span>
										</div>
										<div v-show="isAddRo" class="divCenter" @click="lookWalt(3)">
											<img src="../../assets/img/wm5.png" style="width: 1rem;" />
											<span style="margin-left: 0.625rem;font-size: 0.8125rem;">{{ $t('message.b19') }}</span>
										</div>
									</div>
									<div v-show="index == 0 && !isAddEth" class="divCenter" @click="addWallent(index)">
										<img src="../../assets/img/wm5.png" style="width: 1rem;" />
										<span style="margin-left: 0.625rem;font-size: 0.8125rem;">{{ $t('message.b20') }}</span>
									</div>
								</div>
								<div v-show="index == 0" class="divCenter">
									<div v-show="isAddEth" class="divCenter" @click="lookWalt(2)">
										<img src="../../assets/img/wm5.png" style="width: 1rem;" />
										<span style="margin-left: 0.625rem;font-size: 0.8125rem;">{{ $t('message.b21') }}</span>
									</div>
									<div v-show="isAddEth" class="divCenter" @click="upWal()">
										<img src="../../assets/img/wm4.png" style="width: 1rem;margin-left: 0.625rem;" />
										<span style="margin-left: 0.625rem;font-size: 0.8125rem;">{{ $t('message.b22') }}</span>
									</div>
								</div>
								<div v-show="index != 0" class="divCenter">
									<div @click="createWal" v-show="!isAddRo" class="divCenter">
										<img src="../../assets/img/wm4.png" style="width: 1rem;margin-left: 0.625rem;" />
										<span style="margin-left: 0.625rem;font-size: 0.8125rem;">{{ $t('message.b23') }}</span>
									</div>
									<div v-show="isAddRo" class="divCenter" @click="upWal()">
										<img src="../../assets/img/wm4.png" style="width: 1rem;margin-left: 0.625rem;" />
										<span style="margin-left: 0.625rem;font-size: 0.8125rem;">{{ $t('message.b24') }}</span>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<!-- 导入文件按钮 -->
				<div class="leftAndRight aiWrap">
					<van-uploader capture="camera" :after-read="afterRead" class="divCenter aiInWrap">
						<div>{{ $t('message.b25') }}</div>
					</van-uploader>
					<!-- <input id="file" type="file" accept=".json" style="background: #00000000;position: absolute;opacity: 0;" class="aiInWrap" multiple="multiple" /> -->
					<div class="divCenter aiOutWrap" @click="OutFile">{{ $t('message.b26') }}</div>
				</div>
				<!-- 温馨提示 -->
				<div class="divColumn" style="text-align: left;margin-top: 1.25rem;margin-bottom: 2.5rem;">
					<span style="margin-bottom: 0.625rem;">{{ $t('message.b27') }}</span>
					<span v-for="(item, index) in tip" :key="index" style="text-indent:2em;line-height: 1.5rem;text-align: justify;">*{{ item }}</span>
				</div>
			</div>
		</div>
		<van-dialog
			v-model="show"
			:title="$t('message.b28')"
			show-cancel-button
			@confirm="sureCache"
			:confirm-button-text="$t('message.b4')"
			:cancel-button-text="$t('message.b5')"
		>
			<div style="width: 100%;height: 10rem;" class="divColumn">
				<span style="margin: 0.875rem;text-align: left;font-size: 0.8125rem;">{{ $t('message.b29') }}</span>
				<div style="margin: 0.875rem;"><van-field style="background-color:#f6f8fe;" v-model="pwd" :placeholder="$t('message.b30')" type="password"></van-field></div>
			</div>
		</van-dialog>
		<div id="qrcode" ref="qrcode" style="display: none;"></div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
import QRCode from 'qrcodejs2';
import QrCodeDe from 'qrcode-decoder';

export default {
	data() {
		return {
			topBk: {
				backgroundImage: 'url(' + require('../../assets/img/as_bk.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			walData: {},
			address: '',
			otherWlist: [
				{
					name: this.$t('message.b31'),
					icon: require('../../assets/img/eth_img.png')
				},
				{
					name: this.$t('message.b32'),
					icon: require('../../assets/img/lb_img.png')
				}
			],
			tip: [],
			show: false,
			pwd: '',
			isAddRo: false, //判断是否添加了瑞波链
			ImportJSON: {},
			isAddEth: false // 判断是否添加了eth链
		};
	},
	components: {
		Title
	},
	created: function() {
		var _this = this;
		//获取本地钱包数据
		_this.walData = _this.getWallent();
		console.log('>>>>>>>>' + JSON.stringify(_this.walData));
		_this.address = _this.replaceX(_this.walData.address);
		if (localStorage.getItem('lang') == 'ch') {
			_this.tip = _this.gloabal.walTip;
		} else {
			_this.tip = _this.gloabal.walTipEn;
		}
		if (_this.walData.address_ro != undefined) {
			_this.isAddRo = true;
		}
		if (_this.walData.address_eth != undefined) {
			_this.isAddEth = true;
		}
		_this.$nextTick(() => {
			_this.qrcode();
		});
	},
	methods: {
		onCopy: function() {
			this.$toast.success(this.$t('message.b44'));
		},
		updataPwd: function() {
			var _this = this;
			_this.$router.push('/UpdataPwd');
		},
		updataAdd: function() {
			var _this = this;
			_this.$router.push('/UpdataAddress');
		},
		cleanCache: function() {
			var _this = this;
			_this.show = true;
		},
		sureCache: function() {
			var _this = this;
			if (_this.pwd == '') {
				_this.$toast.fail(_this.$t('message.b30'));
				_this.pwd = '';
				return;
			}
			if (_this.pwd != _this.walData.pwd) {
				_this.$toast.fail(_this.$t('message.b33'));
				_this.pwd = '';
				return;
			}
			_this.pwd = '';
			_this.cleanWallent();
			_this.postApi('api/Auth/logOut.html', {}, function() {
				localStorage.removeItem('ACC');
			});
			_this.$router.go(-1);
		},
		addWallent: function(index) {
			var _this = this;
			_this.$router.push({
				path: '/AddWallent',
				query: {
					type: index + 1
				} // 1 以太链钱包 2 瑞波链钱包
			});
		},
		createWal: function() {
			var _this = this;
			_this.$router.push({
				path: '/CreateWallent',
				query: {
					type: 2
				}
			});
		},
		lookWalt: function(type) {
			var _this = this;
			_this.$router.push({
				path: '/LookWalt',
				query: {
					type: type // 2 以太链钱包 3 瑞波钱包
				}
			});
		},
		upWal: function() {
			//查看AICOIN的钱包详情
			var _this = this;
			_this.$router.push({
				path: '/AddWallent',
				query: {
					type: 1
				}
			});
		},
		lkWalt: function() {
			//查看AICOIN的钱包详情
			var _this = this;
			_this.$router.push({
				path: '/LookWalt',
				query: {
					type: 1
				}
			});
		},
		//导出文件
		OutFile: function() {
			let myCanvas = document.getElementById('qrcode').getElementsByTagName('canvas');
			// 创建一个a标签
			let a = document.createElement('a');
			//设置a 标签的href属性，将canvas变成图片
			a.href = myCanvas[0].toDataURL('image/png');
			//设置下载文件的名字
			a.download = 'keyStore文件';
			a.click();
		},
		//动态生成二维码
		qrcode() {
			var _this = this;
			const data = JSON.stringify(_this.getWallent());
			new QRCode('qrcode', {
				width: 100, // 设置宽度，单位像素
				height: 100, // 设置高度，单位像素
				text: data // 设置二维码内容或跳转地址
			});
		},
		afterRead(file) {
			this.getUrl(file.file);
		},
		getUrl(file) {
			let qr = new QrCodeDe();
			let url = this.getObjectURL(file);
			qr.decodeFromImage(url).then(res => {
				//打印结果为 解析出来的 二维码地址
				console.log(res.data);
			});
		},
		getObjectURL(file) {
			let url = null;

			if (window.createObjectURL !== undefined) {
				// basic

				url = window.createObjectURL(file);
			} else if (window.URL !== undefined) {
				// mozilla(firefox)

				url = window.URL.createObjectURL(file);
			} else if (window.webkitURL !== undefined) {
				// webkit or chrome

				url = window.webkitURL.createObjectURL(file);
			}

			return url;
		}
	}
};
</script>

<style>
.aiWrap {
	margin-top: 1.25rem;
	height: auto;
	color: white;
	font-weight: 700;
}
.aiInWrap {
	width: 48%;
	height: 2.625rem;
	background-color: #518cff;
	border-radius: 0.3125rem;
}
.aiOutWrap {
	width: 48%;
	height: 2.625rem;
	background-color: #ea8925;
	border-radius: 0.3125rem;
}

#topName {
	height: 2.625rem;
	width: 100%;
	margin-top: 0.625rem;
}
</style>
