<script>
var page = 1;
var pagesize = 10;
export default {
	data() {
		return {
			loading: false,
			finished: false,
			refreshing: false,
			listData: []
		};
	},
	created: function() {
		page = 1;
	},
	methods: {
		btnBack: function() {
			this.$router.go(-1);
		},
		onLoad() {
			if (this.refreshing) {
				this.listData = [];
				this.refreshing = false;
			}
			this.getRecord();
		},
		onRefresh() {
			// 清空列表数据
			this.finished = false;
			page = 1;
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.refreshing = true;
			this.loading = true;
			this.onLoad();
		},
		getRecord: function() {
			var _this = this;
			_this.postApi(
				'api/tradeCoin/list.html',
				{
					page: page,
					pagesize: pagesize,
					type: '0'
				},
				function(res) {
					_this.listData = _this.listData.concat(res.data);
					page++;
					_this.loading = false;
					if (res.data.length == 0) {
						_this.finished = true;
					}
				}
			);
		}
	}
};
</script>

