<template>
	<div class="divColumn">
		<div style="position: fixed;width: 100%;background-color: white;" class="divColumn">
			<div class="divColumn">
				<div style="width: 100%;position: relative;margin-top: 0.625rem;" class="divCenter">
					<img src="../../assets/img/ex_back.png" style="width: 1.5rem;position: absolute;left: 0.625rem;" @click="btnBack" />
					<span style="font-size: 1.125rem;font-weight: 600;">处理申请</span>
				</div>
			</div>
		</div>
		<div style="margin-top: 4.2rem;text-align: left;" class="divColumn">
			<div style="width: 100%;font-weight: 800;" class="leftAndRight">
				<span style="margin-left: 1rem;">账号：{{ name }}</span>
				<span style="margin-right: 1rem;">数量：{{ Number(paymoney).toFixed(4) }}</span>
			</div>
			<div style="margin: 0rem 1rem;height: 3rem;background-color:#EEEEEE;border-radius: 0.3125rem;margin-top: 1.25rem;" class="divRow divColumnCenter">
				<input
					readonly="true"
					placeholder="请输入钱包地址"
					style="background: none;padding: 0rem;margin: 0rem;margin-left: 0.625rem;font-weight: 800;width: 100%;"
					v-model="address"
				/>
			</div>
			<span style="margin-left: 1rem;color: #999999;margin-top: 0.625rem;">钱包地址</span>
			<div style="margin: 0rem 1rem;height: 3rem;background-color:#EEEEEE;border-radius: 0.3125rem;margin-top: 1.25rem;" class="divRow divColumnCenter">
				<input placeholder="请输入交易密码" style="background: none;padding: 0rem;margin: 0rem;margin-left: 0.625rem;font-weight: 800;width: 100%;" v-model="pwd" type="password" />
			</div>
			<!-- <span style="margin-left: 1rem;color: #999999;margin-top: 0.625rem;">钱包私钥</span> -->
			<div style="width: 100%;margin-top: 3.125rem;" class="divCenter">
				<div style="width: 50%;height: 3rem;background-color: #4884F6;color: white;border-radius: 1.875rem;" @click="sureCheck" class="divCenter">确认</div>
			</div>
		</div>
	</div>
</template>

<script>
import JCCExchange from 'jcc_exchange';
export default {
	data() {
		return {
			name: '',
			address: '',
			paymoney: '',
			sysAddress: '',
			serect: '',
			id_: '',
			walData: {}, //钱包信息
			pwd: ''
		};
	},
	created: function() {
		var _this = this;
		this.name = this.$route.query.name;
		this.address = this.$route.query.address;
		this.paymoney = this.$route.query.paymoney;
		this.id_ = this.$route.query.id_;
		_this.walData = _this.getWallent();
		_this.sysAddress = _this.walData.address;
		console.log(JSON.stringify(_this.walData));
		_this.serect = _this.walData.secret;
	},
	methods: {
		btnBack: function() {
			this.$router.go(-1);
		},
		sureCheck: function() {
			var _this = this;
			if (_this.pwd == '') {
				_this.$toast('请输入交易密码');
				return;
			}
			if (_this.pwd.trim() != _this.walData.pwd) {
				_this.$toast.fail(_this.$t('message.e66'));
				_this.pwd = '';
				return;
			}
			_this.post(_this.gloabal.baseApi + _this.getInfoData().exHosts[0] + '/exchange/balances/' + _this.address, {}, function(res) {
				console.log(JSON.stringify(res));
				var count = 0;
				for (var i = 0; i < res.data.length; i++) {
					var item = res.data[i];
					if (item.currency == 'JACC') {
						count = Number(item.value) - Number(item.freezed);
					}
				}

				if (Number(_this.paymoney) > Number(count)) {
					//账户余额不足
					_this.$toast.fail('账户余额不足！');
					return;
				}
				_this.check();
			});
		},
		async check() {
			var _this = this;
			_this.$toast.loading({
				forbidClick: true,
				duration: 0
			});
			try {
				JCCExchange.init(_this.getInfoData().jcNodes);
				var address = _this.sysAddress; //转出钱包地址
				var secret = _this.serect; //转出钱包秘钥
				var amount = _this.paymoney; //交易数量
				var memo = '提币'; //描述
				var to = _this.address; //互转到的系统钱包地址
				var token = 'JACC'; //需要转的币种
				const issuer = 'jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or'; // the default value is "jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or"
				var hash = await JCCExchange.transfer(address, secret, amount, memo, to, token, issuer);
				console.log(hash);
				if (hash) {
					//互转成功
					_this.$toast.success('转账成功！');
					_this.successCheck(hash);
				} else {
					_this.$toast.fail('转账失败');
					_this.$toast.clear();
				}
			} catch (e) {
				//TODO handle the exceptio
				console.log(e);
			}
		},
		successCheck: function(hash) {
			/* Withdraw/confirmPay.html  id, sysaddress, hash  这个是支付完成，提交hash */
			var _this = this;
			_this.postApi(
				'api/Withdraw/confirmPay.html',
				{
					id: _this.id_,
					sysaddress: _this.sysAddress,
					hash: hash
				},
				function(res) {
					_this.$toast.clear();
					console.log(JSON.stringify(res));
				}
			);
		}
	}
};
</script>

<style></style>
