<template>
	<!-- 划转 -->
	<div class="divColumn">
		<div style="margin: 0rem 0.625rem;margin-top: 1.25rem;" class="divColumn">
			<img src="../../assets/img/ex_back.png" style="width: 1.5rem;" @click="btnBack" />
			<div style="width: 100%;color: #3B79F4;text-align: left;"><h2>划转</h2></div>
			<div style="width: 100%;height: 8.125rem;color: white;position: relative;" :style="topBk" class="divColumn">
				<span style="position: absolute;left: 0.625rem;top: 10%;">钱包ACC资产：</span>
				<span style="position: absolute;left: 0.625rem;top: 40%;font-size: 1.25rem;">{{ Number(currencyData.value).toFixed(6) }}</span>
			</div>
			<div style="width: 100%;">
				<div @click="itemClick()" class="asWrapUn divCenter" :class="{ asWrapChoi: isChoice }" v-for="(item, index) in priList" :key="index">{{ item }}</div>
			</div>
			<div style="width: 100%;text-align: left;margin-top: 0.25rem;color: #999999;"><span>旷工费：0.0002AICC</span></div>

			<div class="divCenter tsBtnWrap" :class="{ tsBtnWrapChoi: isChoice }" @click="sureTrans">确定</div>
		</div>
		<van-dialog
			v-model="showPwd"
			:title="$t('message.b48')"
			show-cancel-button
			@confirm="sureBtn"
			:confirm-button-text="$t('message.b4')"
			:cancel-button-text="$t('message.b5')"
		>
			<div style="width: 100%;height: 3.625rem;" class="divColumn">
				<div style="margin: 0.875rem;"><van-field style="background-color:#f6f8fe;" v-model="pwd" :placeholder="$t('message.b30')" type="password"></van-field></div>
			</div>
		</van-dialog>
	</div>
</template>

<script>
import JCCExchange from 'jcc_exchange';
export default {
	data() {
		return {
			topBk: {
				backgroundImage: 'url(' + require('../../assets/img/hz_img.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			priList: ['300'],
			showPwd: false,
			pwd: '',
			isChoice: false,
			address: '', //转出的钱包地址
			sysAddress: '', //系统收币钱包地址
			title: 'SWT', //需要获取到的当前币的余额
			currencyData: {},
			walData: {} //钱包信息
		};
	},
	created: function() {
		//判断当前是否有那么多资产
		var _this = this;
		_this.walData = _this.getWallent();
		_this.getWallentBal(function(res) {
			console.log(JSON.stringify(res));
			for (var i = 0; i < res.data.length; i++) {
				if (_this.title == res.data[i].title) {
					_this.currencyData = res.data[i];
				}
			}
		});
	},
	methods: {
		btnBack: function() {
			this.$router.go(-1);
		},
		async sureBtn() {
			//确认划转
			var _this = this;
			try {
				JCCExchange.init(_this.getInfoData().jcNodes);
				var address = _this.address; //转出钱包地址
				var secret = _this.walData.secret; //转出钱包秘钥
				var amount = _this.priList[0]; //交易数量
				var memo = '互转'; //描述
				var to = _this.sysAddress; //互转到的系统钱包地址
				var token = _this.title; //需要转的币种
				const issuer = 'jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or'; // the default value is "jGa9J9TkqtBcUoHe2zqhVFFbgUVED6o9or"
				var hash = await JCCExchange.transfer(address, secret, amount, memo, to, token, issuer);
				console.log(hash);
				if (hash) {
					//互转成功
					_this.$toast.success('互转成功！');
				} else {
					_this.$toast.fail('划转失败');
				}
			} catch (e) {
				//TODO handle the exception
				console.log(e);
			}
		},
		sureTrans: function() {
			console.log(JSON.stringify(this.getInfoData()));
			// if (this.currencyData.value < 300) {
			// 	this.$toast('当前资产不足300！');
			// 	return;
			// }
			this.showPwd = true;
		},
		itemClick: function() {
			this.isChoice = true;
		},
		// 获取当前钱包资产 已绑定的AICC钱包为列
		getWallentdata: function() {}
	}
};
</script>

<style>
.asWrapUn {
	width: 6.25rem;
	height: 2.625rem;
	border: 0.0625rem solid #bdbdbd;
	border-radius: 0.3125rem;
	margin-top: 1rem;
	margin-right: 0.625rem;
	float: left;
	display: flex;
	color: #bdbdbd;
	font-size: 1.125rem;
}
.asWrapChoi {
	border: 0.0625rem solid #538df7;
	color: #427ff5;
}
.tsBtnWrap {
	width: 100%;
	height: 3.125rem;
	background-color: #bdbdbd;
	border-radius: 0.3125rem;
	margin-top: 3.125rem;
	color: white;
}
.tsBtnWrapChoi {
	background-color: #427ff5;
}
</style>
