<template>
	<!-- 系统公告 -->
	<div class="divColumn">
		<Title title="系统公告"></Title>
		<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
			<div class="conTop conMagin divColumn">
				<div class="noticWrap" v-for="(item, index) in noticeData" :key="index" @click="itemClick(item.id)">
					<span style="display:block;overflow-wrap:break-word;">{{ item.title }}</span>

					<span style="position: absolute;bottom: 0.625rem;right: 0.625rem;">{{ item.updatedAt }}</span>
				</div>
			</div>
		</van-pull-refresh>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
export default {
	components: {
		Title
	},
	data() {
		return {
			loading: false,
			finished: false,
			refreshing: false,
			list: [],
			noticeData: []
		};
	},
	created: function() {
		var _this = this;
		_this.getNoticeInfo(function(res) {
			_this.noticeData = res.data[0].dataList;
			console.log(JSON.stringify(res));
		});
	},
	methods: {
		onLoad() {
			setTimeout(() => {
				if (this.refreshing) {
					this.list = [];
					this.refreshing = false;
				}
			}, 600);
		},
		onRefresh() {
			// 清空列表数据
			this.finished = false;
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.loading = true;
			this.onLoad();
			var _this = this;
			_this.getNoticeInfo(function(res) {
				_this.noticeData = res.data[0].dataList;
				console.log(JSON.stringify(res));
			});
		},
		itemClick: function(id_) {
			this.$router.push({
				path: '/SystemNotice',
				query: {
					id_: id_
				}
			});
		}
	}
};
</script>

<style>
.noticWrap {
	height: 5rem;
	background-color: #f6f8fe;
	border-radius: 0.625rem;
	position: relative;
	text-align: left;
	padding: 0.625rem;
	margin-top: 0.625rem;
}
</style>
