<template>
	<div>
		<van-uploader capture="camera" :after-read="afterRead"><van-button icon="photo" type="primary">上传文件</van-button></van-uploader>
	</div>
</template>

<script>
import QrCode from 'qrcode-decoder';
export default {
	methods: {
		afterRead(file) {
			this.getUrl(file.file);
		},
		getUrl(file) {
			let qr = new QrCode();
			let url = this.getObjectURL(file);
			qr.decodeFromImage(url).then(res => {
				//打印结果为 解析出来的 二维码地址
				console.log(res.data);
				alert(res.data)
			});
		},
		getObjectURL(file) {
			let url = null;

			if (window.createObjectURL !== undefined) {
				// basic

				url = window.createObjectURL(file);
			} else if (window.URL !== undefined) {
				// mozilla(firefox)

				url = window.URL.createObjectURL(file);
			} else if (window.webkitURL !== undefined) {
				// webkit or chrome

				url = window.webkitURL.createObjectURL(file);
			}

			return url;
		}
	}
};
</script>
