<template>
	<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
		<div class="divColumn">
			<div style="width: 100%;height: 15rem;position: fixed;color: white;z-index: 99;" :style="topBkN" class="divCenter divColumn">
				<div style="position: absolute;width: 100%;top: 1.25rem;" class="leftAndRight">
					<img src="../../assets/img/back.png" style="width: 1.5rem;width: 1.5rem;margin-left: 0.625rem;" @click="btnBack" />
					<div class="divRow divColumnCenter" style="margin-right: 0.625rem;" @click="design">
						<span>{{ $t('message.e6') }}</span>
						<img src="../../assets/img/ex_design.png" style="width: 1.125rem;margin-left: 0.25rem;" />
					</div>
				</div>
				<span style="font-size: 1.125rem;position: absolute;top: 1.25rem;font-weight: 800;">{{ $t('message.e7') }}</span>
				<div style="position: absolute;width: 100%;top: 4rem;" class="leftAndRight">
					<div class="divRow divColumnCenter" style="margin-left: 0.625rem;">
						<img src="../../assets/img/ac_img.png" style="width: 1.5rem;margin-right: 0.625rem;" />
						<span>{{ $t('message.e8') }}：{{ user.name }}</span>
					</div>
					<div class="divRow divColumnCenter" style="margin-right: 0.625rem;" @click="zdRecord">
						<!-- <img src="../../assets/img/zc_de_img.png" style="width: 4.5rem;margin-left: 0.25rem;" /> -->
						<div style="width: 5.625rem;height: 1.75rem;border: 0.0625rem solid #FFFFFF;border-radius: 1.875rem;" class="divCenter">{{ $t('message.e32') }}</div>
					</div>
				</div>
				<span style="font-size: 1rem;margin-top: 2rem;font-weight: 800;">{{ $t('message.e9') }}(ACC)</span>
				<span style="margin-top: 0.625rem;font-weight: 800;font-size: 1.5rem;">{{ Number(user.balance_1_1).toFixed(4) }}</span>
				<div v-show="user.isadmin != 1" style="width: 100%;position: absolute;bottom: 10%;" class="divCenter">
					<div class="divCenter roungWrap" style="width: 6rem;" @click="transfer">{{ $t('message.e10') }}</div>
				</div>
				<div class="divCenter roungWrap" v-show="user.isadmin == 1" style="position: absolute;left: 16%;bottom: 10%;width: 6rem;" @click="transfer">
					{{ $t('message.e10') }}
				</div>
				<div
					class="divCenter roungWrap"
					v-show="user.isadmin == 1"
					style="margin-left: 1.125rem;color: #77A0FA;position: absolute;right: 16%;bottom: 10%;width: 6rem"
					@click="checkWithdraw"
				>
					{{ $t('message.e11') }}
				</div>
			</div>
			<div style="height: auto;margin: 0rem 0.625rem;padding-bottom: 1rem;margin-top: 15.2rem;" class="divColumn">
				<!-- 具体交易信息 -->
				<div style="width: 100%;height: auto;margin-top: 1.25rem;" class="divRow">
					<!-- 交易 -->
					<div style="width: 52%;height: auto;" class="divColumn">
						<div style="width: 100%;" class="divRow divColumnCenter">
							<div class="divCenter typeWrap" :class="{ typeWrapChoiBuy: type == 1 }" @click="buy">{{ $t('message.a31') }}</div>
							<div style="width: 4%;"></div>
							<div class="divCenter typeWrap" :class="{ typeWrapChoiSell: type == 2 }" @click="sell">{{ $t('message.c74') }}</div>
						</div>
						<div style="width: 100%;margin-top: 1.25rem;" class="divRow divColumnCenter">
							<div class="divColumn divCenter" @click="exhangeX">
								<span class="exText" :class="{ exTextCho: exType == 1 }">{{ $t('message.e12') }}</span>
								<div class="lineEx" v-show="exType == 1"></div>
							</div>
							<div class="divColumn divCenter" style="margin-left: 1.875rem;" @click="exchangeS">
								<span class="exText" :class="{ exTextCho: exType == 2 }">{{ $t('message.e13') }}</span>
								<div class="lineEx" v-show="exType == 2"></div>
							</div>
						</div>
						<!-- 价格 -->
						<div class="leftAndRight itemWrapE">
							<span style="margin-left: 0.625rem;" v-show="exType == 1">0.0</span>
							<span style="margin-left: 0.625rem;" v-show="exType == 2">{{ $t('message.e14') }}</span>
							<!-- <img src="../../assets/img/ex_arrow_img.png" style="width: 0.75rem;margin-right: 0.625rem;" /> -->
						</div>
						<div class="dyWrap">
							<span v-show="exType == 1">=0CNY</span>
							<span v-show="exType == 2" style="font-size: 0.6875rem;">{{ $t('message.e15') }}</span>
						</div>
						<div class="leftAndRight itemWrapE">
							<div class="divRow divColumnCenter">
								<span style="margin-left: 0.625rem;" @click="show = true">{{ count }}</span>
								<img src="../../assets/img/ex_arrow_img.png" style="width: 0.75rem;margin-right: 0.625rem;margin-left: 0.625rem;" />
							</div>
							<div class="divRow divColumnCenter">
								<span @click="showType = true">{{ binName }}</span>
								<img src="../../assets/img/ex_arrow_img.png" style="width: 0.75rem;margin-right: 0.625rem;margin-left: 0.625rem;" />
							</div>
						</div>
						<div class="dyWrap">
							<span>可用：{{ Number(user.balance_1_1).toFixed(4) }}ACC</span>
						</div>
						<div style="margin: 1.5rem 0rem;z-index: 1;">
							<van-slider v-model="value" :step="0.01" active-color="#46F7FF" button-size="20px" @input="onChange" :min="min" :max="max" />
						</div>
						<div class="divCenter btnWrap" :class="{ btnWrapBuy: buySell == 1 && type == 1 && exType == 2 && binName == 'EQC' }" @click="sureExchange">
							{{ btnText }}
						</div>
					</div>
					<div style="width: 4%;"></div>
					<!-- 深度 -->
					<div style="width: 44%;height: auto;" class="divColumn">
						<div style="width: 100%;color: #666666;font-size: 0.8125rem;margin-bottom: 0.625rem;" class="leftAndRight">
							<span>{{ $t('message.e16') }}(ACC)</span>
							<span>{{ $t('message.a41') }}(EQC)</span>
						</div>
						<!-- 卖出 -->
						<div style="width: 100%;" class="divColumn">
							<div class="leftAndRight itemDepWrap" v-for="(item, index) in pan2" :key="index">
								<span style="color: #FF4C4C;">{{ item.price_pairs }}</span>
								<span>{{ item.left_count }}</span>
							</div>
						</div>
						<div style="width: 100%;text-align: left;margin: 0.625rem 0rem;">
							<span style="color: #0AC52C;font-weight: 800;">{{cnyPrice}} CNY</span>
						</div>
						<!-- 买入 -->
						<div style="width: 100%;margin-top: 0.625rem;" class="divColumn">
							<div class="leftAndRight itemDepWrap" v-for="(item, index) in pan1" :key="index">
								<span style="color: #0AC52C;">{{ item.price_pairs }}</span>
								<span>{{ item.left_count }}</span>
							</div>
						</div>
					</div>
				</div>
			</div>
			<!-- 委托信息 -->
			<div style="height: 1rem;width: 100%;background-color: #EEEEEE;"></div>
			<div style="width: 100%;" class="divColumn">
				<div style="width: 100%;" class="leftAndRight">
					<div class="divColumn divCenter" style="color: #528CFF;margin-left: 1.125rem;margin-top: 1rem;">
						<span>{{ $t('message.e18') }}</span>
						<div class="recordLine"></div>
					</div>
					<div class="recordWrap divCenter" style="margin-right: 1rem;" @click="exRecord">{{ $t('message.e19') }}</div>
				</div>
				<!-- 空白页 -->
				<div v-show="isRecord" style="width: 100%;height: 6.25rem;color: #999999;" class="divCenter divColumn">
					<img src="../../assets/img/no_record.png" style="width:3.75rem;" />
					<span>{{ $t('message.e20') }}</span>
				</div>
				<!-- 委托列表 -->
				<div style="width: 100%;margin-top: 0.9375rem;padding-bottom: 1.875rem;" class="divColumn">
					<div style="width: 100%;height: 0.0625rem;background-color: #EEEEEE;"></div>
					<div style="height: auto;width: 100%;padding-top: 0.875rem;" class="divColumn" v-for="(item, index) in wtList" :key="index">
						<div style="margin: 0rem 1rem;" class="divColumn">
							<div class="divRow divColumnCenter" style="font-weight: 800;">
								<span style="color: #FF2222;font-size: 1rem;" v-show="item.type == 2">{{ $t('message.e21') }}</span>
								<span style="color: #1FCD33;font-size: 1rem;" v-show="item.type == 1">{{ $t('message.e22') }}</span>
								<span style="margin-top: 0.375rem;margin-left: 0.25rem;">{{ item.pairs_name }}</span>
							</div>
							<div style="width: 100%;font-size: 0.75rem;color: #999999;margin-top: 0.625rem;font-size: 0.875rem;" class="leftAndRight">
								<div class="divColumn" style="text-align: left;width: 30%;">
									<span style="font-weight: 800;color: #666666;margin: 0.625rem 0rem;">{{ $t('message.e31') }}</span>
									<span>{{ item.create_time }}</span>
									<span style="font-weight: 800;color: #666666;margin: 0.625rem 0rem;">{{ $t('message.e23') }}</span>
									<span>{{ Number(item.count) - Number(item.left_count) }}</span>
								</div>
								<div class="divColumn" style="text-align: center;">
									<span style="font-weight: 800;color: #666666;margin: 0.625rem 0rem;">{{ $t('message.e24') }}</span>
									<span>{{ item.price }}</span>
									<span style="font-weight: 800;color: #666666;margin: 0.625rem 0rem;">{{ $t('message.e25') }}</span>
									<span>{{ item.price_pairs }}</span>
								</div>
								<div class="divColumn" style="text-align: right;width: 30%;">
									<span style="font-weight: 800;color: #666666;margin: 0.625rem 0rem;">{{ $t('message.e26') }}</span>
									<span>{{ item.count }}</span>
									<span style="font-weight: 800;color: #666666;margin: 0.625rem 0rem;">{{ $t('message.e27') }}</span>
									<span style="color: #17B76A;" v-show="item.status == 1">{{ $t('message.e28') }}</span>
									<span style="color: #17B76A;" v-show="item.status == 2">{{ $t('message.e29') }}</span>
									<span style="color: #17B76A;" v-show="item.status == 3">{{ $t('message.e30') }}</span>
								</div>
							</div>
						</div>
						<div style="height: 0.0625rem;width: 100%;background-color: #EEEEEE;margin-top: 1.125rem;"></div>
					</div>
				</div>
			</div>
			<van-action-sheet v-model="show" :actions="actions" @select="onSelect" :cancel-text="$t('message.a11')" />
			<van-action-sheet v-model="showType" :actions="actionType" @select="onSelectType" :cancel-text="$t('message.a11')" />
			<van-dialog
				v-model="showPwd"
				:title="$t('message.b48')"
				show-cancel-button
				@confirm="sureBtn"
				:confirm-button-text="$t('message.b4')"
				:cancel-button-text="$t('message.b5')"
			>
				<div style="width: 100%;height: 3.625rem;" class="divColumn">
					<div style="margin: 0.875rem;"><van-field style="background-color:#f6f8fe;" v-model="pwd" :placeholder="$t('message.b30')" type="password"></van-field></div>
				</div>
			</van-dialog>
		</div>
	</van-pull-refresh>
</template>

<script>
export default {
	data() {
		return {
			topBk: {
				backgroundImage: 'url(' + require('../../assets/img/ex_bk.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			topBkN: {
				backgroundImage: 'url(' + require('../../assets/img/exja_bk.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			type: 1, //1 买入 2 卖出
			exType: 1, // 限价 2 市价
			min: 0,
			max: 100,
			value: 60,
			buySell: 1, //1 买 2 卖
			btnText: this.$t('message.c72'),
			wtList: [],
			isRecord: false,
			show: false,
			actions: [{ name: '300' }],
			showType: false,
			actionType: [{ name: 'EQC' }, { name: 'IQC' }],
			showPwd: false,
			pwd: '',
			loading: false,
			finished: false,
			refreshing: false,
			user: {},
			count: '300',
			binName: 'EQC',
			pan1: [], // 买
			pan2: [] ,//卖
			cnyPrice:0
		};
	},
	created: function() {
		this.getData();
	},
	methods: {
		getData: function() {
			var _this = this;
			this.upDataUser(function(res) {
				console.log(JSON.stringify(res));
				_this.user = res;
			});
			// 获取交易价格
			_this.postApi('api/Tradecoin/tradePairs.html', {}, function(res) {
				console.log(JSON.stringify(res));
			});
			// 获取系统资产
			_this.postApi('api/user/balances.html', {}, function(res) {
				console.log(JSON.stringify(res));
			});

			_this.postApi(
				'api/tradeCoin/list.html',
				{
					page: 1,
					pagesize: 5,
					type: '0'
				},
				function(res) {
					_this.wtList = res.data;
					console.log(JSON.stringify(res));
				}
			);
			// 获取交易盘
			_this.postApi('api/tradecoin/depth.html', {}, function(res) {
				_this.pan1 = res.data.data1;
				_this.pan2 = res.data.data2;
				_this.cnyPrice=  res.data.price
			});
		},
		// 获取用户ACC数据
		btnBack: function() {
			var type = this.$route.query.type;
			if (type == 1) {
				this.$router.go(-1);
			} else {
				this.$router.go(-3);
			}
		},
		buy: function() {
			var _this = this;
			_this.type = 1;
			_this.btnText = _this.$t('message.c72');
		},
		sell: function() {
			var _this = this;
			_this.type = 2;
			_this.btnText = _this.$t('message.c74');
		},
		exhangeX: function() {
			this.exType = 1;
		},
		exchangeS: function() {
			this.exType = 2;
		},
		onSelect(item) {
			this.show = false;
			this.count = item.name;
		},
		onSelectType(item) {
			this.showType = false;
			this.binName = item.name;
		},
		transfer: function() {
			this.$router.push('/TransferNew');
		},
		checkWithdraw: function() {
			this.$router.push('/CheckWithdraw');
		},
		exRecord: function() {
			this.$router.push('/ExRecord');
		},
		zdRecord: function() {
			this.$router.push('/ZdRecord');
		},
		design: function() {
			this.$router.push('/ExDesign');
		},
		onChange: function() {},
		sureExchange: function() {
			if (this.buySell == 1 && this.type == 1 && this.exType == 2 && this.binName == 'EQC') {
				this.showPwd = true;
			}
		},
		sureBtn: function() {
			var _this = this;
			if (this.type == 1) {
				//买
				/**
			 * 
			   * Trade/sale.html  pairs_id 交易对id   tradepassword 交易密码  tradecount  数量  price 价格  卖出交易》
Trade/buy.html  pairs_id 交易对id   tradepassword 交易密码  买入交易
@刘荣芳 

*/
				var dataBuy = {
					pairs_id: 1,
					tradepassword: _this.pwd
				};
				_this.postApi('api/Tradecoin/buy.html', dataBuy, function(res) {
					console.log(JSON.stringify(res));
					_this.getData();
				});
			}
		},
		onLoad() {
			setTimeout(() => {
				if (this.refreshing) {
					this.refreshing = false;
				}
			}, 600);
		},
		onRefresh() {
			// 清空列表数据
			this.finished = false;
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.loading = true;
			this.onLoad();
			this.getData();
		}
	}
};
</script>

<style>
.recordLine {
	width: 100%;
	height: 0.0625rem;
	background-color: #528cff;
	margin-top: 0.1875rem;
}
.recordWrap {
	width: 5rem;
	height: 1.4rem;
	border: 0.0625rem solid #666666;
	border-radius: 1.875rem;
	color: #666666;
	font-size: 0.75rem;
	margin-top: 1rem;
}

.btnWrap {
	width: 100%;
	height: 2.25rem;
	border-radius: 0.3125rem;
	background-color: #999999;
	color: white;
}
.btnWrapBuy {
	background-color: #00b577;
}
.dyWrap {
	width: 100%;
	text-align: left;
	font-size: 0.8125rem;
	margin-top: 0.25rem;
	color: #8e8e8e;
}

.itemWrapE {
	height: 2.25rem;
	width: 100%;
	border: 0.0625rem solid #49c4ff;
	border-radius: 0.3125rem;
	margin-top: 1rem;
}
.roungWrap {
	width: 5.2rem;
	height: 2rem;
	background-color: white;
	border-radius: 1.875rem;
	color: #7267f7;
}
.typeWrap {
	width: 48%;
	height: 2rem;
	background-color: #bdbdbd;
	color: white;
	border-radius: 0.3125rem;
}
.typeWrapChoiBuy {
	background-color: #00b577;
}
.typeWrapChoiSell {
	background-color: #f53636;
}
.topWrap {
	height: 11rem;
	width: 100%;
	position: relative;
	color: white;
}
.topAsset {
	position: absolute;
	top: 0.625rem;
	left: 0.875rem;
}
.itemDepWrap {
	height: 1.4rem;
	width: 100%;
	font-size: 0.75rem;
	font-weight: 500;
}
.exText {
	color: #999999;
}
.exTextCho {
	color: #49c4ff;
}
.lineEx {
	height: 0.125rem;
	width: 100%;
	background-color: #49c4ff;
}
</style>
