<template>
	<!-- 行情 -->
	<div class="divColumn">
		<div class="tabTittleWrap divCenter">
			<span class="spanTitle">{{ $t('message.a22') }}</span>
		</div>
		<div style="height: 2rem;position: fixed;top: 3rem;width: 100%;background-color: white;z-index: 9999;">
			<div style="margin: 0rem 0.625rem;height: 100%;position: relative;font-weight: bold;background-color: white;" class="divCenter">
				<span class="itemL">{{ $t('message.a23') }}</span>
				<span class="itemLc">{{ $t('message.a24') }}</span>
				<span class="itemRc">{{ $t('message.a25') }}</span>
				<span class="itemR">{{ $t('message.a26') }}</span>
			</div>
		</div>
		<div style="margin-top: 5rem;width: 100%;" class="divColumn">
			<div class="divColumn" style="width: 100%;" v-for="(item, index) in qouteList" :key="index">
				<div style="height: 0.0625rem;background-color: #F0F0F0;width: 100%;margin-bottom: 0.25rem;"></div>
				<div style="height: 2.625rem;position: relative;margin: 0rem 0.625rem;" class="divCenter">
					<div class="itemL divColumn" style=";text-align: left;">
						<div class="divRow">
							<span style="font-weight: bold;" v-show="item.name == 'SWT'">AICC</span>
							<span style="font-weight: bold;" v-show="item.name != 'SWT'">{{(item.name).replace('J','')}}</span>
							<span style="color: #999999;font-size: 0.75rem;margin-top: 0.125rem;">/USDT</span>
							<!-- <span style="color: #999999;font-size: 0.75rem;margin-top: 0.125rem;" v-show="item.pai!='SWT'">/{{ item.pai }}</span> -->
						</div>
						<span style="font-size: 0.75rem;">{{ $t('message.a27') }} {{ Number(item.count).toFixed(2) }}</span>
					</div>
					<div class="itemLc divColumn" style="text-align: left;">
						<span style="font-weight: bold;">{{ Number(item.price).toFixed(6) }}</span>
						<span style="font-size: 0.75rem;color: #999999;">≈ ¥ {{ Number((Number(item.price).toFixed(4) * 7.0898).toFixed(4)).toFixed(4) }}</span>
					</div>
					<div class="itemRc" style="font-weight: bold;">
						<span style="color: red;" v-show="item.zt < 0">{{ item.zt }}%</span>
						<span v-show="item.zt > 0" style="color: #07C160;">+{{ item.zt }}%</span>
						<span v-show="item.zt == 0" style="color: #999999;">{{ item.zt }}%</span>
					</div>
					<div class="itemR"><img src="../../assets/img/kx_img.png" style="width: 1.25rem;" /></div>
				</div>
			</div>
		</div>
		<div style="height: 3.75rem;width: 100%;"></div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			qouteList: []
		};
	},
	created: function() {
		var _this = this;
		_this.getQueteData();
	},
	methods: {
		getQueteData: function() {
			var _this = this;
			//获取行情
			_this.post(_this.gloabal.baseApi + _this.getInfoData().infoHosts[0] + '/info/alltickers', {}, function(res) {
				//遍历对象
				for (var key in res.data) {
					var pair = key.split('-');
					// 	日涨跌(大于0表示涨 小于0表示跌 换算成百分比要除100)
					var zt = 0.0;
					if (res.data[key].length > 2 && Number(res.data[key][2]).toFixed(0) != 0) {
						zt = Number(res.data[key][2]) / 100;
					}
					var data = {
						name: pair[0],
						pai: pair[1],
						count: res.data[key].length > 6 ? res.data[key][6] : 0,
						price: res.data[key][1],
						zt: zt.toFixed(2)
					};
					// if ((pair[0] === 'SWT' && pair[1] === 'JUSDT') || (pair[0] === 'JETH' && pair[1] === 'JUSDT') || (pair[0] === 'JXRP' && pair[1] === 'JUSDT')) {
					// 	_this.qouteList.push(data);
					// }
					if (pair[1] === 'JUSDT') {
						_this.qouteList.push(data);
					}
				}
			});
		}
	}
};
</script>

<style>
.itemL {
	position: absolute;
	left: 1%;
}
.itemLc {
	position: absolute;
	left: 29%;
}
.itemRc {
	position: absolute;
	right: 29%;
}
.itemR {
	position: absolute;
	right: 1%;
}
</style>
