<template>
	<div class="divColumn">
		<div class="tabTittleWrap divCenter">
			<span class="spanTitle">AICOIN</span>
			<img src="../../assets/img/home_share.png" class="navRightImg" />
		</div>
		<div style="margin-top: 3rem;" class="divColumn">
			<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
				<van-list v-model="loading" :finished="finished" :finished-text="$t('message.a13')" :loading-text="$t('message.b6')" @load="onLoad">
					<!-- 头部上半部分 -->
					<div style="margin: 0rem 0.625rem;">
						<van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
							<van-swipe-item><img class="banImg" src="../../assets/img/ban1.png" /></van-swipe-item>
							<van-swipe-item><img class="banImg" src="../../assets/img/ban2.png" /></van-swipe-item>
							<van-swipe-item><img class="banImg" src="../../assets/img/ban3.png" /></van-swipe-item>
							<van-swipe-item><img class="banImg" src="../../assets/img/ban4.png" /></van-swipe-item>
						</van-swipe>
						<!-- 公告 -->
						<div style="height: 1.875rem;width: 100%;" class="divRow divColumnCenter" @click="toNotic">
							<div style="width: 10%;height: 100%;" class="divColumnCenter"><img src="../../assets/img/note_img.png" style="width: 60%;" /></div>
							<van-notice-bar :text="notic.title" class="noteBarWrap" />
						</div>
						<van-grid square :border="false">
							<van-grid-item v-for="(item, index) in hList" :key="index" @click="itemClickH(index)">
								<img :src="item.icon" style="width: 1.5rem;margin-bottom: 0.625rem;" />
								<span style="font-size: 0.75rem;">{{ item.name }}</span>
							</van-grid-item>
						</van-grid>
					</div>
					<div style="height: 0.625rem;width: 100%;background-color: #F5F5F5;"></div>
					<!-- 行情 -->
					<div style="margin: 0rem 1rem;">
						<div style="height: 2.625rem;width: 100%;position: relative;font-size: 0.8125rem;font-weight: bold;" class="divCenter">
							<span style="position: absolute;left: 0rem;">{{ $t('message.a19') }}</span>
							<span>{{ $t('message.a20') }}</span>
							<span style="position: absolute;right: 0rem;">{{ $t('message.a21') }}</span>
						</div>
					</div>
					<div style="width: 100%;" class="divColumn">
						<div class="divColumn" v-for="(item, index) in qouteList" :key="index">
							<div style="height: 0.0625rem;width: 100%;background-color: #F0F0F0;"></div>
							<div style="height: 3rem;width: 100%;position: relative;" class="divCenter">
								<div style="position: absolute;left: 1rem;text-align: left;" class="divColumn">
									<div>
										<span style="font-weight: bold;font-size: 0.75rem;" v-show="item.name == 'SWT'">AICC</span>
										<span style="font-weight: bold;font-size: 0.75rem;" v-show="item.name != 'SWT'">{{ item.name.replace('J', '') }}</span>

										<span style="font-size: 0.625rem;color: #999999;">/USDT</span>
										<!-- <span style="font-size: 0.625rem;color: #999999;"  v-show="item.pai!='SWT'">/{{ item.pai }}</span> -->
									</div>
									<div style="font-size: 0.625rem;">
										<span>24h{{ $t('message.a27') }}</span>
										<span style="margin-left: 0.125rem;">{{ Number(item.count).toFixed(2) }}</span>
									</div>
								</div>
								<div class="divColumn">
									<span style="font-weight: bold;font-size: 0.75rem;">{{ Number(item.price).toFixed(6) }}</span>
									<span style="font-size: 0.75rem;">≈¥ {{ (Number(item.price).toFixed(4) * 7.0898).toFixed(6) }}</span>
								</div>
								<div style="position: absolute;right: 1rem;">
									<div class="zdWrap up" v-show="item.zt < 0">{{ item.zt }}%</div>
									<div class="zdWrap" v-show="item.zt > 0 || item.zt == 0">+{{ item.zt }}%</div>
								</div>
							</div>
						</div>
					</div>
				</van-list>
			</van-pull-refresh>
		</div>
		<div style="height: 4.25rem;width: 100%;"></div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			hList: [
				{
					name: this.$t('message.a14'),
					icon: require('../../assets/img/h1.png'),
					url: ''
					// https://devaiccotc.jccdex.cn/?t=1589788796401#/home/market/3/2
				},
				{
					name: this.$t('message.a15'),
					icon: require('../../assets/img/h2.png'),
					// url: '/StrategeOrder',
					url: ''
				},
				{
					name: this.$t('message.a16'),
					icon: require('../../assets/img/h3.png'),
					url: ''
				},
				{
					name: this.$t('message.a17'),
					icon: require('../../assets/img/h4.png'),
					url: '/CustServe'
				}
			],
			loading: false,
			finished: false,
			refreshing: false,
			list: [],
			qouteList: [],
			notic: {}
		};
	},
	created: function() {
		var _this = this;
		//获取行情
		_this.post(_this.gloabal.baseApi + _this.getInfoData().infoHosts[0] + '/info/alltickers', {}, function(res) {
			console.log("====="+JSON.stringify(res))
			//遍历对象
			for (var key in res.data) {
				var pair = key.split('-');
				// 	日涨跌(大于0表示涨 小于0表示跌 换算成百分比要除100)
				var zt = 0.0;
				if (res.data[key].length > 2 && Number(res.data[key][2]).toFixed(0) != 0) {
					zt = Number(res.data[key][2]) / 100;
				}
				var data = {
					name: pair[0],
					pai: pair[1],
					count: res.data[key].length > 6 ? res.data[key][6] : 0,
					price: res.data[key][1],
					zt: zt.toFixed(2)
				};
				// if ((pair[0] === 'SWT' && pair[1] === 'JUSDT') || (pair[0] === 'JETH' && pair[1] === 'JUSDT') || (pair[0] === 'JXRP' && pair[1] === 'JUSDT')) {
				// 	_this.qouteList.push(data);
				// }

				if (pair[1] === 'JUSDT') {
					_this.qouteList.push(data);
				}
				console.log(key + '======' + res.data[key][0]);
			}
			_this.qouteList.sort(_this.compare('zt'));
		});
		_this.getNoticeInfo(function(res) {
			if (res.data[0].dataList.length > 0) {
				_this.notic = res.data[0].dataList[0];
			}
			console.log(JSON.stringify(res));
		});
	},
	methods: {
		onLoad() {
			setTimeout(() => {
				if (this.refreshing) {
					this.list = [];
					this.refreshing = false;
				}

				for (let i = 0; i < 10; i++) {
					this.list.push(this.list.length + 1);
				}
				this.loading = false;

				if (this.list.length >= 40) {
					this.finished = true;
				}
			}, 300);
		},
		onRefresh() {
			// 清空列表数据
			this.finished = false;
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.loading = true;
			this.onLoad();
		},
		toNotic: function() {
			var _this = this;
			this.$router.push({
				path: '/SystemNotice',
				query: {
					id_: _this.notic.id
				}
			});
		},
		itemClickH: function(index) {
			var _this = this;
			// if (index == 0) {
			// 	window.location.href = _this.hList[index].url;
			// } else {
			var url = _this.hList[index].url;
			if (url == '') {
				_this.$toast.fail(_this.$t('message.a18'));
			} else {
				_this.$router.push(url);
			}
			// }
		},
		compare: function(property) {
			return function(a, b) {
				var value1 = a[property];
				var value2 = b[property];
				return value2 - value1;
			};
		}
	}
};
</script>

<style>
.tabTittleWrap {
	height: 3rem;
	background-color: white;
	position: fixed;
	z-index: 999999;
	width: 100%;
}
.my-swipe .van-swipe-item {
	color: #fff;
	font-size: 20px;
	text-align: center;
	background-color: white;
}
.banImg {
	width: 100%;
	height: 10rem;
	object-fit: cover;
	border-radius: 0.3125rem;
}
.noteBarWrap {
	width: 90%;
	margin-left: -2%;
	background-color: white;
	font-size: 0.875rem;
	color: black;
}
.zdWrap {
	padding: 0.25rem 0.625rem;
	color: white;
	border-radius: 0.3125rem;
	background: #07c160;
}
.down {
	background: #07c160;
}

.up {
	background: red;
}
</style>
