<template>
	<!-- 创建钱包和秘钥 -->
	<div class="divColumn">
		<Title :title="$t('message.b95')"></Title>
		<div class="conTop divColumn">
			<div style="margin: 0rem 1rem;margin-top: 1rem;text-align: left;" class="divColumn">
				<span>{{ $t('message.b70') }}：{{ walName }}</span>
				<p style="text-indent:2em;">
					{{ $t('message.b96') }}
					<span style="color: #FD6800;">{{ $t('message.b97') }}</span>
					{{ $t('message.b98') }}
				</p>
				<span style="margin-top: 1.25rem;">{{ $t('message.b99') }}：</span>
				<div class="divColumnCenter walItmeW">{{ walData.address }}</div>
				<span style="margin-top: 1.25rem;">{{ $t('message.b100') }}：</span>
				<div class="divColumnCenter walItmeW">{{ walData.secret }}</div>
				<div class="divCenter sureBtnWrap" @click="copy">{{ $t('message.c1') }}</div>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
import { jtWallet } from 'jcc_wallet';
export default {
	components: {
		Title
	},
	data() {
		return {
			walData: {},
			type: 1, // 1 创建aicoin钱包，2 创建瑞波链钱包
			walName: ''
		};
	},
	created: function() {
		//创建钱包
		var _this = this;
		_this.type = _this.$route.query.type;
		if (_this.type == 1) {
			var t = jtWallet.createWallet('swt');
			_this.walData = t;
			_this.walName = _this.$t('message.c2');
		} else {
			const rippleWallet = require('jcc_wallet').rippleWallet;
			var ro = rippleWallet.createWallet(); //创建瑞波链钱包
			_this.walName = _this.$t('message.b84');
			_this.walData = ro;
		}
	},
	methods: {
		copy: function() {
			var _this = this;
			_this.$router.push({
				path: '/Copy',
				query: {
					address: _this.walData.address,
					secret: _this.walData.secret,
					type: _this.type
				}
			});
		}
	}
};
</script>

<style>
.walItmeW {
	height: 2.625rem;
	background-color: #f6f8fe;
	padding: 0rem 0.625rem;
	border-radius: 0.3125rem;
	margin-top: 1rem;
	font-size: 0.8125rem;
}
</style>
