<template>
	<!-- 复制地址和秘钥 -->
	<div class="divColumn">
		<Title :title="$t('message.c3')"></Title>
		<div class="conTop divColumn conMagin">
			<div class="divColumn divCenter">
				<div id="qrcode" ref="qrcode" style="margin-top: 1.25rem;"></div>
				<span style="margin-top: 1.125rem;">{{ address }}</span>
				<span class="copyWrapC" v-clipboard:copy="address" v-clipboard:success="onCopy">{{ $t('message.b37') }}</span>
			</div>
			<div class="divColumn divCenter">
				<div id="qrcode2" ref="qrcode2" style="margin-top: 1.25rem;"></div>
				<span style="margin-top: 1.125rem;">{{ secret }}</span>
				<span class="copyWrapC" v-clipboard:copy="secret" v-clipboard:success="onCopy">{{ $t('message.b37') }}</span>
			</div>
			<div class="divCenter sureBtnWrap" @click="sureCopy">{{ $t('message.c4') }}</div>
		</div>
		<van-dialog v-model="show" :title="$t('message.b48')" show-cancel-button @confirm="sureCache" 	:confirm-button-text="$t('message.b4')"
			:cancel-button-text="$t('message.b5')">
			<div style="width: 100%;height: 10rem;" class="divColumn">
				<span style="margin: 0.875rem;text-align: left;font-size: 0.8125rem;">{{ $t('message.c5') }}</span>
				<div style="margin: 0.875rem;"><van-field style="background-color:#f6f8fe;" v-model="pwd" :placeholder="$t('message.b30')" type="password"></van-field></div>
			</div>
		</van-dialog>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
import QRCode from 'qrcodejs2';
export default {
	components: {
		Title
	},
	data() {
		return {
			address: '',
			secret: '',
			pwd: '',
			show: false,
			type: 1 // 1 AICC 钱包， 2 瑞波链钱包
		};
	},
	created: function() {
		var _this = this;
		_this.address = _this.$route.query.address;
		_this.secret = _this.$route.query.secret;
		_this.type = _this.$route.query.type;
		_this.$nextTick(() => {
			_this.qrcode();
			_this.qrcode2();
		});
	},
	methods: {
		//动态生成二维码
		qrcode() {
			var _this = this;
			new QRCode('qrcode', {
				width: 120, // 设置宽度，单位像素
				height: 120, // 设置高度，单位像素
				text: _this.address // 设置二维码内容或跳转地址
			});
		},
		qrcode2() {
			var _this = this;
			new QRCode('qrcode2', {
				width: 120, // 设置宽度，单位像素
				height: 120, // 设置高度，单位像素
				text: _this.secret // 设置二维码内容或跳转地址
			});
		},
		onCopy: function() {
			this.$toast.success(this.$t('message.b44'));
		},
		sureCopy: function() {
			//要输入钱包交易密码 获取本地keystore 文件
			var _this = this;
			if (_this.type != 1) {
				_this.show = true;
			} else {
				var data2 = {
					address: _this.address,
					secret: _this.secret
				};
				_this.$router.push({
					path: '/Pwd',
					query: data2
				});
			}
		},
		sureCache: function() {
			var _this = this;
			if (_this.pwd == '') {
				_this.$toast.fail(_this.$t('message.c6'));
				return;
			}
			//匹配交易密码是有一致
			if (_this.getWallent().pwd != _this.pwd) {
				_this.$toast.fail(_this.$t('message.c7'));
				_this.pwd = '';
			} else {
				//存入钱包xinx
				var data = {
					address_ro: _this.address,
					secret_ro: _this.secret
				};
				_this.saveWallent(data);
				//创建钱包成功
				_this.$toast.success(_this.$t('message.c8'));
				this.$router.go(-2);
			}
		}
	}
};
</script>

<style>
.copyWrapC {
	padding: 0.625rem 1.25rem;
	background-color: #518cff;
	color: white;
	margin-top: 1.25rem;
	border-radius: 0.3125rem;
}
</style>
