<template>
	<!-- 添加钱包 -->
	<div class="divColumn">
		<Title :title="$t('message.b69')"></Title>
		<div class="conTop divColumn">
			<div style="text-align: left;margin-left: 0.625rem;margin-top: 1.25rem;margin-bottom: 1.25rem;" class="divColumnCenter">{{ $t('message.b70') }}：{{ walName }}</div>
			<div style="height: 2.625rem;margin: 0.625rem;" class="leftAndRight">
				<div :style="lbk" class="divCenter dlWrap" :class="{ dlWrapChoi: type == 1 }" @click="intoKey">{{ $t('message.b71') }}</div>
				<div :style="rbk" class="divCenter dlWrap" :class="{ dlWrapChoi: type == 2 }" @click="intoStore">{{ $t('message.b72') }}</div>
			</div>
			<!-- 秘钥导入 -->
			<div style="margin-left: 0.625rem;margin-right: 0.625rem;margin-top: 0.625rem;" class="divColumn" v-show="type == 1">
				<div class="leftAndRight itemW">
					<span style="width: 30%;">{{ $t('message.b73') }}：</span>
					<van-field style="background-color:#f6f8fe;" :placeholder="$t('message.b81')" v-model="secret"></van-field>
				</div>
				<div class="leftAndRight itemW">
					<span style="width: 30%;">{{ $t('message.b48') }}：</span>
					<van-field style="background-color:#f6f8fe;" :placeholder="$t('message.b30')" v-model="pwd" type="password"></van-field>
				</div>
				<div class="leftAndRight itemW" v-show="fromType == 3">
					<span style="width: 30%;">{{ $t('message.b50') }}：</span>
					<van-field style="background-color:#f6f8fe;" :placeholder="$t('message.b53')" v-model="pwdAg" type="password"></van-field>
				</div>
				<div
					@click="secretInto"
					class="itemW divCenter"
					style="background-color: #518CFF;border-radius: 0.3125rem;margin-top: 3.125rem;font-size: 1rem;font-weight: 600;color: white;"
				>
					<span>{{ $t('message.a79') }}</span>
				</div>
			</div>
			<!-- 通过文件导入 -->

			<div style="margin-left: 0.625rem;margin-right: 0.625rem;margin-top: 0.625rem;" class="divColumn" v-show="type == 2">
				<van-uploader capture="camera" :after-read="afterRead" class="divCenter itemW">
					<div style="width: 100%;">{{ $t('message.b74') }}</div>
				</van-uploader>
				<div class="leftAndRight itemW">
					<span style="width: 30%;">{{ $t('message.b75') }}：</span>
					<van-field style="background-color:#f6f8fe;" v-model="intoPwdKey" type="password" :placeholder="$t('message.b82')"></van-field>
				</div>
				<div class="leftAndRight itemW" style="display: none;">
					<span style="width: 30%;">{{ $t('message.b48') }}：</span>
					<van-field style="background-color:#f6f8fe;" :placeholder="$t('message.b38')" type="password" v-model="intoPwd"></van-field>
				</div>
				<div
					@click="sureCommit"
					class="itemW divCenter"
					style="background-color: #518CFF;border-radius: 0.3125rem;margin-top: 3.125rem;font-size: 1rem;font-weight: 600;color: white;"
				>
					<span>{{ $t('message.a79') }}</span>
				</div>
			</div>
			<!-- 温馨提示 -->
			<div class="divColumn" style="text-align: left;margin-left: 0.625rem;margin-right: 0.625rem;margin-top: 1.875rem;">
				<span style="color: #f37028;">{{ $t('message.a80') }}</span>
				<span v-show="fromType != 3" style="margin-top: 0.625rem;">{{ $t('message.b76') }}</span>
				<div v-show="fromType == 3" class="divColumn">
					<span>* {{ $t('message.b77') }}</span>
					<span>*{{ $t('message.b78') }}</span>
					<span>* {{ $t('message.b79') }}</span>
					<span>* {{ $t('message.b80') }}</span>
				</div>
			</div>
		</div>
		<div id="qrcode" ref="qrcode" style="display: none;"></div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
var ethWallet;
var rippleWallet;
import { jtWallet } from 'jcc_wallet';
import QrCodeDe from 'qrcode-decoder';
export default {
	components: {
		Title
	},
	data() {
		return {
			lbk: {
				backgroundImage: 'url(' + require('../../assets/img/dlc.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			rbk: {
				backgroundImage: 'url(' + require('../../assets/img/dr.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			type: 1,
			pwd: '',
			secret: '',
			walData: {},
			fromType: 1, // 1 创建eth地址钱包，2 创建瑞波链地址钱包
			walName: '',
			intoPwdKey: '', //keystore密码
			intoPwd: '',
			keyStoreData: {},
			pwdAg: ''
		};
	},
	created: function() {
		var _this = this;
		ethWallet = require('jcc_wallet').ethWallet;
		rippleWallet = require('jcc_wallet').rippleWallet;
		_this.walData = _this.getWallent();
		_this.fromType = _this.$route.query.type;
		if (_this.fromType == 1) {
			_this.walName = _this.$t('message.b83');
		} else if (_this.fromType == 2) {
			_this.walName = _this.$t('message.b84');
		} else if (_this.fromType == 3) {
			_this.walName = _this.$t('message.b85');
		}
	},
	methods: {
		intoKey: function() {
			var _this = this;
			_this.type = 1;
			_this.lbk.backgroundImage = 'url(' + require('../../assets/img/dlc.png') + ')';
			_this.rbk.backgroundImage = 'url(' + require('../../assets/img/dr.png') + ')';
		},
		intoStore: function() {
			var _this = this;
			_this.type = 2;
			_this.lbk.backgroundImage = 'url(' + require('../../assets/img/dl.png') + ')';
			_this.rbk.backgroundImage = 'url(' + require('../../assets/img/drc.png') + ')';
		},
		async secretInto() {
			//私钥导入
			var _this = this;
			if (_this.secret == '') {
				_this.$toast.fail(_this.$t('message.b86'));
				return;
			}
			if (_this.pwd == '') {
				_this.$toast.fail(_this.$t('message.b38'));
				return;
			}
			if (_this.fromType != 3 && _this.pwd != _this.walData.pwd) {
				_this.$toast.fail(_this.$t('message.b87'));
				return;
			}
			if (_this.fromType == 3) {
				if (_this.pwdAg == '') {
					_this.$toast.fail(_this.$t('message.b53'));
					return;
				}
				if (_this.pwdAg != _this.pwd) {
					_this.$toast.fail(_this.$t('message.b54'));
					_this.pwdAg = '';
					return;
				}
			}
			try {
				if (_this.fromType == 1) {
					var eth = await ethWallet.getAddress(_this.secret);
					if (eth == null) {
						_this.$toast.fail(_this.$t('message.b88'));
						return;
					}
					var data = {
						secret_eth: _this.secret,
						address_eth: eth
					};
					_this.saveWallent(data);
					_this.$toast.success(_this.$t('message.b89'));
					_this.$router.go(-1);
				} else if (_this.fromType == 2) {
					var xrp = await rippleWallet.getAddress(_this.secret);
					if (xrp == null) {
						_this.$toast.fail(_this.$t('message.b90'));
						return;
					}
					var dataR = {
						secret_ro: _this.secret,
						address_ro: xrp
					};
					_this.saveWallent(dataR);
					_this.$toast.success(_this.$t('message.b91'));
					_this.$router.go(-1);
				} else if (_this.fromType == 3) {
					var aicc = jtWallet.isValidSecret(_this.secret);
					if (!aicc) {
						_this.$toast.fail(_this.$t('message.b90'));
						_this.secret = '';
						return;
					}
					var aicc_addrss = await jtWallet.getAddress(_this.secret);
					var dataA = {
						secret: _this.secret,
						address: aicc_addrss,
						pwd: _this.pwd
					};
					_this.saveWallent(dataA);
					_this.$toast.success(_this.$t('message.b91'));
					_this.$router.go(-1);
				}
			} catch (e) {
				//TODO handle the exception
			}
		},
		afterRead(file) {
			this.getUrl(file.file);
		},
		sureCommit: function() {
			var _this = this;
			if (JSON.stringify(_this.keyStoreData) === '{}') {
				_this.$toast.fail(_this.$t('message.b92'));
				return;
			}
			if (_this.intoPwdKey == '') {
				_this.$toast.fail(_this.$t('message.b93'));
				return;
			}
			if (_this.intoPwdKey != _this.keyStoreData.pwd) {
				_this.$toast.fail(_this.$t('message.b94'));
				_this.intoPwdKey = '';
				return;
			}
			if (_this.fromType == 3) {
				var dataA = {
					secret: _this.keyStoreData.secret,
					address: _this.keyStoreData.address,
					pwd: _this.keyStoreData.pwd
				};
				_this.saveWallent(dataA);
			} else if (_this.fromType == 1) {
				var dataEth = {
					secret_eth: _this.keyStoreData.secret_eth,
					address_eth: _this.keyStoreData.address_eth
				};
				_this.saveWallent(dataEth);
			} else if (_this.fromType == 2) {
				var dataRb = {
					secret_ro: _this.keyStoreData.secret_ro,
					address_ro: _this.keyStoreData.address_ro
				};
				_this.saveWallent(dataRb);
			}

			_this.$toast.success(_this.$t('message.b91'));
			_this.$router.go(-1);
		},
		getUrl(file) {
			var _this = this;
			let qr = new QrCodeDe();
			let url = this.getObjectURL(file);
			qr.decodeFromImage(url).then(res => {
				//打印结果为 解析出来的 二维码地址
				console.log(res.data);
				_this.keyStoreData = JSON.parse(res.data);
			});
		},
		getObjectURL(file) {
			let url = null;

			if (window.createObjectURL !== undefined) {
				// basic

				url = window.createObjectURL(file);
			} else if (window.URL !== undefined) {
				// mozilla(firefox)

				url = window.URL.createObjectURL(file);
			} else if (window.webkitURL !== undefined) {
				// webkit or chrome

				url = window.webkitURL.createObjectURL(file);
			}

			return url;
		}
	}
};
</script>

<style>
.dlWrap {
	width: 50%;
	height: 100%;
	font-weight: 700;
	color: black;
}
.dlWrapChoi {
	color: white;
}
</style>
