<template>
	<!-- 开通竞价交易 -->
	<div :style="openBk" class="divColumn divColumnCenter" style="color: #FFFFFF;">
		<div style="width: 100%;text-align: left;"><img src="../../assets/img/back.png" style="margin-left: 1.25rem;margin-top: 1.25rem;width: 1.5rem;" @click="btnBack" /></div>
		<img src="../../assets/img/op1.png" style="width: 56%;margin-top: 12%;" />
		<h1 style="margin-bottom: 3.75rem;">{{ $t('message.e7') }}</h1>
		<div style="width: 100%;position: relative;">
			<div style="width: 60%;height: auto;position: absolute;right: 16%;" class="divColumn">
				<input :placeholder="$t('message.e48')" style="font-size: 0.75rem;text-align: center;background: none;" v-model="name" />
				<div style="width: 100%;height: 0.0625rem;background-color: white;margin-top: 0.625rem;"></div>
			</div>
			<div style="position: absolute;right: 78%;">{{ $t('message.e8') }}：</div>
		</div>
		<div style="width: 100%;position: relative;margin-top: 14%;">
			<div style="width: 60%;height: auto;position: absolute;right: 16%;" class="divColumn">
				<input :placeholder="$t('message.e50')" style="font-size: 0.75rem;text-align: center;background: none;" type="password" v-model="pwd" />
				<div style="width: 100%;height: 0.0625rem;background-color: white;margin-top: 0.625rem;"></div>
				<span style="color: white;font-size: 0.625rem;width: 100%;text-align: left;margin-top: 0.1875rem;">·{{ $t('message.e51') }}</span>
			</div>
			<div style="position: absolute;right: 78%;">{{ $t('message.b75') }}：</div>
		</div>
		<div :style="openBtnBk" style="width: 60%;height: 3.6rem;margin-top: 28%;color: #5D92FF;font-size: 1rem;font-weight: 600;" class="divCenter" @click="toLogin">
			{{ $t('message.e56') }}
		</div>
		<div class="divColumn divCenter" style="margin-top: 12%;font-size: 1rem;font-weight: 600;" @click="toOpen">
			<span>{{ $t('message.e57') }}</span>
			<div style="width: 100%;height: 0.125rem;background-color: white;"></div>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			openBk: {
				backgroundImage: 'url(' + require('../../assets/img/op_bk.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%',
				minHeight: '640px'
			},
			openBtnBk: {
				backgroundImage: 'url(' + require('../../assets/img/nf4.png') + ')',
				backgroundRepeat: 'no-repeat',
				backgroundSize: '100% 100%'
			},
			curHeight: 0,
			name: '',
			pwd: ''
		};
	},
	beforeMount: function() {
		var h = document.documentElement.clientHeight || document.body.clientHeight;
		this.curHeight = h;
		this.openBk.minHeight = h + 'px';
	},
	methods: {
		toLogin: function() {
			var _this = this;
			if (_this.name == '') {
				_this.$toast(_this.$t('message.e55'));
				return;
			}
			if (_this.pwd == '') {
				_this.$toast(_this.$t('message.e50'));
				return;
			}
			var data = {
				name: _this.name,
				password: _this.pwd,
				vcode: '123456',
				_session: ''
			};
			_this.postApi('api/Auth/login.html', data, function(res) {
				_this.setUser(res);
				_this.$router.push({
					path: '/JjExchange',
					query: {
						type: 2
					}
				});
			});
		},
		btnBack: function() {
			var type = this.$route.query.type;
			if (type == 1) {
				this.$router.go(-2);
			} else {
				this.$router.go(-3);
			}
		},
		toOpen: function() {
			this.$router.go(-1);
		}
	}
};
</script>

<style>
.openBtnWrap {
	width: 30%;
	margin-top: 1.875rem;
	width: 6.5rem;
	height: 3rem;
	color: #5d92ff;
	font-weight: 600;
}

.inOWrap {
	height: 1.875rem;
	width: 14rem;
	background-color: #ffffff;
	border-radius: 0.3125rem;
}
.lgItemW {
	width: 90%;
	height: 2.4rem;
	text-align: right;
	display: flex;
	margin-right: 20%;
	margin-top: 1.25rem;
}
.inpuWrap {
	width: 60%;
	height: 100%;
	background-color: white;
	border-radius: 0.3125rem;
}
.tipWrap {
	width: 60%;
	height: 100%;
}
.textWrap {
	width: 40%;
	height: auto;
	text-align: right;
}
</style>
