<template>
	<!-- 设置和修改交易密码 -->
	<div class="divColumn">
		<Title title="设置交易密码"></Title>
		<div class="conTop">
			<div style="margin: 0rem 0.625rem;margin-top: 0.875rem;" class="divColumn">
				<van-field v-model="pwd" placeholder="请输入密码" label="密 码:" label-align="left" label-width="80" class="filedWrap" type="password" />
				<van-field v-model="pwdagin" placeholder="请再次输入密码" label="确认密码:" label-align="left" label-width="84" class="filedWrap" type="password" />

				<div class="divCenter sureBtnWrap" @click="sure">确定</div>
				<div style="width:100%;margin-top: 3rem;text-align: left;font-size: 0.8125rem;" class="divColumn">
					<span style="color: red;">温馨提示</span>
					<p>*设置交易密码方便用户授权，非常重要！</p>
					<span>*平台不接触也不保存无法帮您找回密码，请务必牢记！</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Title from '@/components/Title.vue';
export default {
	data() {
		return {
			pwd: '',
			pwdagin: '',
			address: '',
			secret: ''
		};
	},
	components: {
		Title
	},
	created: function() {
		var _this = this;
		_this.address = _this.$route.query.address;
		_this.secret = _this.$route.query.secret;
	},
	methods: {
		sure: function() {
			var _this = this;
			if (_this.pwd == '') {
				_this.$toast('交易密码不能为空！');
				return;
			}
			if (_this.pwdagin == '') {
				_this.$toast('请确定密码！');
				return;
			}
			if (_this.pwd != _this.pwdagin) {
				_this.$toast('两次输入的密码不一致！');
				return;
			}
			_this.$toast.loading({
				message: '正在创建钱包',
				forbidClick: true,
				duration: 0
			});
			var data = {
				pwd: encodeURIComponent(_this.pwd),
				address: _this.address,
				secret: _this.secret
			};
			_this.saveWallent(data);
			_this.$toast.clear();
			_this.$router.go(-3);
		}
	}
};
</script>

<style>
.filedWrap {
	background-color: #f6f8fe;
	padding-left: 0rem;
	padding-right: 0rem;
	border-radius: 0.3125rem;
	margin-bottom: 1.5rem;
}
</style>
